import { Formik, FormikProps } from 'formik';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import { Button, Divider, Form, Grid, Header, Icon, Label, List, Segment } from 'semantic-ui-react';
import AdminContainer from '../../../containers/base/AdminContainer';
import { IAdminContainerProps } from '../../../containers/base/containerInterfaces';
import api from '../../../utils/api';
import { formNameMasterData } from '../../../utils/data/ebps-setup/tableSetup';
import { getOptionText } from '../../../utils/dataUtils';
import { showErrorToast, showToast } from '../../../utils/functionUtils';
import { isStringEmpty } from '../../../utils/stringUtils';
import { ModalComponent } from '../../ebps-setup/shared/ModalComponent';
import { FlexSingleRight, FlexSpaceBetween } from '../../uiComponents/FlexDivs';
import { useTableFormField } from '../hooks/useTableFormField';
import { IFormFieldList, IFormNameMaster, IFormPermissions, IUserTypeMaster, TOption, IEBPSTableColumns, IVocabulary } from '../interfaces';
import { BackButton } from '../shared/BackButton';
import { SaveChangesButton } from '../shared/SaveChangesButton';
import { FormFieldListTable } from './FormFieldListTable';
import { FormFieldPicker } from './FormFieldPicker';
import { FormPermissionsTable } from './FormPermissionsTable';
import { PermissionPicker } from './PermissionPicker';

const tableHeadings = formNameMasterData.table.tableHeadings;
const modalData = formNameMasterData.columnModal;

export const EditFormNameDetailsComponent = ({
	match,
	adminData,
	postAdminDataByUrl,
	deleteAdminDataByUrl,
	success,
	errors,
	loading,
}: IAdminContainerProps<{ tables: []; formNameMaster: []; userTypeMaster: []; vocabulary: [] }>) => {
	const history = useHistory();
	const {
		formFieldDispatch,
		formFieldModal,
		formFieldDeleteModal,
		toggleFormFieldModal,
		formFieldState,
		startEdit,
		startDelete,
		closeFormFieldModal,
		isFormFieldEdit,
	} = useTableFormField();

	const [data, setData] = useState<IFormNameMaster>();

	const [approvalUserOptions, setApprovalUserOptions] = useState<TOption[]>([]);
	const [tableOptions, setTableOptions] = useState<TOption[]>([]);
	const [columnOptions, setColumnOptions] = useState<TOption[]>([]);
	const [vocabularyOptions, setVocabularyOptions] = useState<TOption[]>([]);

	const [tables, setTables] = useState<IEBPSTableColumns[]>([]);
	const [permissionModalOpen, setPermissionModalOpen] = useState(false);
	const [pkField, setPkField] = useState<number | null>(null);
	const [cUserType, setCUserType] = useState<string>('D');
	const [cStatus, setCStatus] = useState<'Y' | 'N'>('Y');

	useEffect(() => {
		if (adminData.userTypeMaster && Array.isArray(adminData.userTypeMaster)) {
			const approvalUsers = adminData.userTypeMaster
				.filter((user: IUserTypeMaster) => !isStringEmpty(user.approveColumn))
				.map((user: IUserTypeMaster, idx: number) => ({ text: user.designationNepali || user.designation, value: user.id, key: idx }));
			setApprovalUserOptions(approvalUsers);
		}
	}, [adminData.userTypeMaster]);

	useEffect(() => {
		setData(adminData.formNameMaster.find((row: IFormNameMaster) => String(row.id) === String(match.params.id)));
	}, [adminData.formNameMaster, match.params.id]);

	useEffect(() => {
		if (adminData.tables && Array.isArray(adminData.tables)) {
			const tableOptions: TOption[] = adminData.tables.map((row: IEBPSTableColumns, idx: number) => ({
				key: idx,
				value: row.id,
				text: row.name,
			}));

			setTables(adminData.tables);
			setTableOptions(tableOptions);
		}
	}, [adminData.tables]);

	useEffect(() => {
		if (adminData.vocabulary) {
			const options = adminData.vocabulary.map((row: IVocabulary) => ({ key: row.id, value: row.id, text: row.name }));
			setVocabularyOptions(options);
		}
	}, [adminData.vocabulary]);

	useEffect(() => {
		if (tables.length > 0 && data && data.formFieldsList.length > 0) {
			const ebpsColumns = tables.map((table) => table.ebpsColumns).flat();
			setColumnOptions(ebpsColumns.map((row, index) => ({ key: index, value: row.id, text: row.name })));
			const pkField = data.formFieldsList.find((field) => {
				const reqCol = ebpsColumns.find((column) => column.id === field.ebpsColumnId);
				if (reqCol) return reqCol.isPk === 'Y';
				else return false;
			});
			if (pkField) {
				setPkField(pkField.id || null);
			}
		}
	}, [data, tables]);

	useEffect(() => {
		if (success && success.success) {
			// if (isDeleting) {
			// 	showToast('Data deleted successfully');
			// 	setOpenDeleteModal(false);
			// 	setIsDeleting(false);
			// } else {
			showToast('Data saved successfully');
			// }
		}
	}, [success]);

	const handleFormFieldEdit = (id: number, row: IFormFieldList) => {
		if (id && row) {
			startEdit(row);
		}
	};

	const handleFormFieldDelete = () => {
		deleteAdminDataByUrl(api.d_formNameMasterFields, formFieldState.id);
		closeFormFieldModal();
	};

	const handleApprovalPermissionsDelete = (id: number) => {
		deleteAdminDataByUrl(api.d_formNameMasterFormPermissions, id);
		closeFormFieldModal();
	};

	return (
		<div className="english-div">
			{data ? (
				<div>
					<Segment>
						<FlexSpaceBetween>
							<Header>Form Information</Header>
							<div>
								<BackButton url="/admin/form-name" compact />
								<Button
									onClick={() => history.push(`/admin/edit-table-columns/${String(data.tableId)}`)}
									size="tiny"
									compact
									content="Edit Table Columns"
									icon="edit"
									className="primary-btn"
								/>
							</div>
						</FlexSpaceBetween>
						<List className="app-info">
							<Grid>
								<Grid.Row>
									<Grid.Column width="5">
										<List.Item>
											<List.Description>{data.name}</List.Description>
											<List.Header>{tableHeadings.name}</List.Header>
										</List.Item>
									</Grid.Column>
									<Grid.Column width="5">
										<List.Item>
											<List.Description>{data.viewUrl}</List.Description>
											<List.Header>{tableHeadings.viewUrl}</List.Header>
										</List.Item>
									</Grid.Column>
									<Grid.Column width="3">
										<List.Item>
											<List.Description>{data.pageCode}</List.Description>
											<List.Header>{tableHeadings.pageCode}</List.Header>
										</List.Item>
									</Grid.Column>
									<Grid.Column width="3">
										<List.Item>
											<List.Description>{data.enterBy}</List.Description>
											<List.Header>{tableHeadings.enterBy}</List.Header>
										</List.Item>
									</Grid.Column>
								</Grid.Row>
								<Grid.Row>
									<Grid.Column>
										<List.Item>
											<List.Description>{getOptionText(data.tableId, tableOptions)}</List.Description>
											<List.Header>{tableHeadings.tableName}</List.Header>
										</List.Item>
									</Grid.Column>
								</Grid.Row>
							</Grid>
						</List>
					</Segment>
					<Formik
						enableReinitialize
						initialValues={{ ...data }}
						// validationSchema={!isDeleting ? null : null}
						onSubmit={(values, actions) => {
							actions.setSubmitting(true);
							const formatPermissions = () => {
								if (permissionModalOpen) {
									const existingData = values.formPermissions || [];
									const existingRow = existingData.find((row: IFormPermissions) => row.userType === cUserType);
									const requiredValue =
										existingData.length > 0
											? existingRow
												? existingData.map((row: IFormPermissions) => {
														if (row.userType === cUserType) {
															return { ...row, userType: cUserType, approvalStatus: cStatus };
														} else {
															return row;
														}
												  })
												: existingData.concat({ formId: data.id, userType: cUserType, approvalStatus: cStatus })
											: [{ formId: data.id, userType: cUserType, approvalStatus: cStatus }];
									return requiredValue;
								} else {
									return values.formPermissions;
								}
							};

							const formatFormFields = () => {
								if (formFieldModal) {
									const existingData = values.formFieldsList || [];
									const existingRow = existingData.find((row: IFormFieldList) => row.id && row.id === formFieldState.id);
									const requiredValue =
										existingData.length > 0
											? existingRow
												? existingData.map((row: IFormFieldList) => {
														if (row.id && row.id === formFieldState.id) {
															return { ...row, ...formFieldState, formId: data.id };
														} else {
															return row;
														}
												  })
												: existingData.concat({ ...formFieldState, formId: data.id })
											: [{ ...formFieldState, formId: data.id }];
									return requiredValue;
								} else {
									return values.formFieldsList;
								}
							};

							try {
								const dataToSend = { ...values, formFieldsList: formatFormFields(), formPermissions: formatPermissions() };
								// if (isEditing) {
								// 	postAdminDataByUrl(api.d_formNameMaster, values);
								// } else if (isDeleting) {
								// 	deleteAdminDataByUrl(api.d_formNameMaster, formState.id);
								// } else {
								postAdminDataByUrl(api.d_formNameMaster, dataToSend);
								// }
								actions.setSubmitting(false);
								closeFormFieldModal();
								setPermissionModalOpen(false);
							} catch (error) {
								console.log('Error in menu master post', error);
								showErrorToast('Something went wrong.');
								actions.setSubmitting(false);
							}
						}}
					>
						{({ setFieldValue, values, isSubmitting, handleSubmit }: FormikProps<IFormNameMaster>) => {
							const layoutEdit = values.formLayout ? true : false;
							return (
								<Form loading={isSubmitting || loading}>
									<FlexSingleRight>
										<div>
											{!pkField && (
												<Label className="warning-label-light">
													<Icon name="warning sign" />
													Please add a primary key{' '}
												</Label>
											)}
											<SaveChangesButton onClick={() => handleSubmit()} />
										</div>
									</FlexSingleRight>
									<Divider horizontal>
										Form Fields{' '}
										<Button
											icon="add"
											compact
											size="tiny"
											className="primary-btn"
											content="Add"
											onClick={() => toggleFormFieldModal(true)}
										/>
									</Divider>
									<FormFieldListTable
										handleDelete={(id) => {
											const existingData = values.formFieldsList || [];
											const existingRow = existingData.length > 0 && existingData.find((row: IFormFieldList) => row.id === id);

											if (existingRow) {
												startDelete(existingRow);
												// setFieldValue('formFieldsList', requiredValue);
												// setPermissionModalOpen(false);
											}
										}}
										handleEdit={handleFormFieldEdit}
										formFieldList={values.formFieldsList}
										tableOptions={tableOptions}
										columnOptions={columnOptions}
										pkField={pkField}
									/>
									<Grid>
										<Grid.Row columns="2">
											<Grid.Column>
												<Divider horizontal>
													Approval Permissions{' '}
													<Button
														icon="add"
														compact
														size="tiny"
														className="primary-btn"
														content="Add"
														onClick={() => setPermissionModalOpen(true)}
													/>
												</Divider>
											</Grid.Column>
											<Grid.Column>
												<Divider horizontal>
													Form Layout{' '}
													<Button
														size="tiny"
														compact
														className="primary-btn"
														icon={layoutEdit ? 'edit' : 'add'}
														onClick={() => history.push(`/admin/layout-setup/${data.id}`)}
														content={layoutEdit ? 'Edit Layout' : 'Add Layout'}
													/>
												</Divider>
											</Grid.Column>
										</Grid.Row>
										<Grid.Row columns="2">
											<Grid.Column>
												<FormPermissionsTable
													handleDeletePermission={(userType, id) => {
														const existingData = values.formPermissions || [];
														const requiredValue =
															existingData.length > 0
																? existingData.filter((row: IFormPermissions) => row.userType !== userType)
																: [];
														setFieldValue('formPermissions', requiredValue);
														if (id) {
															handleApprovalPermissionsDelete(id);
														}
														// setPermissionModalOpen(false);
													}}
													formPermissions={values.formPermissions}
													options={approvalUserOptions}
												/>
											</Grid.Column>
											<Grid.Column></Grid.Column>
										</Grid.Row>
									</Grid>
									<ModalComponent errors={errors} loading={isSubmitting || loading} handleSubmit={handleSubmit}>
										<ModalComponent.AdminModal
											modalId="permission-form"
											open={permissionModalOpen}
											handleClose={() => setPermissionModalOpen(false)}
											title={modalData.addPermission}
											saveText={modalData.addPermission}
										>
											<PermissionPicker
												approvalUserOptions={approvalUserOptions}
												//@ts-ignore
												setStatus={setCStatus}
												setUserType={setCUserType}
											/>
										</ModalComponent.AdminModal>{' '}
										<ModalComponent.AdminModal
											modalId="field-list-form"
											open={formFieldModal}
											handleClose={closeFormFieldModal}
											title={`${isFormFieldEdit ? 'Edit' : 'Add'} Form Field`}
											saveText={`${isFormFieldEdit ? 'Edit' : 'Add'} Form Field`}
										>
											<FormFieldPicker
												dispatch={formFieldDispatch}
												state={formFieldState}
												tableOptions={tableOptions}
												tables={tables}
												tableId={(data && parseInt(String(data.tableId))) || 0}
												vocabularyOptions={vocabularyOptions}
											/>
										</ModalComponent.AdminModal>
										<ModalComponent.AdminModal
											modalId="form-field-delete"
											size="tiny"
											handleSubmit={handleFormFieldDelete}
											open={formFieldDeleteModal}
											handleClose={closeFormFieldModal}
											title={'Delete Form Field'}
											saveText={'Delete Form Field'}
										>
											Are you sure you want to delete this form field?
											{/* {formFieldState.name} */}
										</ModalComponent.AdminModal>
									</ModalComponent>
								</Form>
							);
						}}
					</Formik>
				</div>
			) : null}
		</div>
	);
};

export const EditFormNameDetails = (parentProps: any) => (
	//@ts-ignore
	<AdminContainer
		//@ts-ignore
		api={[
			{ api: api.d_ebpsTables, objName: 'tables' },
			{ api: api.d_formNameMaster, objName: 'formNameMaster' },
			{ api: api.d_vocabulary, objName: 'vocabulary' },
			{ api: api.userType, objName: 'userTypeMaster' },
		]}
		//@ts-ignore
		updateApi={[{ api: api.formNameMaster, objName: 'formNameMaster' }]}
		//@ts-ignore
		render={(props: any) => <EditFormNameDetailsComponent {...props} parentProps={parentProps} />}
	/>
);
