import { useEffect, useState } from "react";
import { IPermitData } from "../../../interfaces/ApiInterfaces";
import { getCurrentDate, getCurrentTime } from "../../../utils/dateUtils";
import { isEmpty } from "../../../utils/functionUtils";
import { EInputTypes } from "../layoutSetupComponents/utils/enums/inputEnums";
import { TInputTypeObject } from "../layoutSetupComponents/utils/types/setupTypes";

export const useDyamicFormInitializer = (inputTypes: TInputTypeObject[], prevData: any, permitData: IPermitData | any) => {

    const [appIdIndex, setAppIdIndex] = useState(0);
    const [complexDataIndices, setComplexDataIndices] = useState<number[] | null>(null);
    const [initialValues, setInitialValues] = useState<any>({})

    useEffect(() => {
        const inputType = inputTypes.find((row) => row.columnName === 'application_no');

        const complexTypes = inputTypes.filter((row) => row.type === EInputTypes.FLOOR_TABLE || row.type === EInputTypes.SURROUNDING_TABLE);

        setComplexDataIndices(complexTypes ? complexTypes.map(row => row.id) : null);

        let initVal: any = {}
        if (complexTypes) {
            for (const complexType of complexTypes) {
                if (complexType.type === EInputTypes.FLOOR_TABLE) {
                    initVal[complexType.id] = permitData && permitData.floor
                } else if (complexType.type === EInputTypes.SURROUNDING_TABLE) {
                    initVal[complexType.id] = permitData && permitData.surrounding
                }
            }
        }

        for (const inputT of inputTypes) {
            if (inputT.type === EInputTypes.NEPALI_DATE) {
                initVal[inputT.id] = getCurrentDate(true)
            } else if (inputT.type === EInputTypes.NORMAL_NEPALI_DATE) {
                initVal[inputT.id] = getCurrentDate()
            } else if (inputT.type === EInputTypes.TIME_INPUT) {
                initVal[inputT.id] = getCurrentTime();
            }
        }

        initVal = { ...initVal, ...prevData }
        if (initVal && !isEmpty(initVal)) setInitialValues(initVal)

        if (inputType) {
            setAppIdIndex(inputType.id);
        }
    }, [inputTypes]);

    // useEffect(() => {
    //     let initVal: any = {}
    //     if (isEmpty(prevData) && complexDataIndices) {
    //         for (const key of complexDataIndices) {
    //             initVal[key] = permitData.floor
    //         }
    //     } else { initVal = prevData }
    //     if (initVal && !isEmpty(initVal)) setInitialValues(initVal)
    // }, [complexDataIndices])

    function getDataToSend(values: any, applicationNo: string) {
        let dataToSend = { ...values };
        if (appIdIndex) {
            dataToSend = { ...values, [appIdIndex]: applicationNo };
        }
        if (complexDataIndices) {
            for (const index of complexDataIndices) {
                dataToSend = { ...dataToSend, [index]: JSON.stringify(dataToSend[index]) };
            }
        }
        return dataToSend;
    }

    return { complexDataIndices, initialValues, getDataToSend }
}