import React from 'react';
import { Form, Label } from 'semantic-ui-react';
import { useTranslatorIm } from '../../../hooks/useTranslator';
import { FlexSpaceBetween } from '../../uiComponents/FlexDivs';

type TProps = {
	label?: string;
	error?: string;
	setFieldValue: (field: string, value: any) => void;
	onChange: (e: React.ChangeEvent<any>) => void;
	name: string;
	value: string;
	placeholder?: string;
	cols?: number;
	rows?: number;
	pointing?: boolean | 'left' | 'above' | 'below' | 'right' | undefined;
	isNepali: boolean;
	toggler: any;
};

export const MultiLangTextArea = React.memo(
	({ label, error, setFieldValue, onChange, name, value, placeholder, cols, rows, pointing, isNepali = true, toggler }: TProps) => {
		const [className, valueState, handleChange] = useTranslatorIm(value, setFieldValue, error, name);

		return (
			<Form.Group inline>
				<Form.Field error={!!error}>
					{label && (
						<FlexSpaceBetween>
							<b>{label}</b>
							{toggler}
						</FlexSpaceBetween>
					)}
					{isNepali ? (
						<textarea
							placeholder={placeholder}
							cols={cols ? cols : 150}
							rows={rows ? rows : 20}
							name={name}
							onChange={handleChange}
							className={`bpf-inputType text-area ${className}`}
							value={valueState}
						/>
					) : (
						<textarea
							placeholder={placeholder}
							cols={cols ? cols : 150}
							rows={rows ? rows : 20}
							name={name}
							onChange={onChange}
							className={`bpf-inputType text-area ${className}`}
							value={value}
						/>
					)}
					{error && (
						<Label pointing={pointing ? pointing : 'left'} prompt size="large">
							{error}
						</Label>
					)}
				</Form.Field>
			</Form.Group>
		);
	}
);
