import { getIn } from 'formik';
import React from 'react';
import { Form } from 'semantic-ui-react';
import { SelectInputV2 } from '../../shared/fields/SelectInputV2';
import { TOption } from '../interfaces';
import { EFloorValues } from './utils/enums/inputEnums';

export const floorValueOptions = [
	{ key: 1, value: EFloorValues.TOTAL_AREA, text: 'Total Area' },
	{ key: 0, value: EFloorValues.TOTAL_HEIGHT, text: 'Total Height' },
	{ key: 2, value: EFloorValues.NO_OF_FLOORS, text: 'Floor Count' },
	{ key: 3, value: EFloorValues.NO_OF_FLOORS_ENGLISH, text: 'English Floor Count' },
	{ key: 4, value: EFloorValues.NO_OF_FLOORS_NAME, text: 'Floor Count Name' },
	{ key: 5, value: EFloorValues.NO_OF_FLOORS_NAME_ENGLISH, text: 'English Floor Count Name' },
	{ key: 6, value: EFloorValues.FLOOR_NAME, text: 'Floor Name' },
	{ key: 7, value: EFloorValues.FLOOR_NAME_ENGLISH, text: 'English Floor Name' },
	{ key: 8, value: EFloorValues.TOP_FLOOR_LENGTH, text: 'Top Floor Length' },
	{ key: 9, value: EFloorValues.TOP_FLOOR_WIDTH, text: 'Top Floor Width' },
	{ key: 10, value: EFloorValues.TOP_FLOOR_HEIGHT, text: 'Top Floor Height' },
	{ key: 11, value: EFloorValues.TOP_FLOOR_AREA, text: 'Top Floor Area' },
	{ key: 12, value: EFloorValues.GROUND_FLOOR_LENGTH, text: 'Ground Floor Length' },
	{ key: 13, value: EFloorValues.GROUND_FLOOR_WIDTH, text: 'Ground Floor Width' },
	{ key: 14, value: EFloorValues.GROUND_FLOOR_HEIGHT, text: 'Groudn Floor Height' },
	{ key: 15, value: EFloorValues.GROUND_FLOOR_AREA, text: 'Groudn Floor Area' },
];

const samples = {
	[EFloorValues.NO_OF_FLOORS]: ['से.मी. / वेसमेन्ट', '१', '२', '३'],
	[EFloorValues.NO_OF_FLOORS_ENGLISH]: ['Underground', '1', '2', '3'],
	[EFloorValues.NO_OF_FLOORS_NAME]: ['से.मी. / वेसमेन्ट', 'जमिन/भुई तला', '१', '२'],
	[EFloorValues.NO_OF_FLOORS_NAME_ENGLISH]: ['Underground', 'Ground Floor', '1', '2'],
	[EFloorValues.FLOOR_NAME]: ['भुमिगत तला', 'भुई तला', 'पहिलो तला', 'दोस्रो तला'],
	[EFloorValues.FLOOR_NAME_ENGLISH]: ['Underground Floor', 'Ground Floor', 'First Floor', 'Second Floor'],
};
export const FloorValuesInputType = ({ values, vocabulary }: { values: any; vocabulary: TOption[] }) => {
	return (
		<Form.Group>
			<SelectInputV2 name={'meta.identifier'} label="Select Floor Value" options={floorValueOptions} />
			{
				//@ts-ignore
				samples[getIn(values, `meta.identifier`)] && (
					<Form.Field>
						<pre>
							Sample:{' '}
							{JSON.stringify(
								//@ts-ignore
								samples[getIn(values, 'meta.identifier')],
								null,
								2
							)}
						</pre>
					</Form.Field>
				)
			}
		</Form.Group>
	);
};
