import { TEMPLATE_DELIMETER } from "./constants";

export const inputFormatter = (template: string) => {
    const [type, value, label]: string[] = template
        .replace(/\[\[/g, "")
        .replace(/\]\]/g, "")
        .split(TEMPLATE_DELIMETER);
    return [type, value, label]
}

export const templateFormatter = (template: string) => {
    const [type, value]: string[] = template
        .split(TEMPLATE_DELIMETER);
    return [type, value]
}
