import React, { useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Button, Label, Modal, ModalContent, ModalHeader, Table } from 'semantic-ui-react';
import { SectionHeader } from '../../uiComponents/Headers';
import { IEBPSColumns, IFormFieldList, TOption } from '../interfaces';
import { AddInput } from './AddInput';
import { TEMPLATE_DELIMETER } from './utils/constants';
import { TInputTypeObject } from './utils/types/setupTypes';

// const inputTypes = {
//   DASHED_INPUT: {
//     label: "Dashed Input",
//     getFormat: (name: string) => `[[input:${name}]]`,
//   },
//   DASHED_DATE: {
//     label: "Dashed Date",
//   },
// };
type TProps = {
	inputTypes: TInputTypeObject[];
	inputFields: (IFormFieldList & { details: IEBPSColumns })[];
	setInputTypes: React.Dispatch<React.SetStateAction<TInputTypeObject[]>>;
	vocabulary: TOption[];
};

const getFormat = (name: string, type: string, label?: string) =>
	`[[${type}${TEMPLATE_DELIMETER}${name}${label ? `${TEMPLATE_DELIMETER}${label}` : ''}]]`;

export const InputTypes = ({ inputTypes, setInputTypes, inputFields, vocabulary = [] }: TProps) => {
	const [isAddModalOpen, setIsAddModalOpen] = useState(false);
	const [editData, setEditData] = useState<TInputTypeObject | null>(null);

	const handleEdit = (id: number) => {
		setEditData(() => inputTypes.find((obj) => obj.id === id) || null);
		openModal();
	};

	const openModal = () => {
		setIsAddModalOpen(true);
	};
	const closeModal = () => {
		setEditData(null);
		setIsAddModalOpen(false);
	};
	const handleInputDelete = (name: string) => {
		setInputTypes((prevTypes) => prevTypes.filter((obj) => obj.name !== name));
	};

	return (
		<div>
			<SectionHeader>
				<h4>
					Available Input Types
					{/* <Button icon="add" onClick={openModal} size="tiny" content="Add" /> */}
				</h4>
			</SectionHeader>
			<div style={{ maxHeight: '300px', overflow: 'scroll' }}>
				<Table striped compact="very" size="small" className="compact-table">
					<Table.Header>
						<Table.Row>
							<Table.HeaderCell width="3">Copy</Table.HeaderCell>
							<Table.HeaderCell width="4">Field Name</Table.HeaderCell>
							<Table.HeaderCell width="2">Type</Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						{inputTypes.length < 1 ? (
							<Table.Row>
								<Table.Cell colSpan="3" textAlign="center">
									Add Inputs
								</Table.Cell>
							</Table.Row>
						) : (
							inputTypes.map((data) => (
								<Table.Row key={data.name}>
									<Table.Cell>
										<CopyToClipboard text={getFormat(data.columnName, data.type, data.label)}>
											<Button icon="copy" title="Copy" primary size="tiny" compact />
										</CopyToClipboard>
										<Button
											icon="edit"
											title="Edit"
											className="primary-btn"
											size="tiny"
											compact
											onClick={handleEdit.bind(null, data && data.id ? data.id : 0)}
										/>
										{/* <Button
											icon="delete"
											title="Delete"
											color="red"
											size="tiny"
											onClick={handleInputDelete.bind(null, data.name)}
										/> */}
									</Table.Cell>
									<Table.Cell>
										{data.name}{' '}
										{data.meta && data.meta.validation && data.meta.validation.required ? (
											<Label className={'success-label'} size="mini">
												Required
											</Label>
										) : null}
									</Table.Cell>
									<Table.Cell>{data.type}</Table.Cell>
								</Table.Row>
							))
						)}
					</Table.Body>
				</Table>
			</div>
			<Modal open={isAddModalOpen} onClose={closeModal} closeIcon>
				<ModalHeader>{!!editData ? 'Edit Input' : 'Add Input'}</ModalHeader>
				<ModalContent scrolling>
					<AddInput setInputTypes={setInputTypes} onClose={closeModal} isEditing={!!editData} editData={editData} vocabulary={vocabulary} />
				</ModalContent>
			</Modal>
		</div>
	);
};
