import React from 'react';

export const FormValuesDisplay = ({ values }: { values: any }) => {
	return (
		<div
			className="english-div"
			style={{
				textAlign: 'left',
				// overflowY: "scroll",
				height: '100%',
				overflowX: 'scroll',
			}}
		>
			<h4>Form Values</h4>
			<pre
				style={{
					whiteSpace: 'pre-wrap',
				}}
			>
				{JSON.stringify(values, null, 2)}
			</pre>
		</div>
	);
};
