import React from 'react';
import { Label } from 'semantic-ui-react';

type TProps = {
	status: 'Y' | 'N';
	mode?: 'active-inactive' | 'yes-no';
};

export const StatusLabel = ({ status, mode = 'active-inactive' }: TProps) => {
	const label = mode === 'yes-no' ? (status === 'Y' ? 'Yes' : 'No') : status === 'Y' ? 'Active' : 'Inactive';
	return <Label className={status === 'Y' ? 'success-label' : 'danger-label'}>{label}</Label>;
};
