import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router';
import { putApprovalByUrl } from '../../../store/actions/formActions';
import { selectCurrentIds } from '../../../store/selectors/currentFormSelectors';
import { TReduxState, TDynamicSuccess, EApproveRejectModule } from '../../../store/storeStateTypes';
import api from '../../../utils/api';
import { TApprovalAction } from '../../../utils/axiosTypes';
import { getBackUrl } from '../../../utils/config';
import { handleDynamicSuccess } from '../../../utils/dataUtils';
import { TApproveRejectModalState } from '../hooks/useApproveRejectModal';
import { DynamicApprovalModal } from './DynamicApprovalModal';
import { DynamicRejectionModal } from './DynamicRejectionModal';

type TProps = {
	formUrl: string;
	closeModals: () => void;
	putApprovalByUrl: TApprovalAction;
	applicationId: number | string;
	formId: number | string;
	success: TDynamicSuccess;
} & TApproveRejectModalState;

export function DynamicApprovalGroupComponent({
	formUrl,
	approvalModalOpen,
	approvalModalWithFileOpen,
	rejectionModalOpen,
	closeModals,
	success,
	formId,
	applicationId,
}: TProps) {
	const history = useHistory();
	const [url] = useState(() => {
		return `${getBackUrl()}${api.d_general}${applicationId}?formId=${formId}`;
	});

	useEffect(() => {
		if (success && success.success) {
			closeModals();
			if (success.module === EApproveRejectModule.REJECT) {
				handleDynamicSuccess(false, formId, applicationId, history, success, '/', 'Application Rejection Sent Successfully!!');
			} else {
				handleDynamicSuccess(false, formId, applicationId, history, success, null, 'Application Approved Successfully');
			}
		}
	}, [success]);

	return (
		<div>
			<DynamicApprovalModal open={approvalModalOpen} onClose={closeModals} url={url} history={history} />
			<DynamicRejectionModal open={rejectionModalOpen} onClose={closeModals} history={history} url={url} />
		</div>
	);
}

const mapDispatchToProps = { putApprovalByUrl };
const mapStateToProps = (state: TReduxState) => {
	const { applicationId, formId } = selectCurrentIds(state);
	return {
		success: state.root.dynamic.success,
		applicationId,
		formId,
	};
};

export const DynamicApprovalGroup = connect(mapStateToProps, mapDispatchToProps)(DynamicApprovalGroupComponent);
