import { isEmpty } from "../../utils/functionUtils"
import { getLocalStorage } from "../../utils/secureLS"

export const localStorageSelector = (localStorageKey: string, existingData: any): any => {
    
    if (!isEmpty(existingData))
        return existingData;
    else {
        const localStorageData = getLocalStorage(localStorageKey)

        if (localStorageData) {
            try {

                const data = JSON.parse(localStorageData)
                return data
            } catch (error) {
                return {}
            }
        }
    }
}