const getVal = (statusCode) => {
	if (statusCode === 'A') {
		return { label: 'Approved', color: 'teal' };
	} else if (statusCode === 'R') {
		return { label: 'Rejected', color: 'red' };
	} else {
		return { label: 'Pending', color: 'orange' };
	}
};

