import React, { useEffect, useState } from 'react';
import { IUserInfo } from '../../../../interfaces/ApiInterfaces';
import { TOutputFormat } from '../utils/types/setupTypes';

type TProps = {
	userData?: IUserInfo;
	row: TOutputFormat;
};

export const RenderUserInfo = ({ userData, row }: TProps) => {
	const [data, setData] = useState('');
	useEffect(() => {
		if (userData && userData.info && row.content) {
			if (row.content.startsWith('org') && userData.organization) {
				const [, key] = row.content.split('-');
				//@ts-ignore
				if (userData.organization[key]) setData(userData.organization[key]);
			}
			//@ts-ignore
			else if (userData[row.content]) {
				//@ts-ignore
				setData(userData[row.content]);
			}
		}
	}, [userData, row.content]);

	return <div style={{ display: 'inline-block' }}>{data}</div>;
};
