export const FingerPrintBirtamod = ({ height = 150, vertical = false }) => (
	<div className={`${vertical ? 'finger-print-table-vertical' : 'finger-print-table'}`}>
		<Table celled compact>
			<Table.Header>
				<Table.Row textAlign="center">
					<Table.HeaderCell>{getMessage(`${messageId}.right`, fingerPrintData.right)}</Table.HeaderCell>
				</Table.Row>
				<Table.Row textAlign="center">
					<Table.HeaderCell>{getMessage(`${messageId}.lyapche`, fingerPrintData.lyapche)}</Table.HeaderCell>
				</Table.Row>
			</Table.Header>
			<Table.Body>
				<Table.Row height={height}>
					<Table.Cell></Table.Cell>
				</Table.Row>
			</Table.Body>
		</Table>
		<Table celled compact className="right-table">
			<Table.Header>
				<Table.Row textAlign="center">
					<Table.HeaderCell>{getMessage(`${messageId}.left`, fingerPrintData.left)}</Table.HeaderCell>
				</Table.Row>
				<Table.Row textAlign="center">
					<Table.HeaderCell>{getMessage(`${messageId}.lyapche`, fingerPrintData.lyapche)}</Table.HeaderCell>
				</Table.Row>
			</Table.Header>
			<Table.Body>
				<Table.Row height={height}>
					<Table.Cell></Table.Cell>
				</Table.Row>
			</Table.Body>
		</Table>
	</div>
);

export const ThumbSignature = ( {alignment = 'horizontal', height = 150 }) => {
	if(alignment === 'vertical-left'){
		return (
			<div className="thumb-signature-table-vertical" style={{ display: 'inline-block' }}>
				<Table celled compact style={{ transform: 'rotate(90deg)', width: '130px' }}>
					<Table.Header>
						<Table.Row textAlign="center">
							<Table.HeaderCell>{getMessage(`${messageId}.right`, fingerPrintData.right)}</Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						<Table.Row height={height}>
							<Table.Cell></Table.Cell>
						</Table.Row>
					</Table.Body>
				</Table>
				<Table celled compact style={{ transform: 'rotate(90deg)' }}>
					<Table.Header>
						<Table.Row textAlign="center">
							<Table.HeaderCell>{getMessage(`${messageId}.left`, fingerPrintData.left)}</Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						<Table.Row height={height}>
							<Table.Cell></Table.Cell>
						</Table.Row>
					</Table.Body>
				</Table>

				<div style={{ transform: 'rotate(90deg)', marginTop: '95px', display: 'flex' }}>
					{getMessage(`${messageId}.signature`, fingerPrintData.signature)}
					<span className="ui input dashedForm-control" />
					<br />
				</div>
			</div>
		);
	}
	else{
		return (
			<div style={{ display: 'inline-block' }}>
				<div className="finger-print-div">
					<div>
						{getMessage(`${messageId}.signature`, fingerPrintData.signature)}
						<span className="ui input dashedForm-control" />
					</div>
					<br />
					<FingerPrint />
				</div>
			</div>
		);
	}
};
