import { Field, FieldProps, getIn } from 'formik';
import React from 'react';
import { Radio } from 'semantic-ui-react';

type TProps = {
	label?: string;
	name: string;
};

export const RadioInput = ({ label, name }: TProps) => {
	return (
		<Field>
			{({ form }: FieldProps) => {
				const value = getIn(form.values, name);
				return <Radio label={label} toggle checked={value} onChange={(e) => form.setFieldValue(name, !value)} />;
			}}
		</Field>
	);
};
