import React, { useEffect, useState } from 'react';
import { IVocabulary } from '../../interfaces';
import { TOutputFormat } from '../utils/types/setupTypes';

type TProps = {
	readOnlyData: any;
	row: TOutputFormat;
	vocabulary: IVocabulary[];
};

export const RenderReadOnlyData = ({ readOnlyData, row, vocabulary }: TProps) => {
	const [data, setData] = useState('');
	useEffect(() => {
		if (readOnlyData) {
			if (row.vocabId && vocabulary) {
				const requiredVocab = vocabulary.find((vocabs) => String(vocabs.id) === String(row.vocabId));
				if (requiredVocab) {
					const vocabLabel = requiredVocab.vocabularyDetails.find((vocabDetail) => vocabDetail.value === readOnlyData[row.content]);
					if (vocabLabel) {
						setData(vocabLabel.label);
					} else {
						setData(readOnlyData[row.content]);
					}
				}
			} else {
				setData(readOnlyData[row.content]);
			}
		}
	}, [readOnlyData, row, vocabulary]);

	return <div style={{ display: 'inline-block' }}>{data}</div>;
};
