import { useReducer } from "react"

export type TApproveRejectModalState = {
    approvalModalOpen: boolean;
    approvalModalWithFileOpen: boolean;
    rejectionModalOpen: boolean;
}

const initialState: TApproveRejectModalState = {
    approvalModalOpen: false,
    approvalModalWithFileOpen: false,
    rejectionModalOpen: false,
}

export enum ApproveRejectModalActions {
    OPEN_APPROVAL = 'open-approval',
    OPEN_APPROVAL_WITH_FILE = 'open-approval-file',
    OPEN_REJECTION = 'open-rejection',
    CLOSE_MODALS = 'close-modals',
}

function reducer(state: TApproveRejectModalState, action: { type: ApproveRejectModalActions, data?: any }) {
    const { type, data } = action;
    switch (type) {
        case ApproveRejectModalActions.OPEN_APPROVAL:
            return {
                ...state, approvalModalOpen: true
            }
        case ApproveRejectModalActions.OPEN_APPROVAL_WITH_FILE:
            return {
                ...state, approvalModalWithFileOpen: true
            }
        case ApproveRejectModalActions.OPEN_REJECTION:
            return {
                ...state, rejectionModalOpen: true
            }
        case ApproveRejectModalActions.CLOSE_MODALS:
            return initialState
        default:
            return state;
    }

}

export const useApproveRejectModal = () => {
    const [state, dispatch] = useReducer(reducer, initialState)
    return {
        state,
        openApproval: () => dispatch({ type: ApproveRejectModalActions.OPEN_APPROVAL }),
        openFileApproval: () => dispatch({ type: ApproveRejectModalActions.OPEN_APPROVAL_WITH_FILE }),
        openRejection: () => dispatch({ type: ApproveRejectModalActions.OPEN_REJECTION }),
        closeModals: () => dispatch({ type: ApproveRejectModalActions.CLOSE_MODALS })
    }
}