import axios from 'axios';
import { ActionDispatch } from '../../interfaces/ReduxInterfaces';
import api from "../../utils/api";
import { getBackUrl } from "../../utils/config";
import { CURRENT_BUILD_PERMIT, FILE_STORAGE_CATEGORY } from '../../utils/constants';
import { getToken } from "../../utils/functionUtils";
import { getLocalStorage } from '../../utils/secureLS';
import { CLEAR_ERRORS, D_SET_FORM_DATA, F_CLEAR_SUCCESS, F_SET_SUCCESS, LOADING_MODAL, LOADING_UI, SET_ERRORS, STOP_LOADING, STOP_LOADING_MODAL } from "../types";

export const dynamicDeleteFile = (id: string) => async (dispatch: ActionDispatch) => {
    dispatch({ type: LOADING_MODAL });
    dispatch({ type: CLEAR_ERRORS });
    dispatch({ type: F_CLEAR_SUCCESS });

    try {
        const token = getToken();

        const getFileCatUrl = `${getBackUrl()}${api.fileUpload}${id}`;

        axios.defaults.headers.common['Authorization'] = token;

        const response = await axios.delete(getFileCatUrl);
        if (response.data.error) {
            dispatch({ type: SET_ERRORS, payload: response.data.error });
            return response;
        } else {
            dispatch({
                type: F_SET_SUCCESS,
                payload: { success: true, data: response.data }
            });
            return response;
        }
        // dispatch({ type: STOP_LOADING });
        // console.log('response ', response);
    } catch (err) {
        dispatch({ type: SET_ERRORS, payload: err });
        dispatch({ type: STOP_LOADING_MODAL });
        console.log(err);
    }
};

export const getDynamicUploadedFiles = () => async (dispatch: ActionDispatch) => {
    dispatch({ type: LOADING_UI });
    dispatch({ type: CLEAR_ERRORS });
    try {
        const token = getToken();
        const buildPermitObj = JSON.parse(
            getLocalStorage(CURRENT_BUILD_PERMIT)
        );

        const getUrl = `${getBackUrl()}${api.fileUpload}${
            buildPermitObj.applicantNo
        }`;

        axios.defaults.headers.common['Authorization'] = token;
        const response = await axios.get(getUrl);

        if (response.data.error) {
            dispatch({ type: SET_ERRORS, payload: response.data.error });
        } else {
            dispatch({
                type: D_SET_FORM_DATA,
                payload: {
                    files: response.data,
                    success: false
                }
            });
        }

        dispatch({ type: STOP_LOADING });
        // console.log('response ', response);
    } catch (err) {
        dispatch({ type: SET_ERRORS, payload: err });

        dispatch({ type: STOP_LOADING });
    }
};
