import * as React from 'react';
import JqxWindow from 'jqwidgets-scripts/jqwidgets-react-tsx/jqxwindow';
import { Button, SemanticICONS } from 'semantic-ui-react';
import { connect } from 'react-redux';
// import ShortcutHtml from '../../shared/ShortcutHtml';

//@ts-ignore
const mapStateToProps = (state) => ({
	shortcut: state.root.general.shortcut,
});

class DynamicGenericFloatingWindow extends React.PureComponent<{
	icon: SemanticICONS;
	label: string;
	children: any;
	shortcut: boolean;
	setFileUploadRef?: any;
	autoOpen?: any;
	allcategory?: any;
	onclose?: any;
	allupload?: any;
	button?: any;
	notification?: any;
	isRejected?: boolean;
}> {
	private myWindow = React.createRef<JqxWindow>();

	componentDidMount() {
		this.props.setFileUploadRef && this.props.setFileUploadRef(this.myWindow);
	}
	componentWillUnmount() {
		this.myWindow.current!.destroy();
		// this.myWindow.current!.focus();
	}

	public render() {
		const {
			label,
			icon,
			children,
			//@ts-ignore
			button,
			shortcut,
			allupload,
			allcategory,
			notification,
			isRejected = false,
			...rest
		} = this.props;

		return (
			<>
				{/* <span className={label} onClick={() => this.onWindowToggle()}> */}
				<Button
					size="tiny"
					compact
					icon={isRejected ? 'warning circle' : icon}
					className={isRejected ? 'danger-button' : 'primary-btn'}
					content={label}
					onClick={() => this.onWindowToggle()}
				/>
				{/* </span> */}

				<JqxWindow
					ref={this.myWindow}
					width={700}
					height={500}
					minWidth={200}
					maxWidth={1200}
					minHeight={200}
					position="bottom"
					maxHeight={900}
					//@ts-ignore
					autoOpen={this.props.autoOpen || false}
					title={label}
					showCollapseButton={true}
					// onClose={() => {
					//   //@ts-ignore
					//   this.props.allcategory && this.props.allcategory(false);
					//   this.props.allupload && this.props.allupload(false);
					// }}
				>
					<div>
						{/* {button && allupload && (
              //@ts-ignore
              <button onClick={() => allupload(true)}>All Category</button>
            )}
            {button && allcategory && (
              //@ts-ignore
              <button onClick={() => allcategory(true)}>All Category</button>
            )} */}

						{React.cloneElement(children, { ...rest })}
					</div>
				</JqxWindow>
			</>
		);
	}

	onWindowToggle = (): void => {
		this.myWindow.current!.isOpen() ? this.myWindow.current!.close() : this.myWindow.current!.open();
	};
}

export default connect(mapStateToProps)(DynamicGenericFloatingWindow);
