// 		if (serStatus == 'A' || serStatus === 'S' || enterBy == 'SubEngineer') {
// 			return true;
// 		} else {
// 			return false;
// 		}
// 	} else if (UserMain == 'R') {
// 		// if (erStatus == 'A') {
// 		//   return true;
// 		// } else {
// 		//   return false;
// 		// }
// 		// Rajasow doesn't have approve or reject permission.
// 		return false;
// 	} else {
// 		return true;
// 	}
