import { FormikProps } from 'formik';
import React, { useEffect, useState } from 'react';
import { Table } from 'semantic-ui-react';
import { IVocabulary } from '../../interfaces';
import { generateOutput } from '../utils/layoutParsers';
import { TInputTypeObject, TOutputFormat } from '../utils/types/setupTypes';
import { RenderReadOnlyData } from './RenderReadOnlyData';
import { RenderLayoutInput } from './RenderLayoutInput';
import { RenderUserInfo } from './RenderUserInfo';
import { IUserInfo } from '../../../../interfaces/ApiInterfaces';
import { ELayoutIdentifier } from '../utils/enums/LayoutIdentifiers';
import { RenderStaticImage } from './RenderStaticImage';
import { RenderComponents } from './RenderComponents';
import { EComponentIdentifiers } from '../utils/enums/ComponentIdentifiers';

export type TTableContent = {
	tableProps: { summary?: any; };
	header: [{ headerProps: any; headerRows: [{ headerRowProps: any; headerCells: [{ headerCellProps: any; headerCell: string }] }] }];
	body: [{ bodyProps: any; rows: [{ rowProps: any; cells: [{ cellProps: any; cell: string }] }] }];
};

type TProps = {
	row: TOutputFormat;
	formikProps: FormikProps<any>;
	inputTypes: TInputTypeObject[];
	vocabulary: IVocabulary[];
	permitData: any;
	staticFiles: any;
	userData?: IUserInfo;
	readOnlyData: any;
};

export const RenderLayoutTable = ({ row, readOnlyData, formikProps, inputTypes, vocabulary, permitData, userData, staticFiles }: TProps) => {
	const [tableContent, setTableContent] = useState<TTableContent>();
	useEffect(() => {
		try {
			const tableContent: TTableContent = JSON.parse(row.content);
			setTableContent(tableContent);
		} catch (error) {}
	}, [row]);

	const formatCell = (rawString: string) => {
		const output = generateOutput(rawString);
		return output;
	};

	try {
		if (tableContent) {
			let formWithThumbSignature = tableContent.tableProps.summary === 'form-with-thumb-signature' ? true : false;
			return (
				<Table {...tableContent.tableProps} className={!formWithThumbSignature ? "certificate-ui-table" : "certificate-ui-table-no-border"}>
					{tableContent.header &&
						tableContent.header.map((headers, headerIdx) => (
							<Table.Header key={headerIdx} {...headers.headerProps}>
								{headers.headerRows.map((headerRow, headerRowIdx) => (
									<Table.Row key={headerRowIdx} {...headerRow.headerRowProps}>
										{headerRow.headerCells.map((cell, headerCellIdx) => (
											<Table.HeaderCell key={headerCellIdx} {...cell.headerCellProps}>
												{cell.headerCell}
											</Table.HeaderCell>
										))}
									</Table.Row>
								))}
							</Table.Header>
						))}
					{tableContent.body &&
						tableContent.body.map((bdy, bdyIdx) => (
							<Table.Body key={bdyIdx} {...bdy.bodyProps}>
								{bdy.rows.map((row, rowIdx) => (
									<Table.Row key={rowIdx} {...row.rowProps}>
										{row.cells.map((cell, cellIdx) => {
											// const a = { 'text-align': 'left' };
											// console.log(cell.cellProps.style, a, 'cell');
											const output = formatCell(cell.cell);
											return (
												<Table.Cell key={cellIdx} {...cell.cellProps} style={cell.cellProps.style}>
													{output &&
														output.map((row) => (
															<React.Fragment key={row.id}>
																{row.input ? (
																	<div style={{ display: 'inline-block' }}>
																		<RenderLayoutInput
																			content={row.content}
																			formikProps={formikProps}
																			inputTypes={inputTypes}
																			vocabulary={vocabulary}
																			permitData={permitData}
																		/>
																	</div>
																) : row.type === ELayoutIdentifier.DATA ? (
																	<RenderReadOnlyData
																		vocabulary={vocabulary}
																		row={row}
																		readOnlyData={readOnlyData}
																	/>
																) : row.type === ELayoutIdentifier.DESIGNER_INFO ? (
																	<RenderReadOnlyData
																			vocabulary={vocabulary}
																			row={row}
																			readOnlyData={readOnlyData}
																	/>
																) : row.type === ELayoutIdentifier.USER_INFO ? (
																	<RenderUserInfo row={row} userData={userData} />
																) : row.type === ELayoutIdentifier.STATIC_IMAGE ? (
																	<RenderStaticImage row={row} staticFiles={staticFiles} />
																) : row.type === ELayoutIdentifier.COMPONENT ? (
																	<RenderComponents
																		row={row}
																		type={row.content as EComponentIdentifiers}
																		userInfo={userData || { organization: {} }}
																		permitData={permitData}
																	/>
																) : (
																	<span
																		// style={{
																		// 	display: row.inline ? 'inline-block' : 'inline',
																		// }}
																		dangerouslySetInnerHTML={{ __html: row.content }}
																	/>
																)}
															</React.Fragment>
														))}
												</Table.Cell>
											);
										})}
									</Table.Row>
								))}
							</Table.Body>
						))}
				</Table>
			);
		} else {
			return null;
		}
	} catch {
		return null;
	}
};
