import { Field, FieldProps, getIn } from 'formik';
import React from 'react';
import { Form, Input, InputOnChangeData, Label } from 'semantic-ui-react';

type TProps = {
	label: string;
	name: string;
	onChange?: (event: React.ChangeEvent<HTMLInputElement>, data: InputOnChangeData) => void;
	formValue?: string;
	size?: 'big' | 'small' | 'mini' | 'large' | 'huge' | 'massive' | undefined;
	disabled?: boolean;
};

export const NormalInput = ({ label, name, formValue, onChange, size = 'small', disabled = false }: TProps) => {
	return (
		<Field>
			{({ form }: FieldProps) => {
				const error = getIn(form.errors, name);
				const value = getIn(form.values, name);
				return (
					<Form.Field error={!!error}>
						{<span>{label}</span>}
						<Input name={name} disabled={disabled} onChange={onChange || form.handleChange} value={formValue || value} size={size} />
						{error && (
							<Label pointing prompt size="large">
								{error}
							</Label>
						)}
					</Form.Field>
				);
			}}
		</Field>
	);
};

type TUncontrolledProps = {
	label: string;
	name: string;
	onChange: (event: React.ChangeEvent<HTMLInputElement>, data: InputOnChangeData) => void;
	formValue?: string;
	error?: string;
	size?: 'big' | 'small' | 'mini' | 'large' | 'huge' | 'massive' | undefined;
};

export const NormalInputControlled = ({ label, name, formValue, onChange, size = 'small', error }: TUncontrolledProps) => {
	return (
		<Form.Field error={!!error}>
			{<span>{label}</span>}
			<Input name={name} onChange={onChange} value={formValue} size={size} />
			{error && (
				<Label pointing prompt size="large">
					{error}
				</Label>
			)}
		</Form.Field>
	);
};
