import { IFile, IFileCategory } from "../../interfaces/FileInterfaces";
import { TReduxState } from "../storeStateTypes";

export const selectNeedsFileView = (state: TReduxState): boolean => {
    const dynamicState = state.root.dynamic;
    if (dynamicState && dynamicState.fileCategories && dynamicState.fileCategories.length > 0) {
        return dynamicState.fileCategories.some(category => dynamicState.cFormName && String(category.formId) && String(category.formId) === String(dynamicState.cFormName.id));
    } else {
        return false;
    }
}

export const selectExistingFiles = (state: TReduxState): IFile[] | null => {
    const dynamicState = state.root.dynamic;
    if (dynamicState && dynamicState.fileCategories && dynamicState.fileCategories.length > 0) {
        return dynamicState.files.filter(files => dynamicState.cFormName && String(files.fileType.formName.id) && String(files.fileType.formName.id) === String(dynamicState.cFormName.id));
    } else {
        return null;
    }
}

export const selectRequiredFileCategories = (state: TReduxState): IFileCategory[] | null => {

    const dynamicState = state.root.dynamic;
    if (dynamicState && dynamicState.fileCategories && dynamicState.fileCategories.length > 0) {
        return dynamicState.fileCategories.filter(category => dynamicState.cFormName && String(category.formId) && String(category.formId) === String(dynamicState.cFormName.id));
    } else {
        return null;
    }

}