import React, { useState } from 'react';
import { Form, Select } from 'semantic-ui-react';
import { statusOption } from '../../../utils/optionUtils';
import { TOption } from '../interfaces';

type TProps = {
	approvalUserOptions: TOption[];
	setStatus: (value: string) => void;
	setUserType: (value: string) => void;
};
export const PermissionPicker = ({ approvalUserOptions, setStatus, setUserType }: TProps) => {
	return (
		<Form>
			<Form.Group>
				<Form.Field>
					<label>Choose Approve By User</label>
					<Select
						id={'permission-select'}
						label=""
						name="formPermissions"
						options={approvalUserOptions}
						onChange={(e, { value }) => {
							setUserType(String(value));
						}}
					/>
				</Form.Field>
				<Form.Field>
					<label>Status</label>
					<Select
						id={'permission-select'}
						label=""
						name="formPermissions"
						options={statusOption}
						onChange={(e, { value }) => {
							setStatus(String(value));
						}}
					/>
				</Form.Field>
			</Form.Group>
		</Form>
	);
};
