import React, { Dispatch, useEffect, useState } from 'react';
import { Divider, Form } from 'semantic-ui-react';
import { statusOption } from '../../../utils/optionUtils';
import { isStringEmpty } from '../../../utils/stringUtils';
import { NormalInputControlled } from '../../shared/fields/NormalInput';
import { SelectInputV2 } from '../../shared/fields/SelectInputV2';
import { FormFieldActions } from '../hooks/useTableFormField';
import { IEBPSColumns, IEBPSTableColumns, IFormFieldList, TOption } from '../interfaces';

type TProps = {
	dispatch: Dispatch<{ type: FormFieldActions; data: any }>;
	state: IFormFieldList;
	tableOptions: TOption[];
	vocabularyOptions: TOption[];
	tables: IEBPSTableColumns[];
	tableId: number;
};

export const FormFieldPicker = ({ dispatch, state, tableOptions, vocabularyOptions, tables, tableId }: TProps) => {
	const { name, placeholder, required, readOnly, ebpsColumnId, referencedColumnId, referencedTableId, vocabularyId } = state;
	const [columnOptions, setColumnOptions] = useState<(TOption & { needsVocab?: boolean; vocabId?: number | null })[]>([]);
	const [ownColumnOptions, setOwnColumnOptions] = useState<(TOption & { needsVocab?: boolean; vocabId?: number | null })[]>([]);
	const [needsVocabulary, setNeedsVocabulary] = useState(false);

	useEffect(() => {
		const tableRow = tables.find((row) => row.id === tableId);
		if (tableRow) {
			const options = tableRow.ebpsColumns.map((row: IEBPSColumns, idx) => ({
				key: idx,
				value: row.id || 0,
				text: row.name,
				needsVocab: row.reference === 'vocab',
			}));
			setOwnColumnOptions(options);
		}
	}, [tables, tableId]);

	useEffect(() => {
		const tableRow = tables.find((row) => String(row.id) === String(referencedTableId));

		if (tableRow) {
			const options = tableRow.ebpsColumns.map((row: IEBPSColumns, idx) => ({
				key: idx,
				value: row.id || 0,
				text: row.name,
				needsVocab: row.reference === 'vocab',
			}));
			setColumnOptions(options);
		}
	}, [referencedTableId, tables]);

	const handleColumnSelect = (value: string | number | boolean | (string | number | boolean)[] | undefined, action: FormFieldActions) => {
		let optionRow = null;

		if (action === FormFieldActions.EBPS_COLUMN_ID) {
			optionRow = ownColumnOptions.find((option) => option.value === value);
		} else {
			optionRow = columnOptions.find((option) => option.value === value);
		}

		if (optionRow) {
			if (optionRow.needsVocab) {
				setNeedsVocabulary(true);
			} else {
				setNeedsVocabulary(false);
			}
		}
		dispatch({ type: action, data: value });
	};

	return (
		<Form>
			<Form.Group>
				<NormalInputControlled
					error={isStringEmpty(name) ? 'Required Field' : ''}
					name="name"
					formValue={name}
					label="Name"
					onChange={(e, { value }) => dispatch({ type: FormFieldActions.NAME, data: value })}
				/>
				<NormalInputControlled
					name="placeholder"
					label="Placeholder"
					formValue={placeholder}
					onChange={(e, { value }) => dispatch({ type: FormFieldActions.PLACEHOLDER, data: value })}
				/>
				<SelectInputV2
					controlled
					name="required"
					formValue={required}
					options={statusOption}
					label="Required?"
					onChange={(e, { value }) => dispatch({ type: FormFieldActions.REQUIRED, data: value })}
				/>
			</Form.Group>
			<Form.Group>
				<SelectInputV2
					name="readOnly"
					controlled
					formValue={readOnly}
					options={statusOption}
					label="Read Only?"
					onChange={(e, { value }) => {
						dispatch({ type: FormFieldActions.READ_ONLY, data: value });
						// dispatch({ type: FormFieldActions.EBPS_COLUMN_ID, data: undefined });
					}}
				/>
				{needsVocabulary ? (
					<SelectInputV2
						name="vocabularyId"
						formValue={vocabularyId}
						options={vocabularyOptions}
						label="Choose Vocabulary"
						onChange={(e, { value }) => dispatch({ type: FormFieldActions.VOCABULARY_ID, data: value })}
					/>
				) : null}
			</Form.Group>
			{/* {readOnly === 'N' ? ( */}
			{/* <> */}
			<Divider horizontal>Choose A Column</Divider>
			<Form.Group>
				{/* <SelectInputV2
					name="table"
					formValue={currentTable}
					options={tableOptions}
					label="Choose table"
					onChange={(e, { value }) => setCurrentTable(parseInt(String(value)))}
				/> */}
				<SelectInputV2
					clearable
					searchable
					controlled
					name="column"
					formValue={ebpsColumnId}
					options={ownColumnOptions}
					label="Ebps Column"
					onChange={(e, { value }) => handleColumnSelect(value, FormFieldActions.EBPS_COLUMN_ID)}
				/>
			</Form.Group>
			{/* </> */}
			{/* ) : null} */}
			<Divider horizontal>Choose A Reference Column</Divider>
			<Form.Group>
				<SelectInputV2
					clearable
					searchable
					controlled
					name="referencedTableId"
					formValue={referencedTableId}
					options={tableOptions}
					label="Choose reference table"
					onChange={(e, { value }) => dispatch({ type: FormFieldActions.REFERENCE_TABLE, data: value })}
				/>
				<SelectInputV2
					clearable
					searchable
					controlled
					name="referencedColumnId"
					formValue={referencedColumnId}
					options={columnOptions}
					label="Reference Column"
					onChange={(e, { value }) => handleColumnSelect(value, FormFieldActions.REFERENCE_COLUMN)}
				/>
			</Form.Group>
		</Form>
	);
};
