const initialValues = {
	id: 0,
	name: '',
	columnName: '',
	type: EReadOnlyTypes.DATA,
	meta: '',
};

export const AddReadOnlyType = ({
	setReadOnlyTypes,
	open,
	handleClose,
	editData,
}: {
	setReadOnlyTypes: React.Dispatch<React.SetStateAction<TReadOnlyTypeObject[]>>;
	handleClose: () => void;
	open: boolean;
	editData?: TReadOnlyTypeObject | null;
}) => {
	const [typeOptions, setTypeOptions] = useState(inputTypeOptions);

	return (
		<div>
			<Formik
				// validationSchema={schema}
				enableReinitialize
				initialValues={editData || initialValues}
				onSubmit={(values, actions) => {
					actions.setSubmitting(true);
					setReadOnlyTypes((inputTypes) => {
						const filtered = inputTypes.filter((type) => editData && type.id !== editData.id);
						if (values.type === EReadOnlyTypes.DATA) {
							values.meta = values.columnName;
						}
						return filtered.concat(values);
					});
					actions.setSubmitting(false);
					handleClose();
				}}
			>
				{({ values, handleSubmit, isSubmitting }) => (
					<div>
						<Modal open={open} onClose={handleClose} size="tiny" closeIcon>
							<ModalHeader>Edit Read Only Data</ModalHeader>
							<ModalContent>
								<Form loading={isSubmitting}>
									<SelectInputV2 options={readOnlyTypeOptions} name="type" label="Select Data Type" />
									{values.type === EReadOnlyTypes.DESIGNER_INFO ? (
										<SelectInputV2 options={userInfoOptions} name="meta" label="Select Designer Info" />
									) : values.type === EReadOnlyTypes.STATIC_IMAGE ? (
										<SelectInputV2 options={staticFileOptions} name="meta" label="Select Static Image" />
									) : values.type === EReadOnlyTypes.FLOOR_VALUES ? (
										<SelectInputV2 options={floorValueOptions} name="meta" label="Select Floor Value" />
									) : values.type === EReadOnlyTypes.SURROUNDING_VALUES ? (
										<SelectInputV2 options={surroundingValueOptions} name="meta" label="Select Surrounding Value" />
									) : values.type === EReadOnlyTypes.USERTYPE_DATA ? (
										<SelectInputV2 options={statusDataOptions} name="meta" label="Select Status Value" />
									): null}
								</Form>
							</ModalContent>
							<ModalActions>
								<Button content="Cancel" size="tiny" compact icon="close" className="danger-button" onClick={handleClose} />
								<Button
									content="Save Form Input"
									size="tiny"
									compact
									icon="save"
									type="button"
									className="success-button"
									onClick={() => handleSubmit()}
								/>
							</ModalActions>
						</Modal>
					</div>
				)}
			</Formik>
		</div>
	);
};
