import React, { useState, useEffect, useCallback } from 'react';
import { useHistory } from 'react-router';
import { Button, ButtonContent, Form, Icon, Segment } from 'semantic-ui-react';
import { useAdminSetup } from '../../hooks/useAdminSetup';
import api from '../../utils/api';
import { formNameMasterData } from '../../utils/data/ebps-setup/tableSetup';
import { showErrorToast } from '../../utils/functionUtils';
import { validateString } from '../../utils/validationUtils';
import * as Yup from 'yup';
import AdminContainer from '../../containers/base/AdminContainer';
import { DeleteButtonV2, EditButtonV2 } from '../ebps-setup/forms/Buttons';
import { Formik } from 'formik';
import Helmet from 'react-helmet';
import { SelectField } from '../shared/Select';
import { IEBPSTableColumns, IFormNameMaster, TOption } from './interfaces';
import { NormalInput } from '../shared/fields/NormalInput';
import { getAdminFilteredOptions } from '../../utils/userTypeUtils';
import { getOptionText } from '../../utils/dataUtils';
import { commonMessages } from '../../utils/data/validationData';
import { ModalComponent } from '../ebps-setup/shared/ModalComponent';
import { SetupTable } from '../ebps-setup/shared/SetupTable';
import { InputColumnFilter } from '../shared/tableFilters/InputColumnFilter';
import { SelectColumnFilter } from '../shared/tableFilters/SelectColumnFilter';

const tableHeadings = formNameMasterData.table.tableHeadings;
const modalData = formNameMasterData.modal;

const initialState = {
	id: '',
	name: '',
	viewUrl: '',
	pageCode: '',
	tableId: '',
	enterBy: 'D',
	formFieldsList: [],
	formPermissions: [],
};

const schema = Yup.object().shape({
	name: validateString,
	tableId: validateString,
	viewUrl: validateString,
	pageCode: Yup.string().typeError(commonMessages.required).max(5, 'Cant be longer than 5 letters').required(commonMessages.required),
	enterBy: validateString,
});

const FormNameComponent = ({ adminData, success, postAdminDataByUrl, deleteAdminDataByUrl, errors, loading }: any) => {
	const {
		isEditing,
		isDeleting,
		formState,
		openAddModal,
		openDeleteModal,
		handleEdit,
		handleDelete,
		setOpenAddModal,
		setOpenDeleteModal,
		resetFormState,
	} = useAdminSetup(initialState, success);

	const history = useHistory();
	const [userTypeOptions, setUserTypeOptions] = useState<TOption[]>([]);
	const [formNameMaster, setFormNameMaster] = useState<IFormNameMaster[]>([]);
	const [tableOptions, setTableOptions] = useState<TOption[]>([]);
	// const [tables, setTables] = useState<IEBPSTableColumns[]>([]);

	useEffect(() => {
		if (adminData.userTypeMaster && Array.isArray(adminData.userTypeMaster)) {
			const enterByOptions = getAdminFilteredOptions(adminData.userTypeMaster);
			setUserTypeOptions(enterByOptions);
		}
	}, [adminData.userTypeMaster]);

	useEffect(() => {
		if (adminData.tables && Array.isArray(adminData.tables)) {
			const tableOptions: TOption[] = adminData.tables.map((row: IEBPSTableColumns, idx: number) => ({
				key: idx,
				value: row.id,
				text: row.name,
			}));

			setTableOptions(tableOptions);
		}
	}, [adminData.userTypeMaster, adminData.tables]);

	useEffect(() => {
		if (adminData.formNameMaster && Array.isArray(adminData.formNameMaster)) {
			setFormNameMaster(adminData.formNameMaster);
		}
	}, [adminData.formNameMaster]);

	const renderButtons = useCallback(
		(row: IFormNameMaster) => {
			return (
				<>
					<Button
						title="Edit Permission and Fields"
						type="reset"
						className="success-button"
						onClick={() => {
							history.push(`/admin/form-name-details/${row.id}`);
						}}
						icon="list"
						size="tiny"
						compact
					></Button>
					<EditButtonV2 row={row} handleEdit={handleEdit} />
					<DeleteButtonV2 handleDelete={handleDelete} row={row} />
				</>
			);
		},
		[handleEdit, handleDelete]
	);

	const columns = React.useMemo(() => {
		return [
			{ Header: tableHeadings.id, accessor: 'id' },
			{ Header: tableHeadings.name, accessor: 'name', Filter: InputColumnFilter, filter: 'includes', switchable: true },
			{
				Header: tableHeadings.tableName,
				accessor: (originalRow: IFormNameMaster) => {
					return getOptionText(originalRow.tableId, tableOptions);
				},
				Filter: InputColumnFilter,
				filter: 'includes',
			},
			{
				Header: tableHeadings.enterBy,
				accessor: (originalRow: IFormNameMaster) => {
					return getOptionText(originalRow.enterBy, userTypeOptions);
				},
				Filter: SelectColumnFilter,
				filter: 'includes',
				id: 'enterBy',
				defaultOption: 'All',
			},
			{
				Header: tableHeadings.viewUrl,
				accessor: 'viewUrl',
				Filter: InputColumnFilter,
				filter: 'includes',
			},
			{
				Header: 'Actions',
				semanticWidth: 2,
				Cell: ({ row }: { row: { original: IFormNameMaster } }) => renderButtons(row.original),
			},
		];
	}, [tableOptions, renderButtons]);

	return (
		<div className="english-div">
			<Segment inverted className="tableSectionHeader">
				{formNameMasterData.table.tableHeader}
				<Button
					basic
					style={{ right: 2, top: 4, position: 'absolute' }}
					inverted
					type="reset"
					onClick={() => {
						resetFormState();
						setOpenAddModal(true);
					}}
				>
					<ButtonContent>
						<Icon.Group>
							<Icon name="file outline" />
							<Icon inverted corner name="add" />
						</Icon.Group>
						{`  ${modalData.titleAdd}`}
					</ButtonContent>
				</Button>
			</Segment>
			<Formik
				enableReinitialize
				initialValues={{ ...formState }}
				validationSchema={!isDeleting ? schema : null}
				onSubmit={async (values, actions) => {
					actions.setSubmitting(true);
					try {
						if (isEditing) {
							await postAdminDataByUrl(api.d_formNameMaster, values);
						} else if (isDeleting) {
							await deleteAdminDataByUrl(api.d_formNameMaster, formState.id);
						} else {
							await postAdminDataByUrl(api.d_formNameMaster, values);
						}
						actions.setSubmitting(false);
						resetFormState();
					} catch (error) {
						console.log('Error in menu master post', error);
						showErrorToast('Something went wrong.');
						actions.setSubmitting(false);
					}
				}}
			>
				{({ handleSubmit, isSubmitting }) => (
					<>
						<Helmet title="Table Setup" />
						<ModalComponent errors={errors} loading={isSubmitting || loading} handleSubmit={handleSubmit}>
							<ModalComponent.AdminModal
								modalId="form-add-modal"
								open={openAddModal}
								handleClose={() => setOpenAddModal(false)}
								title={isEditing ? 'Edit Form Name' : 'Add Form Name'}
								saveText={isEditing ? 'Edit Form Name' : 'Add Form Name'}
							>
								<Form loading={isSubmitting || loading}>
									<Form.Group widths="equal">
										<NormalInput name="name" label={tableHeadings.name} />
										<NormalInput name="viewUrl" label={tableHeadings.viewUrl} />
										<NormalInput name="pageCode" label={tableHeadings.pageCode} />
									</Form.Group>
									<Form.Group>
										<SelectField name="tableId" label={tableHeadings.tableName} options={tableOptions} />
										<SelectField name="enterBy" label={tableHeadings.enterBy} options={userTypeOptions} />
									</Form.Group>
								</Form>
							</ModalComponent.AdminModal>{' '}
							<ModalComponent.AdminModal
								modalId="delete-form"
								open={openDeleteModal}
								handleClose={() => setOpenDeleteModal(false)}
								title={modalData.delete.title}
								saveText={modalData.deleteText}
							>
								<Form loading={isSubmitting || loading}>{modalData.delete.content}</Form>
							</ModalComponent.AdminModal>
						</ModalComponent>
					</>
				)}
			</Formik>

			<SetupTable columns={columns} data={formNameMaster} />
		</div>
	);
};

export const FormNameMaster = (parentProps: any) => (
	//@ts-ignore
	<AdminContainer
		//@ts-ignore
		api={[
			{ api: api.d_ebpsTables, objName: 'tables' },
			{ api: api.d_formNameMaster, objName: 'formNameMaster' },
			{ api: api.userType, objName: 'userTypeMaster' },
		]}
		//@ts-ignore
		updateApi={[{ api: api.formNameMaster, objName: 'formNameMaster' }]}
		//@ts-ignore
		render={(props: any) => <FormNameComponent {...props} parentProps={parentProps} />}
	/>
);
