import React from 'react';
import { IPermitData } from '../../../../interfaces/ApiInterfaces';
import { FooterSignatureMultiline } from '../../../loggedInComponents/forms/formComponents/FooterSignature';
import { LetterHeadFlex, LetterHeadPhoto } from '../../../shared/LetterHead';
import { EComponentIdentifiers } from '../utils/enums/ComponentIdentifiers';
import { TOutputFormat } from '../utils/types/setupTypes';
import { ThumbSignature } from '../../../loggedInComponents/forms/formComponents/FingerprintSignature'

type TProps = {
	type: EComponentIdentifiers;
	userInfo: any;
	permitData?: IPermitData;
	row: TOutputFormat;
};

export const RenderComponents = ({ row, type, userInfo, permitData }: TProps) => {
	switch (type) {
		case EComponentIdentifiers.LETTER_HEAD:
			return <LetterHeadFlex userInfo={userInfo} />;
		case EComponentIdentifiers.LETTER_HEAD_PHOTO:
			return <LetterHeadPhoto compact={true} userInfo={userInfo} photo={permitData ? permitData.photo : ''} />;
		case EComponentIdentifiers.FOOTER_SIGNATURE:
			if (row.meta) {
				const eachSignature = row.meta.split(';');
				const signatures = eachSignature.map((s) => s.split('|'));

				return <FooterSignatureMultiline designations={signatures} />;
			}
			return null;
		case EComponentIdentifiers.THUMB_SIGNATURE:
			return <ThumbSignature alignment={'horizontal'} />;
		case EComponentIdentifiers.THUMB_SIGNATURE_VERTICAL:
			return <ThumbSignature alignment={'vertical-left'} />;
		default:
			return null;
	}
};
