import React, { useState, useEffect } from 'react';
import AdminContainer from '../../containers/base/AdminContainer';
import api from '../../utils/api';
import { Segment, Table, Button, ButtonContent, Icon, Form, Label } from 'semantic-ui-react';
import { Formik, Field } from 'formik';
import { AdminModalContainer } from './AdminModalContainer';
import { showErrorToast } from '../../utils/functionUtils';
import { useAdminSetup } from '../../hooks/useAdminSetup';
import { EditButtonV2, DeleteButtonV2 } from './forms/Buttons';
import { SelectInput } from '../shared/Select';
import { statusOption } from '../../utils/optionUtils';
import Helmet from 'react-helmet';
import { tableSetupData } from '../../utils/data/ebps-setup/tableSetup';
import { useHistory } from 'react-router';
import { tablesSchema } from '../dynamic-setup/dynamicSetupSchemas';
import { StatusLabel } from '../dynamic-setup/shared/StatusLabel';

const tableHeadings = tableSetupData.table.tableHeadings;
const modalData = tableSetupData.modal;

const initialState = {
	id: '',
	menu: '',
	menuType: '',
	url: '',
	ebpsColumns: [],
};

const TableSetupComponent = ({ adminData, success, postAdminDataByUrl, deleteAdminDataByUrl,  errors, loading }) => {
	const {
		isEditing,
		isDeleting,
		formState,
		openAddModal,
		openDeleteModal,
		handleAdd,
		handleEdit,
		handleDelete,
		setOpenAddModal,
		setOpenDeleteModal,
	} = useAdminSetup(initialState, success);

	const [tables, setTables] = useState([]);

	const history = useHistory();
	useEffect(() => {
		if (adminData.tables && Array.isArray(adminData.tables)) {
			setTables(adminData.tables);
		}
	}, [adminData.tables]);

	const renderButtons = (row) => {
		return (
			<>
				<Button
					title="Add Columns"
					type="reset"
					className="success-button"
					onClick={() => {
						history.push(`/admin/edit-table-columns/${row.id}`);
					}}
					icon="list"
					size="tiny"
					compact
				>
				</Button>
				<EditButtonV2 row={{ ...row, id: parseInt(row.id) }} handleEdit={handleEdit} />
				<DeleteButtonV2 handleDelete={handleDelete} row={row} />
			</>
		);
	};

	return (
		<div className="english-div">
			<Formik
				enableReinitialize
				initialValues={{ ...formState }}
				validationSchema={!isDeleting ? tablesSchema : null}
				onSubmit={async (values, actions) => {
					actions.setSubmitting(true);
					try {
						if (isEditing) {
							postAdminDataByUrl(api.d_ebpsTables, values);
						} else if (isDeleting) {
							deleteAdminDataByUrl(api.d_ebpsTables, formState.id);
						} else {
							postAdminDataByUrl(api.d_ebpsTables, values);
						}
						actions.setSubmitting(false);
					} catch (error) {
						console.log('Error in menu master post', error);
						showErrorToast('Something went wrong.');
						actions.setSubmitting(false);
					}
				}}
			>
				{({ handleSubmit, isSubmitting, errors: formErrors, values, setFieldValue }) => (
					<>
						<Helmet title="Table Setup" />
						<AdminModalContainer
							size="small"
							open={openAddModal}
							scroll={false}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={() => setOpenAddModal(false)}
							title={isEditing ? modalData.titleEdit : modalData.titleAdd}
							saveText={isEditing ? modalData.editText : modalData.saveText}
						>
							<Form loading={isSubmitting || loading}>
								<Form.Group widths="equal">
									<Form.Field error={!!formErrors.name}>
										{<span>{tableHeadings.name}</span>}
										<Field name="name" />
										{formErrors.name && (
											<Label pointing prompt size="large">
												{formErrors.name}
											</Label>
										)}
									</Form.Field>
									<Form.Field error={!!formErrors.tableName}>
										{<span>{tableHeadings.tableName}</span>}
										<Field name="tableName" />
										{formErrors.tableName && (
											<Label pointing prompt size="large">
												{formErrors.tableName}
											</Label>
										)}
									</Form.Field>
								</Form.Group>
								<Form.Group>
									<Form.Field>
										<SelectInput label={tableHeadings.active} name="active" options={statusOption} />
									</Form.Field>
								</Form.Group>
							</Form>
						</AdminModalContainer>
						<AdminModalContainer
							open={openDeleteModal}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={() => setOpenDeleteModal(false)}
							title={modalData.delete.title}
							saveText={modalData.deleteText}
						>
							<Form loading={isSubmitting || loading}>{modalData.delete.content}</Form>
						</AdminModalContainer>
					</>
				)}
			</Formik>
			<Segment inverted className="tableSectionHeader">
				{tableSetupData.table.tableHeader}
				<Button
					basic
					style={{ right: 6, top: 8, position: 'absolute' }}
					inverted
					type="reset"
					onClick={() => {
						handleAdd();
					}}
				>
					<ButtonContent>
						<Icon.Group>
							<Icon name="file outline" />
							<Icon inverted corner name="add" />
						</Icon.Group>
						{`  ${modalData.titleAdd}`}
					</ButtonContent>
				</Button>
			</Segment>
			<Table sortable striped celled compact="very" size="small">
				<Table.Header>
					<Table.Row>
						<Table.HeaderCell>{tableHeadings.id}</Table.HeaderCell>
						<Table.HeaderCell>{tableHeadings.name}</Table.HeaderCell>
						<Table.HeaderCell>{tableHeadings.tableName}</Table.HeaderCell>
						<Table.HeaderCell>{tableHeadings.active}</Table.HeaderCell>
						<Table.HeaderCell>{tableHeadings.actions}</Table.HeaderCell>
					</Table.Row>
				</Table.Header>
				<Table.Body>
					{tables.length > 0 ? (
						tables.map((row) => (
							<Table.Row key={row.id}>
								<Table.Cell>{row.id}</Table.Cell>
								<Table.Cell>{row.name}</Table.Cell>
								<Table.Cell>{row.tableName}</Table.Cell>
								<Table.Cell>
									<StatusLabel status={row.active} />
								</Table.Cell>
								<Table.Cell>{renderButtons(row)}</Table.Cell>
							</Table.Row>
						))
					) : (
						<Table.Row>
							<Table.Cell colSpan="7">
								<Segment textAlign="center">No Data Found.</Segment>
							</Table.Cell>
						</Table.Row>
					)}
				</Table.Body>
			</Table>
		</div>
	);
};

export const TableSetup = (parentProps) => (
	<AdminContainer
		api={[{ api: api.d_ebpsTables, objName: 'tables' }]}
		updateApi={[{ api: api.d_ebpsTables, objName: 'tables' }]}
		render={(props) => <TableSetupComponent {...props} parentProps={parentProps} />}
	/>
);
