import { Formik } from 'formik';
import React, { useState } from 'react';
import { connect, useSelector } from 'react-redux';
import { Button, Dimmer, Form, Icon, Label, Loader, Modal, Segment } from 'semantic-ui-react';
import { selectCurrentIds } from '../../../store/selectors/currentFormSelectors';
import { EApproveRejectModule, TReduxState, TSuccess } from '../../../store/storeStateTypes';
import { putDynamicApprovalByUrl } from '../../../store/actions/dynamicFormActions';
import { approveReject } from '../../../utils/data/genericData';
import ErrorDisplay from '../../shared/ErrorDisplay';
import { MultiLangTextArea } from '../inputFields/MultiLangTextArea';
import { selectExistingFiles, selectNeedsFileView, selectRequiredFileCategories } from '../../../store/selectors/dynamicSelectors';
import { SingleFileView } from '../../loggedInComponents/forms/SingleFIleView';
import { FileViewOnly } from '../../shared/file/FileViewOnly';
import { IFile, IFileCategory } from '../../../interfaces/FileInterfaces';

type TProps = {
	open: boolean;
	onClose: () => void;
	url: string;
	putDynamicApprovalByUrl: (url: string, data: any, module: EApproveRejectModule) => void;
	success: TSuccess | undefined;
	loadingModal: boolean;
	applicationId: string | number;
	formId: string | number;
	history: any;
	content?: string;
	title?: string;
	apiErrors: any;
};

function DynamicApprovalModalComponent({
	open,
	onClose,
	url,
	loadingModal,
	applicationId,
	formId,
	putDynamicApprovalByUrl,
	success,
	history,
	content = approveReject.approve.content,
	title = approveReject.approve.title,
	apiErrors,
}: TProps) {
	const [error, setError] = useState('');
	const [isNepali, setIsNepali] = useState(false);

	const hasFileView = useSelector(selectNeedsFileView);
	const requiredFiles = useSelector<TReduxState, IFile[] | null>(selectExistingFiles);
	const requiredFileCategories = useSelector<TReduxState, IFileCategory[] | null>(selectRequiredFileCategories);

	// useEffect(() => {
	// 	if (success && success.success) handleDynamicSuccess({}, formId, applicationId, history, success);
	// }, [success]);

	const handleClose = () => {
		onClose();
		setError('');
	};

	return (
		<div>
			<Modal key="approval-modal" closeIcon open={open} onClose={handleClose}>
				<Modal.Header>{title ? title : approveReject.approve.title}</Modal.Header>
				<>
					<Dimmer active={loadingModal} inverted>
						<Loader active={loadingModal} inline="centered">
							Sending Approval...
						</Loader>
					</Dimmer>
					<>
						<Formik
							initialValues={{ status: '', comment: '' }}
							onSubmit={async (values, actions) => {
								values.status = 'A';
								try {
									await putDynamicApprovalByUrl(url, values, EApproveRejectModule.APPROVE);
								} catch (err) {
									console.log('Message', err);
									setError('Network Error');
								}
							}}
							render={({ handleSubmit, errors, setFieldValue, handleChange, values }) => {
								return (
									<Form loading={loadingModal}>
										{apiErrors && <ErrorDisplay message={apiErrors.message} />}
										{error && <ErrorDisplay message={error} />}
										<Modal.Content scrolling>
											<Segment>
												<h3>{content ? content : approveReject.approve.content}</h3>
												{hasFileView ? <FileViewOnly files={requiredFiles} fileCategories={requiredFileCategories} /> : null}
												{/* //@ts-ignore */}
												<MultiLangTextArea
													isNepali={isNepali}
													name="comment"
													onChange={handleChange}
													setFieldValue={setFieldValue}
													value={values.comment}
													error={errors.comment}
													label={approveReject.approve.comment}
													toggler={
														<Label
															as="a"
															className="lang-selector"
															onClick={() => setIsNepali((prevState) => !prevState)}
														>
															<Icon name="keyboard" />
															<span>{isNepali ? 'ने' : 'en'}</span>
														</Label>
													}
													cols={100}
													rows={4}
													pointing="above"
												/>
											</Segment>
										</Modal.Content>
										<Modal.Actions>
											<Segment textAlign="right">
												<Button negative onClick={handleClose}>
													{approveReject.approve.close}
												</Button>
												<Button
													positive
													icon="checkmark"
													labelPosition="right"
													content={approveReject.approve.save}
													// @ts-ignore
													onClick={handleSubmit}
												/>
											</Segment>
										</Modal.Actions>
									</Form>
								);
							}}
						/>
					</>
				</>
			</Modal>
		</div>
	);
}

const mapDispatchToProps = { putDynamicApprovalByUrl };
const mapStateToProps = (state: TReduxState) => {
	const { applicationId, formId } = selectCurrentIds(state);
	return {
		loadingModal: state.root.ui.loadingModal,
		success: state.root.formData.success,
		applicationId,
		formId,
		apiErrors: state.root.ui.errors,
	};
};

export const DynamicApprovalModal = connect(mapStateToProps, mapDispatchToProps)(DynamicApprovalModalComponent);
