import React, { useState } from 'react';
import { Button, Input } from 'semantic-ui-react';
import { getInputType, translate } from '../../../utils/langUtils';

export const InputColumnFilter = ({ column: { filterValue, setFilter, switchable = false } }) => {
	const [isNepali, setIsNepali] = useState(false);

	// Render input filter
	return (
		<Input
			size="mini"
			icon={switchable ? null : 'search'}
			action={
				switchable ? (
					<Button
						size="tiny"
						title="Switch Keyboard"
						compact
						className="primary-btn-light"
						content={isNepali ? 'ne' : 'en'}
						icon="keyboard outline"
						onClick={() => setIsNepali((prevState) => !prevState)}
					/>
				) : null
			}
			value={filterValue || ''}
			onChange={(e) => {
				if (isNepali) {
					const val = translate(e, getInputType());
					setFilter(val);
				} else {
					setFilter(e.target.value);
				}
			}}
		/>
	);
};
