import { getIn } from 'formik';
import React from 'react';
import { Form } from 'semantic-ui-react';
import { SelectInputV2 } from '../../shared/fields/SelectInputV2';
import { TOption } from '../interfaces';
import { ESurroundingValues } from './utils/enums/inputEnums';

export const surroundingValueOptions = [
	{ key: 11, value: ESurroundingValues.SIDE1_SANDHIYAR, text: 'East Land Owner' },
	{ key: 12, value: ESurroundingValues.SIDE1_KITTA_NO, text: 'East Kitta No' },
	{ key: 13, value: ESurroundingValues.SIDE1_FEET, text: 'East Length' },
	{ key: 14, value: ESurroundingValues.SIDE1_SIDE_UNIT, text: 'East Side Unit' },
	{ key: 21, value: ESurroundingValues.SIDE2_SANDHIYAR, text: 'West Land Owner' },
	{ key: 22, value: ESurroundingValues.SIDE2_KITTA_NO, text: 'West Kitta No' },
	{ key: 23, value: ESurroundingValues.SIDE2_FEET, text: 'West Length' },
	{ key: 24, value: ESurroundingValues.SIDE2_SIDE_UNIT, text: 'West Side Unit' },
	{ key: 31, value: ESurroundingValues.SIDE3_SANDHIYAR, text: 'North Land Owner' },
	{ key: 32, value: ESurroundingValues.SIDE3_KITTA_NO, text: 'North Kitta No' },
	{ key: 33, value: ESurroundingValues.SIDE3_FEET, text: 'North Length' },
	{ key: 34, value: ESurroundingValues.SIDE3_SIDE_UNIT, text: 'North Side Unit' },
	{ key: 41, value: ESurroundingValues.SIDE4_SANDHIYAR, text: 'South Land Owner' },
	{ key: 42, value: ESurroundingValues.SIDE4_KITTA_NO, text: 'South Kitta No' },
	{ key: 43, value: ESurroundingValues.SIDE4_FEET, text: 'South Length' },
	{ key: 44, value: ESurroundingValues.SIDE4_SIDE_UNIT, text: 'South Side Unit' },
];

const samples = {
	
};
export const SurroundingValuesInputType = ({ values, vocabulary }: { values: any; vocabulary: TOption[] }) => {
	return (
		<Form.Group>
			<SelectInputV2 name={'meta.identifier'} label="Select Surrounding Value" options={surroundingValueOptions} />
			{
				//@ts-ignore
				samples[getIn(values, `meta.identifier`)] && (
					<Form.Field>
						<pre>
							Sample:{' '}
							{JSON.stringify(
								//@ts-ignore
								samples[getIn(values, 'meta.identifier')],
								null,
								2
							)}
						</pre>
					</Form.Field>
				)
			}
		</Form.Group>
	);
};
