import { FormikErrors } from 'formik';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Button, Modal, Progress } from 'semantic-ui-react';
import { TReduxState } from '../../../store/storeStateTypes';
import { postFileValidation, postFileWithProgress, deleteFile } from '../../../store/actions/fileActions';
import { IFileCategory } from '../../../interfaces/FileInterfaces';
import { saveButton } from '../../../utils/data/genericData';
import { FormUrlFull } from '../../../utils/enums/url';
import PermitPhotoUpload from '../../loggedInComponents/forms/mapPermitComponents/PermitPhotoUpload';
import MultiFileUpload from '../../loggedInComponents/forms/MultiFileUpload';
import { updateFileState, FileObject } from '../../../utils/fileUtils';
import { selectAppId } from '../../../store/selectors/currentFormSelectors';
import { getBackUrl } from '../../../utils/config';
import api from '../../../utils/api';
import { isEmpty } from '../../../utils/functionUtils';
import { selectNeedsFileView } from '../../../store/selectors/dynamicSelectors';
import { FormSaveFileUpload } from '../../shared/file/FormSaveFileUpload';

type TProps = {
	hasSavePermission: boolean;
	hasDeletePermission: boolean;
	isSaveDisabled: boolean;
	isEdit: boolean;
	handleSubmit: (e?: React.FormEvent<HTMLFormElement> | undefined) => void;
	errors: FormikErrors<any>;
	formName: string;
	fileCategories: IFileCategory[];
	validateForm: (values?: any) => Promise<FormikErrors<any>>;
	formUrl: string;
	fileLoading: boolean;
	applicationId: string | number;
	postFileValidation: () => void;
	postFileWithProgress: (url: string, data: any, fileCatName: string) => void;
	deleteFile: () => void;
	needsFile: boolean;
};
function SaveButton({
	hasSavePermission,
	hasDeletePermission,
	isSaveDisabled,
	isEdit,
	handleSubmit,
	errors,
	formName,
	fileCategories,
	validateForm,
	formUrl = '',
	fileLoading,
	applicationId,
	postFileValidation,
	postFileWithProgress,
	deleteFile,
	needsFile,
}: TProps) {
	const [open, setOpen] = useState(false);
	const [isFileUploading, setIsFileUploading] = useState(false);
	const [submitting, setSubmitting] = useState(false);
	const [files, setFiles] = useState<FileObject[]>([]);
	const handleClose = () => {
		setOpen(false);
	};

	const handleModalOpen = () => {
		setOpen(true);
	};

	const handleFileChange = (fileObject: FileObject) => {
		setFiles((prevState) => {
			return updateFileState(prevState, fileObject);
		});
	};

	const handleSubmitModal = () => {
		setSubmitting(true);
		const uploadFiles = async () => {
			await Promise.all(
				files.map(async (fileObj) => {
					const selectedFile = fileObj.files;
					const data = new FormData();
					if (selectedFile) {
						selectedFile.forEach((file) => data.append('file', file));

						data.append('fileType', String(fileObj.fileCatId));
						data.append('applicationNo', String(applicationId));

						try {
							return postFileWithProgress(`${getBackUrl()}${api.fileUpload}${applicationId}`, data, fileObj.fileCatName);
						} catch (err) {
							console.log('File upload failed', err);
							return [];
						}
					} else {
						console.log('Please select a file before uploading.');
						return [];
					}
				})
			);
			setFiles([]);
			setIsFileUploading(false);
			handleSubmit();
		};

		if (files.length > 0) {
			setIsFileUploading(false);
			uploadFiles();
		} else {
			handleSubmit();
		}
		setSubmitting(false);
	};

	return hasSavePermission ? (
		<div>
			<Modal key="save-confirmation" closeIcon open={open} onClose={handleClose}>
				<Modal.Header>{saveButton.uploadFileReminder}</Modal.Header>
				{isFileUploading ? (
					<Modal.Content>
						<div>{saveButton.fileUploading}</div>
						{fileLoading &&
							Object.entries(fileLoading).map(([category, progressObject]) => (
								<Progress percent={progressObject.percent} progress success indicating>
									{category}
								</Progress>
							))}
					</Modal.Content>
				) : (
					<Modal.Content scrolling>
						{formUrl === FormUrlFull.BUILD_PERMIT && <PermitPhotoUpload />}
						<MultiFileUpload
							url={formUrl}
							hasDeletePermission={hasDeletePermission}
							fileCategories={fileCategories}
							handleFileChange={handleFileChange}
						/>
						<FormSaveFileUpload handleFileChange={handleFileChange} hasDeletePermission={true} />
					</Modal.Content>
				)}
				<Modal.Actions>
					<Button disabled={isFileUploading || submitting} negative onClick={handleClose}>
						Exit
					</Button>
					<Button
						type="submit"
						positive
						icon="checkmark"
						loading={isFileUploading || submitting}
						disabled={isFileUploading || submitting}
						labelPosition="right"
						content={isEdit ? 'Update Form' : 'Save Form'}
						onClick={handleSubmitModal}
					/>
				</Modal.Actions>
			</Modal>
			<Button
				type="button"
				className="success-button"
				content={isEdit ? 'Update' : 'Save'}
				icon="save"
				disabled={isSaveDisabled}
				onClick={() => {
					if (!isEmpty(errors) && !(errors.hasOwnProperty('uploadFile') && Object.keys(errors).length === 1)) {
						//@ts-ignore
						const form = document.forms[formName || 0];
						for (let i = 0; i < form.length; i++) {
							if (
								Object.keys(errors).some((errorField) => {
									/**
									 * @todo doesn't work when a field name is a substring of other.
									 */
									return form[i].name.includes(errorField);
								})
							) {
								form[i].focus();
								break;
							}
						}
					} else {
						if (needsFile) {
							if (validateForm) {
								validateForm().then((errors) => {
									if (isEmpty(errors) || (errors.hasOwnProperty('uploadFile') && Object.keys(errors).length === 1)) {
										handleModalOpen();
									}
								});
							} else {
								handleModalOpen();
							}
						} else {
							handleSubmit();
						}
					}
				}}
			/>
		</div>
	) : null;
}

const mapStateToProps = (state: TReduxState) => ({
	fileCategories: state.root.dynamic.fileCategories,
	fileLoading: state.root.ui.fileLoading,
	success: state.root.formData.success,
	permitData: state.root.currentForm.prevData,
	applicationId: selectAppId(state),
	needsFile: selectNeedsFileView(state),
});

const mapDispatchToProps = {
	postFileValidation,
	postFileWithProgress,
	deleteFile,
};

export const DynamicFormSaveButton = connect(mapStateToProps, mapDispatchToProps)(SaveButton);
