import { Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { Button, Divider, Form } from 'semantic-ui-react';
import { TInputTypeObject } from './utils/types/setupTypes';
import * as Yup from 'yup';
import { EInputTypes, EValidationDataTypes } from './utils/enums/inputEnums';
import { NormalInput } from '../../shared/fields/NormalInput';
import { SelectInputV2 } from '../../shared/fields/SelectInputV2';
import { RadioInput } from './RadioInput';
import { TOption } from '../interfaces';
import { FloorTableDetailsInputType } from './FloorTableDetailsInputType';
import { SurroundingDetailsInputType } from './SurroundingDetailsInputType';
import { FloorValuesInputType } from './FloorValuesInputType';
import { SurroundingValuesInputType } from './SurroundingValuesInputType';

export const VocabularyInputs = [EInputTypes.DASHED_DROPDOWN, EInputTypes.DROPDOWN, EInputTypes.RADIO_INPUT, EInputTypes.CHECKBOX_INPUT];
export const inputSubTypeOptions = [
	{ key: 1, value: EInputTypes.NEPALI_INPUT, text: 'Nepali Input' },
	{ key: 2, value: EInputTypes.NEPALI_DATE, text: 'Unicode Nepali Date' },
	{ key: 21, value: EInputTypes.NORMAL_NEPALI_DATE, text: 'Normal Nepali Date' },
	{ key: 3, value: EInputTypes.DASHED_NORMAL_INPUT, text: 'Dashed Normal Input' },
	{ key: 4, value: EInputTypes.UNIT_DROPDOWN, text: 'Unit Dropdown' },
	{ key: 41, value: EInputTypes.DASHED_DROPDOWN, text: 'Dashed Dropdown' },
	{ key: 42, value: EInputTypes.DROPDOWN, text: 'Dropdown' },
	{ key: 43, value: EInputTypes.RADIO_INPUT, text: 'Radio Input' },
	{ key: 44, value: EInputTypes.CHECKBOX_INPUT, text: 'Checkbox Input' },
	{ key: 6, value: EInputTypes.TEXT_AREA, text: 'Text Area' },
	{ key: 7, value: EInputTypes.TIME_INPUT, text: 'Time' },
];

export const inputTypeOptions = [
	{ key: 1, value: EInputTypes.NEPALI_INPUT, text: 'Nepali Input' },
	{ key: 2, value: EInputTypes.NEPALI_DATE, text: 'Unicode Nepali Date' },
	{ key: 21, value: EInputTypes.NORMAL_NEPALI_DATE, text: 'Normal Nepali Date' },
	{ key: 3, value: EInputTypes.DASHED_NORMAL_INPUT, text: 'Dashed Normal Input' },
	{ key: 4, value: EInputTypes.UNIT_DROPDOWN, text: 'Unit Dropdown' },
	{ key: 41, value: EInputTypes.DASHED_DROPDOWN, text: 'Dashed Dropdown' },
	{ key: 42, value: EInputTypes.DROPDOWN, text: 'Dropdown' },
	{ key: 43, value: EInputTypes.RADIO_INPUT, text: 'Radio Input' },
	{ key: 44, value: EInputTypes.CHECKBOX_INPUT, text: 'Checkbox Input' },
	{ key: 5, value: EInputTypes.FLOOR_TABLE, text: 'Floor Table' },
	{ key: 52, value: EInputTypes.FLOOR_VALUES, text: 'Floor Values' },
	{ key: 51, value: EInputTypes.SURROUNDING_TABLE, text: 'Surrounding Table' },
	{ key: 53, value: EInputTypes.SURROUNDING_VALUES, text: 'Surrounding Values' },
	{ key: 6, value: EInputTypes.TEXT_AREA, text: 'Text Area' },
	{ key: 7, value: EInputTypes.TIME_INPUT, text: 'Time' },
];

const dataTypeOptions = [
	{ key: 1, value: EValidationDataTypes.NUMBER, text: 'Number' },
	{ key: 2, value: EValidationDataTypes.STRING, text: 'String' },
	{ key: 3, value: EValidationDataTypes.NORMAL_DATE, text: 'Normal Date' },
	{ key: 4, value: EValidationDataTypes.UNICODE_DATE, text: 'Unicode Date' },
	{ key: 5, value: EValidationDataTypes.TIME_INPUT, text: 'Time' },
];

const initialValues = {
	id: 0,
	name: '',
	label: '',
	columnName: '',
	reference: 'text',
	type: EInputTypes.NEPALI_INPUT,
};

const schema = Yup.object().shape({
	name: Yup.string()
		// .matches(/^[a-z][\w\d]+$/, 'Should only contain more than 1 of a-z, A-Z or 0-9 and start with letter')
		.required('This field is required'),
	type: Yup.string().required('This field is required'),
	//   label: Yup.string().required("This field is required"),
});

export const AddInput = ({
	setInputTypes,
	onClose,
	isEditing = false,
	editData,
	vocabulary = [],
}: {
	setInputTypes: React.Dispatch<React.SetStateAction<TInputTypeObject[]>>;
	onClose: () => void;
	isEditing?: boolean;
	editData?: TInputTypeObject | null;
	vocabulary: TOption[];
}) => {
	const [typeOptions, setTypeOptions] = useState(inputTypeOptions);
	useEffect(() => {
		if (editData) {
			if (editData.vocabularyId) {
				setTypeOptions(inputTypeOptions.filter((row) => VocabularyInputs.includes(row.value)));
			} else {
				setTypeOptions(inputTypeOptions.filter((row) => !VocabularyInputs.includes(row.value)));
			}
		}
	}, [editData]);

	return (
		<div>
			<Formik
				validationSchema={schema}
				initialValues={isEditing && editData ? editData : initialValues}
				onSubmit={(values, actions) => {
					actions.setSubmitting(true);
					if (isEditing) {
						setInputTypes((inputTypes) => {
							const filtered = inputTypes.filter((type) => editData && type.name !== editData.name);
							return filtered.concat(values);
						});
					} else {
						setInputTypes((inputTypes) => inputTypes.concat(values));
					}
					actions.setSubmitting(false);
					onClose();
				}}
			>
				{({ values, handleSubmit, isSubmitting }) => (
					<Form loading={isSubmitting}>
						<Form.Group>
							<NormalInput name="name" label="Field Name" disabled />
							<NormalInput name="reference" label="Field Reference" disabled />
							{VocabularyInputs.includes(values.type) ? (
								<SelectInputV2 disabled label="Vocabulary" options={vocabulary} name="vocabularyId" />
							) : null}
						</Form.Group>
						<Form.Group>
							<NormalInput name="label" label="Field Label" />
							<SelectInputV2 label="Field Type" options={typeOptions} name="type" />
						</Form.Group>
						{values.type === EInputTypes.FLOOR_TABLE ? <FloorTableDetailsInputType values={values} vocabulary={vocabulary} /> : null}
						{values.type === EInputTypes.SURROUNDING_TABLE ? <SurroundingDetailsInputType values={values} vocabulary={vocabulary} /> : null}
						{values.type === EInputTypes.FLOOR_VALUES ? <FloorValuesInputType values={values} vocabulary={vocabulary} /> : null}
						{values.type === EInputTypes.SURROUNDING_VALUES ? <SurroundingValuesInputType values={values} vocabulary={vocabulary} /> : null}
						{values.type === EInputTypes.DROPDOWN ? <Form.Group> <NormalInput name={'meta.width'} label="Enter width" /> </Form.Group> : null}
						<Divider horizontal>Validation</Divider>
						<Form.Group>
							<Form.Field>
								<RadioInput name="meta.validation.required" label="Required" />
							</Form.Field>
							<Form.Field>
								Data Type:
								<SelectInputV2 label="" options={dataTypeOptions} name="meta.validation.dataType" />
							</Form.Field>
						</Form.Group>
						<Button
							content="Save Form Input"
							size="tiny"
							compact
							icon="save"
							className="success-button"
							type="submit"
							onClick={() => handleSubmit()}
						/>
					</Form>
				)}
			</Formik>
		</div>
	);
};
