import { getIn } from 'formik';
import React from 'react';
import { Divider, Grid } from 'semantic-ui-react';
import { NormalInput } from '../../shared/fields/NormalInput';
import { SelectInputV2 } from '../../shared/fields/SelectInputV2';
import { TOption } from '../interfaces';
import { inputSubTypeOptions, VocabularyInputs } from './AddInput';

const columns = [
	{
		label: 'Column 1',
		name: 'column1',
	},
	{
		label: 'Column 2',
		name: 'column2',
	},
	{
		label: 'Column 3',
		name: 'column3',
	},
	{
		label: 'Column 4',
		name: 'column4',
	},
];
const columnTypeOptions = [
	{ key: 1, value: 'length', text: 'Length' },
	{ key: 2, value: 'width', text: 'Width' },
	{ key: 3, value: 'height', text: 'Height' },
	{ key: 4, value: 'area', text: 'Area' },
	{ key: 5, value: 'other', text: 'Other' },
];

export const FloorTableDetailsInputType = ({ values, vocabulary }: { values: any; vocabulary: TOption[] }) => {
	return (
		<div>
			<Divider horizontal>Floor Table Details</Divider>
			<Grid
				divided
				//@ts-ignore
				columns={columns.length}
			>
				<Grid.Row>
					{columns.map((row) => (
						<Grid.Column key={row.name}>
							<SelectInputV2 name={`meta.header.${row.name}.type`} label={row.label} options={columnTypeOptions} />
						</Grid.Column>
					))}
				</Grid.Row>
				<Grid.Row>
					{columns.map((row) => (
						<Grid.Column key={row.name}>
							<NormalInput size="small" name={`meta.header.${row.name}.label`} label={`${row.label} Label`} />
						</Grid.Column>
					))}
				</Grid.Row>
				<Grid.Row>
					{columns.map((row) => (
						<React.Fragment key={row.name}>
							{getIn(values, `meta.header.${row.name}.type`) === 'other' ? (
								<NormalInput size="small" name={`meta.header.${row.name}.field`} label={`${row.label} Field`} />
							) : null}
						</React.Fragment>
					))}
				</Grid.Row>
				<Grid.Row>
					{columns.map((row) => (
						<Grid.Column key={row.name}>
							{getIn(values, `meta.header.${row.name}.type`) === 'other' ? (
								<SelectInputV2 label="Field Type" options={inputSubTypeOptions} name={`meta.header.${row.name}.subType.type`} />
							) : null}
						</Grid.Column>
					))}
				</Grid.Row>
				<Grid.Row>
					{columns.map((row) => (
						<Grid.Column key={row.name}>
							{getIn(values, `meta.header.${row.name}.type`) === 'other' &&
							VocabularyInputs.includes(getIn(values, `meta.header.${row.name}.subType.type`)) ? (
								<SelectInputV2 label="Vocabulary" options={vocabulary} name={`meta.header.${row.name}.subType.vocabularyId`} />
							) : null}
						</Grid.Column>
					))}
				</Grid.Row>
			</Grid>
		</div>
	);
};
