import React, { useEffect, useState } from 'react';
import { ImageDisplayInline } from '../../../shared/file/FileView';
import { TOutputFormat } from '../utils/types/setupTypes';

type TProps = {
	staticFiles?: any;
	row: TOutputFormat;
};

export const RenderStaticImage = ({ staticFiles, row }: TProps) => {
	const [image, setImage] = useState('');
	useEffect(() => {
		if (staticFiles) {
			//@ts-ignore
			setImage(staticFiles[row.content] || '');
		}
	}, [staticFiles, row.content]);

	return <ImageDisplayInline label="" src={image} height={60} alt={row.content} />;
};
