import React, { useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Button, Modal, ModalActions, ModalContent, ModalHeader, Select } from 'semantic-ui-react';
import { TEMPLATE_DELIMETER } from './utils/constants';
import { ELayoutIdentifier } from './utils/enums/LayoutIdentifiers';

/**
 * @todo fetch these dynamically
 */
const staticFileOptions = [
	{ key: 10, text: 'घरधनीको दस्तखत्', value: 'ghardhaniSignature' },
	{ key: 11, text: 'architechSignature', value: 'architechSignature' },
	{ key: 12, text: 'architechStamp', value: 'architechStamp' },
	{ key: 13, text: 'engineerSignature', value: 'engineerSignature' },
	{ key: 14, text: 'engineerStamp', value: 'engineerStamp' },
	{ key: 15, text: 'मुख्य डकर्मीको दस्तखत्', value: 'dakarmiSignature' },
	{ key: 16, text: 'निर्माण व्यवसायको दस्तखत्', value: 'nirmanByabasayeSignature' },
];

export const AddStaticImage = () => {
	const [open, setOpen] = useState(false);
	const [info, setInfo] = useState<string>('ghardhaniSignature');

	const handleClose = () => {
		setOpen(false);
	};

	const handleOpen = () => {
		setOpen(true);
	};

	return (
		<>
			<Button content="Add Static Image" size="tiny" compact onClick={handleOpen} className="primary-btn" icon="image" />
			<Modal open={open} onClose={handleClose} size="tiny" closeIcon>
				<ModalHeader>Add Static Image</ModalHeader>
				<ModalContent>
					Select Static File: <Select options={staticFileOptions} value={info} onChange={(e, { value }) => setInfo(String(value))} />
				</ModalContent>
				<ModalActions>
					<Button negative onClick={handleClose}>
						Cancel
					</Button>
					<CopyToClipboard text={`[[${ELayoutIdentifier.STATIC_IMAGE}${TEMPLATE_DELIMETER}${info}]]`} onCopy={handleClose}>
						<Button positive icon="copy" type="button" content="Copy" />
					</CopyToClipboard>
				</ModalActions>
			</Modal>
		</>
	);
};
