import { useReducer } from 'react'

type TState = {
    id?: number;
    name: string;
    readOnly: 'Y' | 'N';
    placeholder: string;
    required: 'Y' | 'N';
    formFieldModal: boolean;
    formFieldDeleteModal: boolean;
    formId?: number;
    ebpsColumnId?: number;
    referencedColumnId?: number;
    referencedTableId?: number;
    vocabularyId?: number | null;
    isEditing: boolean;
}

const initialState: TState = {
    name: '',
    readOnly: 'N',
    placeholder: '',
    required: 'Y',
    formFieldModal: false,
    formFieldDeleteModal: false,
    vocabularyId: null,
    isEditing: false,
}

export enum FormFieldActions {
    NAME = 'NAME',
    PLACEHOLDER = 'PLACEHOLDER',
    READ_ONLY = 'READ_ONLY',
    REQUIRED = 'REQUIRED',
    MODAL = 'MODAL',
    DELETE_MODAL = 'DELETE_MODAL',
    EBPS_COLUMN_ID = 'EBPS_COLUMN_ID',
    REFERENCE_COLUMN = 'REFERENCE_COLUMN',
    REFERENCE_TABLE = 'REFERENCE_TABLE',
    VOCABULARY_ID = 'VOCABULARY_ID',
    EDIT = 'EDIT',
    RESET = 'RESET',
}

function reducer(state: TState, action: { type: FormFieldActions, data?: any }) {
    const { type, data } = action;
    switch (type) {
        case FormFieldActions.NAME:
            return {
                ...state, name: data
            }
        case FormFieldActions.PLACEHOLDER:
            return {
                ...state, placeholder: data
            }
        case FormFieldActions.READ_ONLY:
            return {
                ...state, readOnly: data
            }
        case FormFieldActions.REQUIRED:
            return {
                ...state, required: data
            }
        case FormFieldActions.EBPS_COLUMN_ID:
            return { ...state, ebpsColumnId: data };
        case FormFieldActions.REFERENCE_COLUMN:
            return { ...state, referencedColumnId: data }
        case FormFieldActions.REFERENCE_TABLE:
            return { ...state, referencedTableId: data }
        case FormFieldActions.VOCABULARY_ID:
            return { ...state, vocabularyId: data }
        case FormFieldActions.MODAL:
            return { ...state, formFieldModal: data }
        case FormFieldActions.DELETE_MODAL:
            return { ...state, formFieldDeleteModal: data }
        case FormFieldActions.EDIT:
            return { ...data, isEditing: true }
        case FormFieldActions.RESET:
            return { ...initialState }
        default:
            return state;
    }

}

export const useTableFormField = () => {
    const [state, dispatch] = useReducer(reducer, initialState)

    return {
        formFieldState: {
            name: state.name,
            placeholder: state.placeholder,
            readOnly: state.readOnly,
            required: state.required,
            ebpsColumnId: state.ebpsColumnId,
            referencedColumnId: state.referencedColumnId,
            referencedTableId: state.referencedTableId,
            vocabularyId: state.vocabularyId,
            id: state.id
        },
        isFormFieldEdit: state.isEditing,
        formFieldModal: state.formFieldModal,
        formFieldDeleteModal: state.formFieldDeleteModal,
        formFieldDispatch: dispatch,
        toggleFormFieldModal: (data: boolean) => dispatch({ type: FormFieldActions.MODAL, data }),
        toggleFormFieldDeleteModal: (data: boolean) => dispatch({ type: FormFieldActions.DELETE_MODAL, data }),
        closeFormFieldModal: () => {
            dispatch({ type: FormFieldActions.RESET });
        },
        startEdit: (rowData: any) => {
            dispatch({ type: FormFieldActions.EDIT, data: rowData });
            dispatch({ type: FormFieldActions.MODAL, data: true });
        },
        startDelete: (rowData: any) => {
            dispatch({ type: FormFieldActions.EDIT, data: rowData });
            dispatch({ type: FormFieldActions.DELETE_MODAL, data: true });
        }
    }
}
