import React, { useEffect, useRef, useState } from 'react';
import { Form, Grid, Ref, Segment, Sticky } from 'semantic-ui-react';
import AdminContainer from '../../containers/base/AdminContainer';
import { IAdminContainerProps } from '../../containers/base/containerInterfaces';
import api from '../../utils/api';
import FallbackComponent from '../shared/FallbackComponent';
import { showToast } from '../shared/viewToast';
import { FlexSpaceBetween } from '../uiComponents/FlexDivs';
import { SectionHeader } from '../uiComponents/Headers';
import { IEBPSColumns, IEBPSTableColumns, IFormFieldList, IFormNameMaster, IVocabulary, TOption } from './interfaces';
import { AddComponents } from './layoutSetupComponents/AddComponents';
import { AddStaticImage } from './layoutSetupComponents/AddStaticImage';
import { AddUserInfo } from './layoutSetupComponents/AddUserInfo';
import { DataPoints } from './layoutSetupComponents/DataPoints';
import { FormEditor } from './layoutSetupComponents/FormEditor';
import { InputTypes } from './layoutSetupComponents/InputTypes';
import { Preview } from './layoutSetupComponents/Preview';
import { EReadOnlyTypes } from './layoutSetupComponents/utils/enums/LayoutIdentifiers';
import { TInputTypeObject, TOutputFormat, TReadOnlyTypeObject } from './layoutSetupComponents/utils/types/setupTypes';
import { BackButton } from './shared/BackButton';
import { SaveChangesButton } from './shared/SaveChangesButton';

export const LayoutSetupComponent = ({
	match,
	adminData,
	postAdminDataByUrl,
	success,
	loading,
}: IAdminContainerProps<{ formNameMaster: IFormNameMaster[]; tables: IEBPSTableColumns[]; vocabulary: IVocabulary[] }>) => {
	const [output, setOutput] = useState<TOutputFormat[]>([]);
	const [data, setData] = useState();
	const [cFormName, setCFormName] = useState<IFormNameMaster>();
	const [vocabularyOptions, setVocabularyOptions] = useState<TOption[]>([]);
	const [readOnlyFields, setReadOnlyFields] = useState<(IFormFieldList & { details: IEBPSColumns })[]>([]);
	const [tables, setTables] = useState<IEBPSTableColumns[]>([]);
	const [inputFields, setInputFields] = useState<(IFormFieldList & { details: IEBPSColumns })[]>([]);
	const [inputTypes, setInputTypes] = useState<TInputTypeObject[]>([
		// {
		// 	id: 1,
		// 	name: 'floor',
		// 	reference: 'text',
		// 	columnN
		// 	label: 'LLS',
		// 	type: 'floor-table',
		// 	meta: {
		// 		header: {
		// 			length: 'लम्बाई',
		// 			width: 'चौडाई',
		// 			height: 'उचाई',
		// 			area: 'क्षेत्रफल',
		// 		},
		// 	},
		// },
		// {
		// 	name: 'testVali',
		// 	reference: 'text',
		// 	id: 2,
		// 	label: 'd',
		// 	type: 'input',
		// 	meta: { header: {}, validation: { dataType: 'number' } },
		// },
	]);

	const [readOnlyTypes, setReadOnlyTypes] = useState<TReadOnlyTypeObject[]>([]);

	useEffect(() => {
		const formMaster =
			adminData.formNameMaster && adminData.formNameMaster.find((row: IFormNameMaster) => String(row.id) === String(match.params.id));
		if (formMaster) {
			// const readOnlies = formMaster.formFieldsList.filter((row) => row.readOnly === 'Y');
			// const inputFields = formMaster.formFieldsList.filter((row) => row.readOnly === 'N');
			// setReadOnlyFields(readOnlies);
			// setInputFields(inputFields);
			setCFormName(formMaster);
		}
	}, [adminData.formNameMaster]);

	useEffect(() => {
		if (adminData.tables) {
			setTables(adminData.tables);
		}
	}, [adminData.tables]);

	useEffect(() => {
		if (adminData.vocabulary) {
			const options = adminData.vocabulary.map((row) => ({ key: row.id, value: row.id, text: row.name }));
			setVocabularyOptions(options);
		}
	}, [adminData.vocabulary]);

	useEffect(() => {
		if (success && success.success) {
			showToast('Form Layout Saved.');
		}
	}, [success]);

	useEffect(() => {
		const formMaster = adminData.formNameMaster.find((row: IFormNameMaster) => String(row.id) === String(match.params.id));
		if (formMaster && adminData.tables.length > 0) {
			const ebpsColumns = tables.flatMap((row) => row.ebpsColumns);

			const inputFieldsAll = formMaster.formFieldsList
				// .filter((row) => row.readOnly === 'N')
				.map((row) => {
					let detailsRow;
					detailsRow = ebpsColumns.find((col) => {
						return col.id === row.ebpsColumnId;
					});

					if (!detailsRow) {
						detailsRow = ebpsColumns.find((col) => {
							return col.id === row.referencedColumnId;
						});
					}
					return {
						...row,
						details: detailsRow,
					};
				});

			const inputFieldsReadOnly = inputFieldsAll.filter((row) => row.readOnly === 'Y');
			const inputFields1 = inputFieldsAll.filter((row) => row.readOnly === 'N');
			setReadOnlyFields(inputFieldsReadOnly);
			setInputFields(inputFields1);

			let prevInputTypes: TInputTypeObject[] = [];
			let prevReadOnlyTypes: TReadOnlyTypeObject[] = [];
			try {
				const { layout, inputTypes: prevInput, rawData, readOnlyTypes: previousReadOnlyTypes } = JSON.parse(formMaster.formLayout);
				prevInputTypes = prevInput;
				prevReadOnlyTypes = previousReadOnlyTypes;
				setOutput(layout);
				setData(rawData);
			} catch (error) {
				console.log(error);
			}

			//@ts-ignore
			const inputTypes: TInputTypeObject[] =
				// prevInputTypes ||
				inputFields1.map((row) => {
					if (row) {
						const existingRow = prevInputTypes && prevInputTypes.find((prevRow) => prevRow.id === row.id);
						const type = row && row.details && row.details.reference === 'text' ? 'input' : 'number';
						const columnName = row.details && row.details.columnName;

						const validation =
							row && row.details && row.required === 'Y'
								? { dataType: 'string', required: true }
								: { dataType: 'string', required: false };
						const rowToMap = {
							id: row.id || 0,
							name: row.name,
							label: '',
							reference: row.details && row.details.reference,
							type,
							columnName,
							meta: { header: {}, validation },
							vocabularyId: row.vocabularyId,
						};
						if (existingRow) {
							return {
								...existingRow,
								/**
								 * These values are not changed from {@link AddInput} but may change from {@link FormPicker}
								 * so we do this
								 */
								columnName,
								name: rowToMap.name,
								reference: rowToMap.reference,
								vocabularyId: rowToMap.vocabularyId,
							};
						} else {
							return rowToMap;
						}
					} else {
						return {};
					}
				});

			//@ts-ignore
			const readOnlyTypes: TReadOnlyTypeObject[] = inputFieldsReadOnly
				.map((row) => {
					// Only for rows with ebpsColumn i.e. the rows that need to be saved.
					if (row && row.ebpsColumnId) {
						const existingRow = prevReadOnlyTypes && prevReadOnlyTypes.find((prevRow) => prevRow.id === row.id);
						const rowToMap = {
							id: row.id || 0,
							type: EReadOnlyTypes.DATA,
							name: row.name,
							meta: row.details && row.details.columnName,
							columnName: row.details && row.details.columnName,
						};
						if (existingRow) {
							return { ...existingRow, name: rowToMap.name, columnName: rowToMap.columnName };
						} else {
							return rowToMap;
						}
					}
				})
				.filter((row) => row);
			setReadOnlyTypes(readOnlyTypes);
			setInputTypes(inputTypes);
		}
	}, [cFormName, tables]);

	// const formattedInputFields = useMemo(() => {
	// 	if (cFormName && adminData.tables) {
	// 		const ebpsColumns = tables.flatMap((row) => row.ebpsColumns);

	// 		const inputFields = cFormName.formFieldsList
	// 			.filter((row) => row.readOnly === 'N')
	// 			.map((row) => ({ ...row, details: ebpsColumns.find((col) => col.id === row.ebpsColumnId) }));
	// 		return inputFields;
	// 	}
	// }, [cFormName, tables]);

	const handleSave = () => {
		window.scroll(0, 0);
		postAdminDataByUrl(api.d_formNameMaster, {
			...cFormName,
			formLayout: JSON.stringify({ layout: output, inputTypes: inputTypes, rawData: data, readOnlyTypes: readOnlyTypes }),
		});
	};

	// console.log(formattedInputFields);
	const contextRef = useRef();

	return (
		<div>
			<Ref innerRef={contextRef}>
				<div>
					<Sticky
						context={contextRef}
						offset={50}
						style={{ marginTop: '-20px', marginBottom: '20px', marginLeft: '-20px', marginRight: '-20px' }}
					>
						<Segment style={{ marginTop: '-10px', marginBottom: '10px', padding: '0.75em' }}>
							<FlexSpaceBetween>
								<div>
									<SectionHeader>
										<h4>Layout Setup - {cFormName && cFormName.name}</h4>
									</SectionHeader>
								</div>
								<div>
									<BackButton compact url={`/admin/form-name-details/${cFormName && cFormName.id}`} />
									<SaveChangesButton compact onClick={() => handleSave()} />
								</div>
							</FlexSpaceBetween>
						</Segment>
					</Sticky>
					<Form loading={loading}>
						{cFormName ? (
							<Grid>
								<Grid.Row>
									<Grid.Column width="12">
										<FormEditor initialData={data} setData={setData} />
									</Grid.Column>
									<Grid.Column width="4">
										<Grid>
											<Grid.Row>
												<Grid.Column width="16">
													<DataPoints
														dataMapping={readOnlyFields}
														setReadOnlyTypes={setReadOnlyTypes}
														readOnlyTypes={readOnlyTypes}
													/>
												</Grid.Column>
											</Grid.Row>
											<Grid.Row>
												<Grid.Column width="5" style={{ padding: 0 }}>
													<AddUserInfo />
												</Grid.Column>
												<Grid.Column width="5" style={{ padding: 0 }}>
													<AddStaticImage />
												</Grid.Column>
												<Grid.Column width="5" style={{ padding: 0 }}>
													<AddComponents />
												</Grid.Column>
											</Grid.Row>
											<Grid.Row>
												<Grid.Column width="16">
													<InputTypes
														inputFields={inputFields}
														inputTypes={inputTypes}
														setInputTypes={setInputTypes}
														vocabulary={vocabularyOptions}
													/>
												</Grid.Column>
											</Grid.Row>
										</Grid>
									</Grid.Column>
								</Grid.Row>
							</Grid>
						) : (
							<FallbackComponent loading={true} />
						)}
					</Form>
					<Preview
						vocabulary={adminData.vocabulary}
						output={output}
						setOutput={setOutput}
						inputTypes={inputTypes}
						// formattedData={formattedData || ""}
						data={data || ''}
						dataMapping={readOnlyFields}
					/>
				</div>
			</Ref>
		</div>
	);
};

export const LayoutSetup = (parentProps: any) => (
	//@ts-ignore
	<AdminContainer
		//@ts-ignore
		api={[
			{ api: api.d_formNameMaster, objName: 'formNameMaster' },
			{ api: api.d_ebpsTables, objName: 'tables' },
			{ api: api.d_vocabulary, objName: 'vocabulary' },
		]}
		//@ts-ignore
		updateApi={[{ api: api.d_formNameMaster, objName: 'formNameMaster' }]}
		//@ts-ignore
		render={(props: any) => <LayoutSetupComponent {...props} parentProps={parentProps} />}
	/>
);
