import { ActionDispatch } from "../../interfaces/ReduxInterfaces"
import { SET_PREV_DATA, SET_APPLICATION_ID, SET_CURRENT_IDS } from "../types"

export const setPrevData = (prevData: any) => (dispatch: ActionDispatch) => {
    dispatch({
        type: SET_PREV_DATA,
        payload: { prevData }
    })
}

export const setApplicationId = (applicationId: number) => (dispatch: ActionDispatch) => {
    dispatch({
        type: SET_APPLICATION_ID,
        payload: { applicationId }
    })
}

export const setCurrentIds = (applicationId: number | string, formId: number | string) => (dispatch: ActionDispatch) => {
    dispatch({
        type: SET_CURRENT_IDS,
        payload: { applicationId, formId }
    })
}
