import { IEBPSTableColumns, IFormNameMaster, IVocabulary, TFormStatus } from "../components/dynamic-setup/interfaces"
import { IFile, IFileCategory } from "../interfaces/FileInterfaces"

export type TReduxState = {
    root: {
        ui: TUi;
        formData: TFormData;
        currentForm: TCurrentForm;
        dynamic: TDynamicState;
        fileState: TFileState;
    }
}

export type TFileState = {
    success?: TSuccess;
}

type TFormData = {
    success?: TSuccess;
    rejectedStatus?: string;
}

export type TSuccess = {
    success: boolean;
    data: any;
}

type TCurrentForm = {
    prevData: any;
    applicationId: number | string;
    formId: number | string;
}

type TUi = {
    loading: boolean;
    errors: any;
    isNavOpen: boolean;
    fileLoading: boolean;
    loadingModal: boolean;
}

export type TDynamicState = {
    success: TDynamicSuccess;
    files: IFile[];
    fileCategories: IFileCategory[];
    staticFiles: any;
    cFormName: IFormNameMaster;
    generalData: any;
    vocabulary: IVocabulary[];
    tables: IEBPSTableColumns[];
    formData: any;
    status: TFormStatus;
    referencedData: any;
}

export type TDynamicSuccess = {
    success: boolean;
    data: any;
    module: EApproveRejectModule
}

export enum EApproveRejectModule {
    APPROVE = 'approve',
    REJECT = 'reject'
} 