import { ESurroundingValues } from "../components/dynamic-setup/layoutSetupComponents/utils/enums/inputEnums";

export class SurroundingArray {
	constructor(surrounding) {
		this.surrounding = surrounding || [];
	}

	// Getters
	getSurrounding() {
		return this.surrounding;
	}

	getAllSurrounding() {
		const completeObject = [1, 2, 3, 4].map(side => {
			const surr = this.surrounding.find(s => s.side === side);
			if (surr) {
				return surr;
			} else {
				return { side };
			}
		});
		return completeObject;
	}
}

export class Surrounding {
	constructor(side, kittaNo, sandhiyar, feet, sideUnit) {
		this.side = side;
		this.kittaNo = kittaNo;
		this.sandhiyar = sandhiyar;
		this.feet = feet;
		this.sideUnit = sideUnit;
	}

	getSide() {
		return this.side;
	}

	getSurroundingObject() {
		return {
			side: this.side,
			kittaNo: this.kittaNo,
			sandhiyar: this.sandhiyar,
			feet: this.feet,
			sideUnit: this.sideUnit,
		};
	}
}

export class SurroundingBlockArray{
	constructor(surrounding) {
		this.surrounding = surrounding || [];
	}
	
	getSurroundingValueWiseValue(identifier) {
		switch (identifier) {
			case ESurroundingValues.SIDE1_SANDHIYAR:
				return this.getData('sandhiyar', 1);
			case ESurroundingValues.SIDE1_KITTA_NO:
				return this.getData('kittaNo', 1);
			case ESurroundingValues.SIDE1_FEET:
				return this.getData('feet', 1);
			case ESurroundingValues.SIDE1_SIDE_UNIT:
				return this.getData('sideUnit', 1);
			case ESurroundingValues.SIDE2_SANDHIYAR:
				return this.getData('sandhiyar', 2);
			case ESurroundingValues.SIDE2_KITTA_NO:
				return this.getData('kittaNo', 2);
			case ESurroundingValues.SIDE2_FEET:
				return this.getData('feet', 2);
			case ESurroundingValues.SIDE2_SIDE_UNIT:
				return this.getData('sideUnit', 2);
			case ESurroundingValues.SIDE3_SANDHIYAR:
				return this.getData('sandhiyar', 3);
			case ESurroundingValues.SIDE3_KITTA_NO:
				return this.getData('kittaNo', 3);
			case ESurroundingValues.SIDE3_FEET:
				return this.getData('feet', 3);
			case ESurroundingValues.SIDE3_SIDE_UNIT:
				return this.getData('sideUnit', 3);
			case ESurroundingValues.SIDE4_SANDHIYAR:
				return this.getData('sandhiyar', 4);
			case ESurroundingValues.SIDE4_KITTA_NO:
				return this.getData('kittaNo', 4);
			case ESurroundingValues.SIDE4_FEET:
				return this.getData('feet', 4);
			case ESurroundingValues.SIDE4_SIDE_UNIT:
				return this.getData('sideUnit', 4);
			default:
				return '';
		}
	}

	getData = (val, side) => {
		let ret = '';
		ret = this.surrounding.find(el => {
			return el.side === side
		});
		return ret[val] || '';
	}
}

