import React, { RefObject } from 'react';
import { connect } from 'react-redux';
import { getPermitAndUserData, getFormContainerData, postFormDataWithFileByUrl } from '../../store/actions/formActions';
import { getAdminDataByUrl } from '../../store/actions/AdminAction';
import { getDynamicFormContainerData, postFormDataByUrl } from '../../store/actions/dynamicFormActions';
import { setPrevData, setCurrentIds } from '../../store/actions/currentFormActions';
import { TDynamicState } from '../../store/storeStateTypes';
import { setPrint } from '../../store/actions/general';
import FallbackComponent from '../../components/shared/FallbackComponent';
import { isEmpty, showWarnToast } from '../../utils/functionUtils';
import { getStaticFiles } from '../../utils/formUtils';
import Helmet from 'react-helmet';
import { dynamicFormErrors } from '../../utils/data/genericData';
import api from '../../utils/api';
import { IEBPSColumns, IFormNameMaster } from '../../components/dynamic-setup/interfaces';
import { DynamicActionBar } from '../../components/dynamic-setup/dynamicFormComponents/DynamicActionBar';
import { DynamicFormUtility } from '../../components/dynamic-setup/dynamicFormComponents/utils/dynamicFormUtils';
import { TDynamicFormProps } from '../../components/dynamic-setup/dynamicFormComponents/DynamicForm';
import { CompactError } from '../../components/shared/ErrorDisplay';
import { IFile, IFileCategory } from '../../interfaces/FileInterfaces';
import { TInputTypeObject, TReadOnlyTypeObject } from '../../components/dynamic-setup/layoutSetupComponents/utils/types/setupTypes';
import { IPermitData, IUserInfo } from '../../interfaces/ApiInterfaces';
import { EReadOnlyTypes } from '../../components/dynamic-setup/layoutSetupComponents/utils/enums/LayoutIdentifiers';
import { EInputTypes } from '../../components/dynamic-setup/layoutSetupComponents/utils/enums/inputEnums';
import { FloorBlockArray } from '../../utils/floorUtils';
import { SurroundingBlockArray } from '../../utils/surroundingUtils';
import { StatusDataArray } from '../../utils/statusValueUtils';
import { permitErrors } from '../../utils/data/errorMessages';

type TApiObject = { api: string; objName: string };
type TReduxState = {
	root: {
		admin: {
			formNameMaster: IFormNameMaster[];
		};
		formData: {
			permitData: {};
			userData: {};
			comment: [];
			history: [];
			success: {};
			printcontent: any;
			fileCategories: any;
			files: any;
		};
		ui: {
			loading: boolean;
			errors: {};
		};
		general: {
			printcontent: any;
		};
		dynamic: any;
	};
};
type TProps = {
	// parentProps: { location: { pathname: string } };
	api: TApiObject[];
	// checkPreviousFiles?: boolean;
	// fetchFiles?: boolean;
	// localData?: {};
	// history: { push: (url: string) => void };
	// // Actions
	// getFormContainerData: (api: TApiObject[], prepareData: (data: any) => {}, formUtility: FormUtility, localData: {}, fetchFile: boolean) => void;
	// Redux state
	permitData: IPermitData;
	userData: IUserInfo;
	formData: {};
	comment: [];
	formHistory: [];
	adminData: {
		formNameMaster: IFormNameMaster[];
	};
	loading: boolean;
	errors: {};
	success: {};
	printcontent: any;
	fileCategories: IFileCategory[];
	files: IFile[];
	//Actions
	getPermitAndUserData: any;
	getDynamicFormContainerData: any;
	setPrevData: (prevData: any) => void;
	setCurrentIds: (applicationId: number | string, formId: number | string) => void;
	getFormContainerData: any;
	getAdminDataByUrl: any;
	postFormDataByUrl: (url: string, data: any, appId: string, formId: string, fetchMenu: boolean) => void;
	postFormDataWithFileByUrl: any;
	setPrint: (val: boolean) => void;
	//Redux State
	dynamic: TDynamicState;
	// From Render Props Section
	parentProps: TDynamicFormProps;
	onBeforeGetContent?: { [key: string]: string[] };
	render: ({}: any) => Element;
};

type TState = {
	allLoaded: boolean;
	cFormMaster: IFormNameMaster | null;
	formUtility: DynamicFormUtility | null;
	applicationNo: string | null;
	formId: string | null;
	isFormSetupValid: boolean;
	isEdit: boolean;
	prevData: any;
	readOnlyData: any;
	appIdIndex: number;
};

const initialState = {
	allLoaded: false,
	isFormSetupValid: false,
	isEdit: false,
	applicationNo: '',
	formId: '',
	cFormMaster: null,
	formUtility: null,
	prevData: null,
	readOnlyData: null,
	appIdIndex: 0,
};

class DynamicFormContaner extends React.Component<TProps, TState> {
	formRef: RefObject<HTMLDivElement> = React.createRef();
	constructor(props: TProps) {
		super(props);
		this.state = initialState;
	}

	componentDidMount() {
		const {
			parentProps: {
				match: {
					params: { formId },
				},
			},
			api,
		} = this.props;
		// this.props.getAdminDataByUrl(api);
		this.props.getDynamicFormContainerData(api, null, false, formId);
	}

	componentDidUpdate(prevProps: TProps) {
		const {
			permitData,
			userData,
			dynamic,
			parentProps: {
				match: {
					params: { applicationNo, formId },
				},
			},
			api,
		} = this.props;

		if (formId !== prevProps.parentProps.match.params.formId) {
			// this.props.getAdminDataByUrl(api);
			this.setState({ ...initialState }, () => {
				this.props.getDynamicFormContainerData(api, null, false, formId);
			});
		}

		if (applicationNo !== prevProps.parentProps.match.params.applicationNo) {
			showWarnToast(permitErrors.selectFromTaskList);
			this.props.parentProps.history.push('/');
		}

		if (dynamic !== prevProps.dynamic) {
			const { cFormName, status } = dynamic;
			if (cFormName && cFormName.formLayout && cFormName.formPermissions && cFormName.formFieldsList) {
				let formUtility;
				if (cFormName) {
					formUtility = new DynamicFormUtility(cFormName, status);
				}

				if (dynamic.tables && Array.isArray(dynamic.tables)) {
					const cTable = dynamic.tables.find((row) => row.id === cFormName.tableId);
					if (cTable) {
						const primaryKeyColumn = cTable.ebpsColumns.find((row: IEBPSColumns) => row.isPk === 'Y');
						if (primaryKeyColumn) {
							const primaryKeyField = cFormName.formFieldsList.find((row) => row.ebpsColumnId === primaryKeyColumn.id);
							if (primaryKeyField) {
								this.setState({ appIdIndex: primaryKeyField.id || 0 });
							}
						}
					}
				}

				if (!isEmpty(dynamic.generalData) && dynamic.formData && Array.isArray(dynamic.formData)) {
					const prevData: any = {};
					let readOnlyData: any = {};
					let tempReadOnlyData: any = {};
					try {
						const { inputTypes, readOnlyTypes } = JSON.parse(cFormName.formLayout);
						readOnlyTypes.forEach((row: TReadOnlyTypeObject) => {
							if (row.type === EReadOnlyTypes.USER_INFO || (row.type === EReadOnlyTypes.DESIGNER_INFO && userData.designer)) {
								if (userData && userData.info && row.meta) {
									if (row.meta.startsWith('org') && userData.organization) {
										const [, key] = row.meta.split('-');
										//@ts-ignore
										prevData[row.id] = userData.organization[key] ? userData.organization[key] : '';
									}
									//@ts-ignore
									else if (userData.info[row.meta]) {
										//@ts-ignore
										prevData[row.id] = userData.info[row.meta];
									}
								}
							}

							if(row.type === EReadOnlyTypes.SURROUNDING_VALUES && permitData && permitData.surrounding){
								const surroundingArray = new SurroundingBlockArray(permitData.surrounding);
								tempReadOnlyData[row.columnName] = surroundingArray.getSurroundingValueWiseValue(row.meta);
							}

							if(row.type === EReadOnlyTypes.FLOOR_VALUES && permitData && permitData.floor){
								const floorArray = new FloorBlockArray(permitData.floor);
								tempReadOnlyData[row.columnName] = floorArray.getFloorValueWiseValue(row.meta);
							}

							if(row.type === EReadOnlyTypes.USERTYPE_DATA){
								const statusAray = new StatusDataArray(status);
								tempReadOnlyData[row.columnName] = statusAray.getStatusValue(row.meta);
							}
						});

						if (permitData && permitData.floor) {
							const floorArray = new FloorBlockArray(permitData.floor);
							inputTypes.forEach((row: TInputTypeObject) => {
								if (row.type === EInputTypes.FLOOR_VALUES) {
									if (row.meta && row.meta.identifier) {
										prevData[row.id] = floorArray.getFloorValueWiseValue(row.meta.identifier);
									}
								}
							});
						}
						if (permitData && permitData.surrounding) {
							const surroundingArray = new SurroundingBlockArray(permitData.surrounding);
							inputTypes.forEach((row: TInputTypeObject) => {
								if (row.type === EInputTypes.SURROUNDING_VALUES) {
									if (row.meta && row.meta.identifier) {
										prevData[row.id] = surroundingArray.getSurroundingValueWiseValue(row.meta.identifier);
									}
								}
							});
						}
						Object.entries(dynamic.referencedData).forEach(([key, value]) => {
							const inputType: TInputTypeObject = inputTypes.find((row: TInputTypeObject) => row.columnName === key);
							if (inputType && inputType.id) prevData[inputType.id] = value;
						});
						if (dynamic.formData.length > 0) {
							this.setState({ isEdit: true });
							Object.entries(dynamic.formData[0]).forEach(([key, value]) => {
								const inputType: TInputTypeObject = inputTypes.find((row: TInputTypeObject) => row.columnName === key);
								if (inputType && inputType.id) prevData[inputType.id] = value;
							});
						}
						readOnlyData = {...dynamic.referencedData, ...tempReadOnlyData};
						this.props.setPrevData(prevData);
						this.props.setCurrentIds(applicationNo, formId);
					} catch (error) {
						console.log(error, 'error>');
					}

					this.setState({ readOnlyData });
				}
				// const dataNames = this.props.api;
				const dataNames = ['generalData', 'vocabulary'];
				let hasLoaded: boolean[] = [];

				dataNames.forEach((obj, index) => {
					//@ts-ignore
					if (!isEmpty(this.props.dynamic[obj]) || Array.isArray(this.props.dynamic[obj])) {
						hasLoaded[index] = true;
					} else {
						hasLoaded[index] = false;
					}
				});

				this.setState({
					allLoaded: hasLoaded.reduce((loaded, next) => loaded && next, true),
				});
				this.setState({
					cFormMaster: cFormName || null,
					applicationNo,
					formId,
					formUtility: formUtility || null,
					isFormSetupValid: true,
				});
			} else {
				this.setState({
					isFormSetupValid: false,
					applicationNo,
					formId,
					allLoaded: true,
				});
			}
		}
		// if (this.props.adminData !== prevProps.adminData) {
		// 	const dataNames = this.props.api;
		// 	let hasLoaded: boolean[] = [];
		// 	let data: any = {};

		// 	dataNames.forEach((obj, index) => {
		// 		//@ts-ignore
		// 		if (!isEmpty(this.props.adminData[obj.objName]) || Array.isArray(this.props.adminData[obj.objName])) {
		// 			hasLoaded[index] = true;
		// 			Object.assign(data, {
		// 				//@ts-ignore
		// 				[obj.objName]: this.props.adminData[obj.objName],
		// 			});
		// 		} else {
		// 			hasLoaded[index] = false;
		// 		}
		// 	});

		// 	this.setState({
		// 		allLoaded: hasLoaded.reduce((loaded, next) => loaded && next, true),
		// 	});
		// }

		if (this.props.success !== prevProps.success) {
			// this.props.getAfterUpdateAdminData(this.props.updateApi);
		}
	}

	//print status globally
	setPrintContent = (value: boolean) => {
		this.props.setPrint(value);
	};

	render() {
		const { isFormSetupValid, cFormMaster, formUtility, applicationNo, formId, appIdIndex, readOnlyData, isEdit } = this.state;
		const { loading, errors, formHistory, comment, fileCategories, files, dynamic, permitData, userData } = this.props;

		const { prefixValues: statuses, isRejected } = formUtility ? formUtility.getStatusNameValues() : { prefixValues: [], isRejected: false };
		if (!isFormSetupValid) {
			if (loading) return <FallbackComponent errors={errors} loading={loading} />;

			return (
				<div>
					<CompactError message={dynamicFormErrors.formSetupError} />
				</div>
			);
		}
		if (!this.state.allLoaded) {
			return <FallbackComponent errors={this.props.errors} loading={this.props.loading} />;
		} else {
			return (
				<div>
					<DynamicActionBar
						parentHistory={this.props.parentProps.history}
						//Print Props
						content={this.formRef.current!}
						setPrintContent={this.setPrintContent}
						onBeforeGetContent={this.props.onBeforeGetContent}
						context={this.formRef}
						//Form Props
						formUrl={cFormMaster ? cFormMaster.viewUrl : null}
						approvalUrl={`${cFormMaster ? cFormMaster.viewUrl : ''}${applicationNo}`}
						statuses={statuses}
						historyList={formHistory}
						comments={comment}
						isRejected={isRejected}
						fileCategories={fileCategories || []}
						files={files || []}
						// Permissions
						hasApprovePermission={formUtility!.approvePermission}
						hasSavePermission={formUtility!.savePermission}
					/>
					<div ref={this.formRef}>
						{this.props.render({
							...this.props,
							savePermission: formUtility!.savePermission,
							vocabulary: dynamic.vocabulary,
							isEdit,
							permitData,
							userData,
							readOnlyData,
							staticFiles: files ? getStaticFiles(files) : {},
							cFormMaster,
							postAction: (data: any) =>
								this.props.postFormDataByUrl(
									api.d_general,
									{
										...data,
										// enter_by: cFormMaster!.enterBy,
										// enter_date: new Date(),
										[appIdIndex]: applicationNo,
									},
									applicationNo || '',
									formId || '',
									formUtility!.refetchMenuAfterSave,
								),
						})}
					</div>
				</div>
			);
		}
	}
}

const mapDispatchToProps = {
	getPermitAndUserData,
	getFormContainerData,
	getDynamicFormContainerData,
	setPrevData,
	setCurrentIds,
	postFormDataByUrl,
	getAdminDataByUrl,
	postFormDataWithFileByUrl,
	setPrint: (res: boolean) => setPrint(res),
};

const mapStateToProps = (state: TReduxState) => {
	return {
		permitData: state.root.dynamic.permitData,
		userData: state.root.dynamic.userData,
		formData: state.root.formData,
		loading: state.root.ui.loading,
		errors: state.root.ui.errors,
		success: state.root.formData.success,
		printcontent: state.root.general.printcontent,
		fileCategories: state.root.dynamic.fileCategories,
		files: state.root.dynamic.files,
		adminData: state.root.admin,
		dynamic: state.root.dynamic,
		comment: state.root.dynamic.comment,
		formHistory: state.root.dynamic.history,
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(DynamicFormContaner);
// export default DynamicFormContaner;
