import React from 'react';
import Viewer from 'react-viewer';
import { List, Divider } from 'semantic-ui-react';
import { useFileViewOnly } from '../../../hooks/useFileView';
import { IFile, IFileCategory } from '../../../interfaces/FileInterfaces';
import { getSplitUrl } from '../../../utils/functionUtils';
import { ImageDisplayPreview } from './FileView';

type TProps = {
	files?: IFile[] | null;
	fileCategories?: IFileCategory[] | null;
};

export function FileViewOnly({ files, fileCategories }: TProps) {
	const { imageOpen, currImg, handleImageClick, handleCloseViewer } = useFileViewOnly();
	return (
		<div>
			{fileCategories &&
				fileCategories.map((category) => (
					<div key={category.id}>
						<Divider horizontal>{category.name}</Divider>{' '}
						<List relaxed>
							<List.Header>Existing Files</List.Header>
							<div className="previewFileUpload">
								{files && files.length > 0 && files.some((file) => file.fileType.id === category.id) ? (
									files
										.filter((file) => file.fileType.id === category.id)
										.map((el, index) => {
											const fileTitleName = getSplitUrl(el.fileUrl, '/');
											return (
												<div key={el.fileUrl} className={'eachPreviewFile viewFileSuccess'} title={fileTitleName}>
													<>
														<ImageDisplayPreview
															isInput={false}
															src={el.fileUrl}
															alt={fileTitleName}
															onClick={() => handleImageClick(el.fileUrl)}
														/>
													</>
												</div>
											);
										})
								) : (
									<List.Item>No files uploaded.</List.Item>
								)}
							</div>
						</List>
						<Viewer visible={imageOpen} onClose={handleCloseViewer} images={[{ src: currImg || '' }]} zIndex={10000} />
					</div>
				))}
		</div>
	);
}
