import React from 'react';
import { Button, Modal } from 'semantic-ui-react';
import ErrorDisplay from '../../shared/ErrorDisplay';

type TModalProps = {
	loading: boolean;
	errors: { message: string } | null;
	handleSubmit?: () => void;
};

interface IModalComposition {
	AdminModal: React.FC<TAdminModalProps>;
}

export const ModalContext = React.createContext<TModalProps>({ loading: false, errors: null });

const useModal = (): TModalProps => {
	const context = React.useContext(ModalContext);

	if (!context) {
		throw new Error('This Component must be used within a <ModalComponent> component');
	}

	return context;
};

type TAdminModalProps = {
	modalId: string;
	open: boolean;
	handleClose: () => void;
	handleSubmit?: () => void;
	size?: 'small' | 'mini' | 'tiny' | 'large' | 'fullscreen' | undefined;
	title: string;
	cancelText?: string;
	saveText: string;
	scroll?: boolean;
};

const AdminModal: React.FC<TAdminModalProps> = ({
	modalId,
	open,
	handleClose,
	size = 'small',
	title,
	scroll = false,
	handleSubmit,
	cancelText = 'Cancel',
	saveText,
	children,
}) => {
	const { loading, errors, handleSubmit: parentHandleSubmit } = useModal();
	return (
		<Modal key={modalId} closeIcon open={open} onClose={handleClose} size={size}>
			<Modal.Header>{title}</Modal.Header>
			<Modal.Content scrolling={scroll ? true : undefined}>
				<>
					{errors && <ErrorDisplay message={errors.message} />}
					{children}
				</>
			</Modal.Content>
			<Modal.Actions>
				<Button disabled={loading} negative onClick={handleClose}>
					{cancelText}
				</Button>
				<Button
					disabled={loading}
					positive
					icon="checkmark"
					labelPosition="right"
					type="button"
					content={saveText || 'Save'}
					onClick={(e) => {
						if (handleSubmit) {
							handleSubmit();
						} else if (parentHandleSubmit) {
							parentHandleSubmit();
						}
					}}
				/>
			</Modal.Actions>
		</Modal>
	);
};

/**
 * Compound Component to render a Admin Modal
 * @param props
 */
const ModalComponent: React.FC<TModalProps> & IModalComposition = ({ loading, errors, handleSubmit, children }) => {
	return <ModalContext.Provider value={{ loading, errors, handleSubmit }}>{children}</ModalContext.Provider>;
};

ModalComponent.AdminModal = AdminModal;

export { ModalComponent };
