import React, { useEffect, useState } from 'react';
import { IUserInfo } from '../../../../interfaces/ApiInterfaces';
import { TOutputFormat, TReadOnlyTypeObject } from '../utils/types/setupTypes';

type TProps = {
	prevData?: any;
	row: TOutputFormat;
	readOnlyTypes: TReadOnlyTypeObject[];
};

export const RenderReadOnlyType = ({ prevData, row, readOnlyTypes }: TProps) => {
	const [data, setData] = useState('');
	useEffect(() => {
		if (prevData) {
			const readOnlyRow = readOnlyTypes.find((obj) => obj.meta === row.content);
			if (readOnlyRow) {
				//@ts-ignore
				setData(prevData[readOnlyRow.id]);
			}
		}
	}, [prevData, row.id]);

	return <div style={{ display: 'inline-block' }}>{data}</div>;
};
