import React, { useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Button, Modal, ModalActions, ModalContent, ModalHeader, Select } from 'semantic-ui-react';
import { TEMPLATE_DELIMETER } from './utils/constants';
import { EComponentIdentifiers } from './utils/enums/ComponentIdentifiers';
import { ELayoutIdentifier } from './utils/enums/LayoutIdentifiers';

const componentOptions = [
	{ key: 1, text: 'Letter Head', value: EComponentIdentifiers.LETTER_HEAD },
	{ key: 2, text: 'Letter Head with Photo', value: EComponentIdentifiers.LETTER_HEAD_PHOTO },
	{ key: 3, text: 'Footer Signature', value: EComponentIdentifiers.FOOTER_SIGNATURE },
];

export const AddComponents = () => {
	const [open, setOpen] = useState(false);
	const [info, setInfo] = useState<string>('letter-head');

	const handleClose = () => {
		setOpen(false);
	};

	const handleOpen = () => {
		setOpen(true);
	};

	return (
		<>
			<Button content="Add Component" size="tiny" compact onClick={handleOpen} className="primary-btn" icon="image" />
			<Modal open={open} onClose={handleClose} size="tiny" closeIcon>
				<ModalHeader>Add Component</ModalHeader>
				<ModalContent>
					Select Component: <Select options={componentOptions} value={info} onChange={(e, { value }) => setInfo(String(value))} />
				</ModalContent>
				<ModalActions>
					<Button negative onClick={handleClose}>
						Cancel
					</Button>
					<CopyToClipboard
						text={`[[${ELayoutIdentifier.COMPONENT}${TEMPLATE_DELIMETER}${info}${
							info === EComponentIdentifiers.FOOTER_SIGNATURE ? `${TEMPLATE_DELIMETER}Type Signature Here Line 1 | Line 2;Type Signature Here` : ''
						}]]`}
						onCopy={handleClose}
					>
						<Button positive icon="copy" type="button" content="Copy" />
					</CopyToClipboard>
				</ModalActions>
			</Modal>
		</>
	);
};
