import React, { useEffect, useState } from 'react';
import * as H from 'history';
import DynamicFormContaner from '../../../containers/base/DynamicFormContainer';
import api from '../../../utils/api';
import { ApiParam } from '../../../utils/paramUtil';
import { PrintParams } from '../../../utils/printUtils';
import { IFormNameMaster, IVocabulary } from '../interfaces';
import { LayoutRenderer } from '../layoutSetupComponents/LayoutRenderer';
import { TInputTypeObject, TOutputFormat, TReadOnlyTypeObject } from '../layoutSetupComponents/utils/types/setupTypes';
import { CompactError } from '../../shared/ErrorDisplay';
import { handleDynamicSuccess } from '../../../utils/dataUtils';
import { IPermitData, IUserInfo } from '../../../interfaces/ApiInterfaces';
import { getLocalStorage } from '../../../utils/secureLS';
import { REJECTED_STATUS } from '../../../utils/constants';

type TProps = {
	cFormMaster: IFormNameMaster;
	formId: string;
	postAction: (data: any) => void;
	errors?: { message: string };
	loading: boolean;
	applicationNo: string;
	savePermission: boolean;
	isEdit: boolean;
	success: any;
	history: H.History;
	readOnlyData: any;
	vocabulary: IVocabulary[];
	permitData: IPermitData;
	userData: IUserInfo;
	staticFiles: any;
};

export const DynamicFormComponent = ({
	applicationNo,
	success = {},
	readOnlyData,
	history,
	cFormMaster,
	formId,
	postAction,
	errors,
	loading,
	isEdit,
	savePermission,
	vocabulary,
	permitData,
	userData,
	staticFiles,
}: TProps) => {
	const [output, setOutput] = useState<TOutputFormat[]>([]);
	const [inputTypes, setInputTypes] = useState<TInputTypeObject[]>([]);
	const [readOnlyTypes, setReadOnlyTypes] = useState<TReadOnlyTypeObject[]>([]);

	useEffect(() => {
		if (cFormMaster) {
			try {
				const { layout, inputTypes, readOnlyTypes } = JSON.parse(cFormMaster.formLayout);
				setOutput(layout);
				setInputTypes(inputTypes);
				setReadOnlyTypes(readOnlyTypes);
			} catch (error) {
				console.log(error);
			}
		}
	}, [formId, cFormMaster]);

	useEffect(() => {
		let isRejected = false;
		try {
			const rejectedStatusObject = JSON.parse(getLocalStorage(REJECTED_STATUS));
			isRejected = Boolean(rejectedStatusObject && rejectedStatusObject.rejectedBy);
		} catch (error) {
			console.log(error);
		}

		if (success && success.success) handleDynamicSuccess(isRejected, cFormMaster.id, applicationNo, history, success);
	}, [success]);

	return (
		<div>
			{errors ? <CompactError message={errors.message} /> : null}
			<LayoutRenderer
				permitData={permitData}
				userData={userData}
				// prevData={prevData}
				applicationNo={applicationNo}
				readOnlyData={readOnlyData}
				output={output}
				inputTypes={inputTypes}
				readOnlyTypes={readOnlyTypes}
				postAction={postAction}
				loading={loading}
				savePermission={savePermission}
				isEdit={isEdit}
				staticFiles={staticFiles}
				vocabulary={vocabulary}
			/>
		</div>
	);
};

export type TDynamicFormProps = {
	match: { params: { applicationNo: string; formId: string } };
	location: H.Location;
	history: H.History;
};

export const DynamicForm = (parentProps: TDynamicFormProps) => {
	const {
		match: {
			params: { applicationNo, formId },
		},
		history,
	} = parentProps;

	useEffect(() => {
		window.scrollTo(0, 0);
		if (!applicationNo || !formId) {
			history.push('/');
		}
	}, [applicationNo, formId]);

	return (
		<DynamicFormContaner
			api={[new ApiParam(api.d_formNameMaster, 'formNameMaster').getParams()]}
			onBeforeGetContent={{
				...PrintParams.INLINE_FIELD,
				param1: ['getElementsByTagName', 'input', 'value'],
				param4: ['getElementsByTagName', 'textarea', 'value'],
				param6: ['getElementsByClassName', 'dashedForm-control', 'value'],
			}}
			parentProps={parentProps}
			render={
				//@ts-ignore
				(props) => <DynamicFormComponent {...props} {...parentProps} formId={formId} applicationNo={applicationNo} />
			}
		/>
	);
};
