import { TReduxState } from "../storeStateTypes"

export const selectPrevData = (state: { root: { currentForm: { prevData: any } } }): any => {
    try {
        return state.root.currentForm.prevData
    } catch (error) {
        return {}
    }
}

export const selectAppId = (state: TReduxState): any => {
    try {
        return state.root.currentForm.applicationId
    } catch (error) {
        return {}
    }
}

export const selectCurrentIds = (state: TReduxState): { applicationId: number | string, formId: number | string } => {
    try {
        return {
            applicationId: state.root.currentForm.applicationId,
            formId: state.root.currentForm.formId,
        }
    } catch (error) {
        return { applicationId: 0, formId: 0 }
    }
}