import { Formik } from 'formik';
import React, { useDebugValue, useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import { Button, Form, Grid, Header, List, Segment, Table } from 'semantic-ui-react';
import AdminContainer from '../../containers/base/AdminContainer';
import { IAdminContainerProps } from '../../containers/base/containerInterfaces';
import api from '../../utils/api';
import { tableSetupData } from '../../utils/data/ebps-setup/tableSetup';
import { showErrorToast, showToast } from '../../utils/functionUtils';
import { AdminModalContainer } from '../ebps-setup/AdminModalContainer';
import { DeleteButtonV2, EditButtonV2 } from '../ebps-setup/forms/Buttons';
import { NormalInput } from '../shared/fields/NormalInput';
import { FlexSpaceBetween } from '../uiComponents/FlexDivs';
import { SectionHeader } from '../uiComponents/Headers';
import { vocabularyDetailsSchema } from './dynamicSetupSchemas';
import { IVocabulary, IVocabularyDetails, TOption } from './interfaces';

const tableHeadings = tableSetupData.table.tableHeadings;
const modalData = tableSetupData.columnModal;

const initialAddState = {
	id: 0,
	value: '',
	label: '',
	vocabularyId: 0,
};

const EditVocabularyDetailsComponent = ({
	match,
	adminData,
	postAdminDataByUrl,
	deleteAdminDataByUrl,
	errors,
	success,
	loading,
}: IAdminContainerProps<{ vocabulary: [] }>) => {
	const history = useHistory();
	const [data, setData] = useState<IVocabulary>();

	const [openAddModal, setOpenAddModal] = useState(false);
	const [openDeleteModal, setOpenDeleteModal] = useState(false);
	const [isEditing, setIsEditing] = useState(false);
	const [deleteIndex, setDeleteIndex] = useState<number | null | undefined>(null);
	const [editData, setEditData] = useState<IVocabularyDetails | null>(null);

	useDebugValue(deleteIndex, (value) => `delete ${value}`);

	useEffect(() => {
		setData(adminData.vocabulary.find((row: IVocabulary) => String(row.id) === String(match.params.id)));
	}, [adminData.vocabulary]);

	useEffect(() => {
		if (success && success.success) {
			if (deleteIndex) {
				showToast('Data deleted successfully');
				setDeleteIndex(null);
				setOpenDeleteModal(false);
			} else {
				showToast('Data saved successfully');
				setEditData(null);
				setOpenAddModal(false);
			}
		}
	}, [success]);

	const handleEdit = (row: IVocabularyDetails) => {
		console.log(row);

		setEditData(row);
		setIsEditing(true);
		setOpenAddModal(true);
	};

	const handleDelete = (row: IVocabularyDetails) => {
		if (row) {
			setDeleteIndex(row.id);
			setOpenDeleteModal(true);
		}
	};

	const handleCloseModal = () => {
		setOpenAddModal(false);
		setOpenDeleteModal(false);
		setIsEditing(false);
		setEditData(null);
		setDeleteIndex(null);
	};

	return (
		<div>
			<Formik
				enableReinitialize
				validationSchema={!deleteIndex ? vocabularyDetailsSchema : null}
				initialValues={isEditing ? editData : initialAddState}
				onSubmit={async (values, actions) => {
					actions.setSubmitting(true);
					try {
						if (isEditing) {
							//@ts-ignore
							const requiredColumns = data ? data.vocabularyDetails.filter((row) => row.id !== values.id).concat(values) : [];
							console.log(requiredColumns);

							postAdminDataByUrl(api.d_vocabulary, {
								...data,
								vocabularyDetails: requiredColumns,
							});
						} else if (deleteIndex !== null) {
							deleteAdminDataByUrl(api.d_vocabularyDetails, deleteIndex);
						} else {
							postAdminDataByUrl(api.d_vocabulary, {
								...data,
								vocabularyDetails: data
									? data.vocabularyDetails.concat({ vocabularyId: data!.id, value: values!.value, label: values!.label })
									: [{ value: values!.value, vocabularyId: data!.id }],
							});
						}
						handleCloseModal();
						actions.setSubmitting(false);
					} catch (error) {
						console.log('Error in menu master post', error);
						showErrorToast('Something went wrong.');
						actions.setSubmitting(false);
					}
					setOpenAddModal(false);
				}}
			>
				{({ isSubmitting, handleSubmit }) => (
					<>
						<AdminModalContainer
							key="add-edit-columns"
							size="small"
							open={openAddModal}
							scroll={false}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={handleCloseModal}
							title={isEditing ? modalData.titleEdit : modalData.titleAdd}
							saveText={isEditing ? modalData.editText : modalData.saveText}
						>
							<Form loading={loading || isSubmitting}>
								<Form.Group>
									<NormalInput name="value" label="Value" />
									<NormalInput name="label" label="Label" />
								</Form.Group>
							</Form>
						</AdminModalContainer>
						<AdminModalContainer
							key="delete-columns"
							size="tiny"
							open={openDeleteModal}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={handleCloseModal}
							title={modalData.delete.title}
							saveText={modalData.delete.title}
						>
							<Form loading={loading || isSubmitting}>{modalData.delete.content}</Form>
						</AdminModalContainer>
					</>
				)}
			</Formik>
			<div>
				{data ? (
					<Segment loading={loading}>
						<FlexSpaceBetween>
							<Header>Table Information</Header>
							<div>
								<Button
									onClick={() => history.push('/admin/vocabulary-setup')}
									icon="arrow left"
									content="Back"
									compact
									size="tiny"
									className="danger-button"
								/>
							</div>
						</FlexSpaceBetween>
						<List className="app-info">
							<Grid>
								<Grid.Row columns="3">
									<Grid.Column>
										<List.Item>
											<List.Description>{data.name}</List.Description>
											<List.Header>{tableHeadings.name}</List.Header>
										</List.Item>
									</Grid.Column>
									<Grid.Column>
										<List.Item>
											<List.Description>{data.id}</List.Description>
											<List.Header>{tableHeadings.id}</List.Header>
										</List.Item>
									</Grid.Column>
								</Grid.Row>
								<Grid.Row></Grid.Row>
							</Grid>
						</List>
					</Segment>
				) : null}
				<Form.Group>
					<FlexSpaceBetween>
						<SectionHeader>
							<h4 className="left-align">Columns</h4>
						</SectionHeader>
						<Button
							onClick={() => setOpenAddModal(true)}
							icon="add"
							content="Add Columns"
							className="success-button"
							compact
							size="tiny"
						/>
					</FlexSpaceBetween>
					<Table striped celled compact="very" size="small" className="english-div">
						<Table.Header>
							<Table.Row>
								<Table.HeaderCell>Id</Table.HeaderCell>
								<Table.HeaderCell>Value</Table.HeaderCell>
								<Table.HeaderCell>Label</Table.HeaderCell>
								<Table.HeaderCell>Actions</Table.HeaderCell>
							</Table.Row>
						</Table.Header>
						<Table.Body>
							{data && data.vocabularyDetails && data.vocabularyDetails.length > 0
								? data.vocabularyDetails.map((row) => (
										<Table.Row>
											<Table.Cell>{row.id}</Table.Cell>
											<Table.Cell>{row.value}</Table.Cell>
											<Table.Cell>{row.label}</Table.Cell>
											<Table.Cell>
												<EditButtonV2 handleEdit={handleEdit} row={row} />
												<DeleteButtonV2 handleDelete={handleDelete} row={row} />
											</Table.Cell>
										</Table.Row>
								  ))
								: null}
						</Table.Body>
					</Table>
				</Form.Group>
			</div>
		</div>
	);
};

export const EditVocabularyDetails = (parentProps: any) => (
	//@ts-ignore
	<AdminContainer
		//@ts-ignore
		api={[{ api: api.d_vocabulary, objName: 'vocabulary' }]}
		//@ts-ignore
		updateApi={[{ api: api.d_vocabulary, objName: 'vocabulary' }]}
		//@ts-ignore
		render={(props: any) => <EditVocabularyDetailsComponent {...props} parentProps={parentProps} />}
	/>
);
