import JqxMenu from 'jqwidgets-scripts/jqwidgets-react-tsx/jqxmenu';
import React, { RefObject } from 'react';
import { useSelector } from 'react-redux';
import { Button, Sticky } from 'semantic-ui-react';
import { IFile, IFileCategory } from '../../../interfaces/FileInterfaces';
import { selectNeedsFileView } from '../../../store/selectors/dynamicSelectors';
import { TReduxState } from '../../../store/storeStateTypes';
import { NIBEDAK_PHOTO_TITLE } from '../../../utils/constants';
import { FormUrlFull } from '../../../utils/enums/url';
import { brack, makeprintformat } from '../../../utils/functionUtils';
import ForwardToNextForm from '../../loggedInComponents/forms/ForwardToNextForm';
import ListFiles from '../../loggedInComponents/forms/ListFiles';
import PermitPhotoUpload from '../../loggedInComponents/forms/mapPermitComponents/PermitPhotoUpload';
import SingleFileUpload from '../../loggedInComponents/forms/SingleFileUpload';
import { ActionComments } from '../../shared/ApprovalStatusDisplay';
import { GenericFileView } from '../../shared/file/NoticePaymentFileView';
import { HistoryContent } from '../../shared/HistoryComponent';
import Printthis from '../../shared/Printthis';
import ShortcutHtml from '../../shared/ShortcutHtml';
import { useApproveRejectModal } from '../hooks/useApproveRejectModal';
import { DynamicApprovalGroup } from './DynamicApprovalGroup';
import DynamicApprovalStatus from './DynamicApprovalStatus';
import DynamicGenericFloatingWindow from './DynamicGenericFloatingWindow';
import { TStatuses } from './utils/dynamicFormUtils';

type TProps = {
	context: RefObject<HTMLDivElement>;
	isDisabled?: boolean;
	hasApprovePermission?: boolean;
	hasSavePermission?: boolean;
	shortcut?: boolean;
	isRejected?: boolean;
	statuses: TStatuses[];
	onBeforeGetContent?: { [key: string]: string[] };
	content: HTMLDivElement;
	setPrintContent: (val: boolean) => void;
	approvalRef?: any;
	approvalUrl: string;
	historyList: [];
	comments: [];
	parentHistory: any;
	formUrl: string | null;
	fileCategories: IFileCategory[];
	files: IFile[];
};
export const DynamicActionBar = ({
	onBeforeGetContent,
	context,
	isDisabled = false,
	hasApprovePermission = false,
	hasSavePermission = false,
	shortcut = false,
	statuses,
	isRejected,
	setPrintContent,
	content,
	historyList = [],
	comments = [],
	formUrl,
	parentHistory,
	// approvalRef,
	approvalUrl,
	files,
	fileCategories,
}: TProps) => {
	const needsFile = useSelector<TReduxState, boolean>(selectNeedsFileView);
	let approvalRef = React.createRef();
	const { state: modalState, openApproval, openFileApproval, openRejection, closeModals } = useApproveRejectModal();
	const isMapPermit = formUrl === FormUrlFull.BUILD_PERMIT;

	let dupContent: Node;
	let printContent = onBeforeGetContent ? (
		<Printthis
			content={() => dupContent}
			onBeforeGetContent={() => {
				setPrintContent(true);
				dupContent = content.cloneNode(true);
				brack(dupContent);
				Object.values(onBeforeGetContent).forEach((each) => {
					//@ts-ignore
					return makeprintformat(dupContent, ...each);
				});
				if (approvalRef) {
					//@ts-ignore
					dupContent.append(approvalRef);
				}
			}}
			onBeforePrint={() => {
				setPrintContent(false);
			}}
			ui={<Button className="primary-btn" compact size="tiny" icon="print" content="Print" />}
		/>
	) : (
		<Printthis
			content={() => content}
			onBeforeGetContent={() => {
				setPrintContent(true);
			}}
			onBeforePrint={() => {
				setPrintContent(false);
			}}
			ui={<Button className="primary-btn" compact size="tiny" icon="print" content="Print" />}
		/>
	);

	return (
		<div>
			<DynamicApprovalGroup
				// parentHistory={parentHistory}
				// noticePaymentData={undefined}
				// url={approvalUrl}
				// fileCategories={fileCategories}
				formUrl={formUrl || ''}
				{...modalState}
				closeModals={closeModals}
				//@ts-ignore
				// approvalAppNo={approvalAppNo}
			/>

			<Sticky context={context} offset={43} style={{ marginTop: '-26px', marginBottom: '20px', marginLeft: '-20px', marginRight: '-20px' }}>
				<JqxMenu>
					<ul>
						{hasApprovePermission && (
							<>
								<li style={{ color: '#099e2c', pointerEvents: isDisabled ? 'none' : 'auto', opacity: isDisabled ? 0.3 : 1 }}>
									<Button
										content="Approve"
										size="tiny"
										compact
										className="success-button"
										icon="thumbs up"
										onClick={() => {
											//@ts-ignore
											// if (isNoticePayment) this.approveRef.handleNoticeApproval(true);
											// //@ts-ignore
											// else if (isMapPermit) this.approveRef.handleMapPermitApproval(true);
											// else {
											// fileCategories &&
											// fileCategories.some(
											// 	//@ts-ignore
											// 	(category) => category.viewUrl && category.viewUrl.trim() === formUrl
											// )
											// 	? openFileApproval()
											// :
											openApproval();
										}}
									/>{' '}
									{shortcut && <ShortcutHtml content="[alt + a]" />}
								</li>
								<li style={{ color: '#099e2c', pointerEvents: isDisabled ? 'none' : 'auto', opacity: isDisabled ? 0.3 : 1 }}>
									<Button
										content="Reject"
										size="tiny"
										compact
										className="danger-button"
										icon="thumbs down"
										onClick={
											() => {
												// console.log('oi');

												openRejection();
											}
											// //@ts-ignore
											// if (isNoticePayment) this.approveRef.handleNoticeApproval(true);
											// //@ts-ignore
											// else if (isMapPermit) this.approveRef.handleMapPermitApproval(true);
											// else {
											// 	fileCategories &&
											// 	fileCategories.some(
											// 		//@ts-ignore
											// 		(category) => category.viewUrl && category.viewUrl.trim() === formUrl
											// 	)
											// 		? // @ts-ignore
											// 		  //@ts-ignore
											// 		  this.approveRef.handleApprovalModalConfirmOpen()
											// 		: //@ts-ignore
											// 		  //@ts-ignore
											// 		  this.approveRef.handleApprovalModalOpen();
											// }
										}
									/>
									{shortcut && <ShortcutHtml content="[alt + r]" />}
								</li>
							</>
						)}
						{needsFile && (
							<>
								{hasSavePermission && (
									<li
										id="menuItemFileUpload"
										className="jqx-item jqx-menu-item-top jqx-rc-all"
										role="menuitem"
										style={{ float: 'left' }}
									>
										<DynamicGenericFloatingWindow label="Upload File" icon="file">
											<>
												{isMapPermit && <PermitPhotoUpload />}
												<SingleFileUpload url={formUrl} fileCategories={fileCategories} prevData={{}} files={files} />
											</>
										</DynamicGenericFloatingWindow>
									</li>
								)}{' '}
								<li
									id="menuItemFileUpload"
									className="jqx-item jqx-menu-item-top jqx-rc-all"
									role="menuitem"
									style={{ float: 'left' }}
								>
									<DynamicGenericFloatingWindow label="View File" icon="file code">
										<div>
											{isMapPermit && <GenericFileView url={''} width={350} title={NIBEDAK_PHOTO_TITLE} />}
											<ListFiles url={formUrl} Durl={formUrl} prevData={{}} />
										</div>
									</DynamicGenericFloatingWindow>
								</li>
							</>
						)}
						<li>{printContent}</li>
						<li>
							<DynamicGenericFloatingWindow label="History" icon="history">
								<HistoryContent historyList={historyList} />
							</DynamicGenericFloatingWindow>
						</li>
						<li>
							<DynamicGenericFloatingWindow label="Comments" icon="comments" isRejected={isRejected}>
								<ActionComments comments={comments} />
							</DynamicGenericFloatingWindow>
						</li>{' '}
						<li>
							<DynamicApprovalStatus isRejected={isRejected} statuses={statuses} />
						</li>
						<li>
							<DynamicGenericFloatingWindow label="Forward to Next" icon="arrow left">
								<ForwardToNextForm history={parentHistory} location={{ pathname: formUrl }} modal={true} />
							</DynamicGenericFloatingWindow>
						</li>
					</ul>
				</JqxMenu>
			</Sticky>
		</div>
	);
};
