import { Formik } from 'formik';
import React, { useState } from 'react';
import * as Yup from 'yup';
import { connect } from 'react-redux';

import { Button, Dimmer, Form, Icon, Label, Loader, Modal, Segment } from 'semantic-ui-react';
import { approveReject } from '../../../utils/data/genericData';
import { validateString } from '../../../utils/validationUtils';
import ErrorDisplay from '../../shared/ErrorDisplay';
import { MultiLangTextArea } from '../inputFields/MultiLangTextArea';
import { EApproveRejectModule, TReduxState, TSuccess } from '../../../store/storeStateTypes';
import { putDynamicApprovalByUrl } from '../../../store/actions/dynamicFormActions';
import { selectCurrentIds } from '../../../store/selectors/currentFormSelectors';

const validationSchema = Yup.object().shape({
	comment: validateString,
	remark: validateString,
});

type TProps = {
	open: boolean;
	onClose: () => void;
	url: string;
	putDynamicApprovalByUrl: (url: string, data: any, module: EApproveRejectModule) => void;
	success: TSuccess | undefined;
	loadingModal: boolean;
	applicationId: string | number;
	formId: string | number;
	history: any;
	content?: string;
	title?: string;
	apiErrors: any;
};

function DynamicRejectionModalComponent({
	open,
	onClose,
	url,
	loadingModal,
	applicationId,
	formId,
	putDynamicApprovalByUrl,
	success,
	history,
	content = approveReject.approve.content,
	title = approveReject.approve.title,
	apiErrors,
}: TProps) {
	const [error, setError] = useState('');
	const [isNepali, setIsNepali] = useState([false, false]);

	const handleClose = () => {
		setError('');
		onClose();
	};
	return (
		<Modal key="rejection-modal" closeIcon open={open} onClose={handleClose}>
			<Modal.Header>{approveReject.reject.title}</Modal.Header>
			<>
				<Dimmer active={loadingModal} inverted>
					<Loader active={loadingModal} inline="centered">
						Sending Rejection...
					</Loader>
				</Dimmer>
				<Formik
					initialValues={{ status: '', comment: '', remark: '' }}
					validationSchema={validationSchema}
					onSubmit={async (values, actions) => {
						actions.setErrors({});
						actions.setSubmitting(true);
						values.status = 'R';
						try {
							await putDynamicApprovalByUrl(url, values, EApproveRejectModule.REJECT);
						} catch (err) {
							console.log('Message', err);
							setError('Network Error');
						}

						// rejectionUrl(url, values)
						// 	.then((res) => {
						// 		if (res.data && res.data.message) {
						// 			showToast('Form rejection sent.');
						// 			handleClose();
						// 			history.push('/user/task-list');
						// 			actions.setSubmitting(false);
						// 		} else if (res.data && res.data.error) {
						// 			setError(res.data.error.message);
						// 		} else {
						// 			setError(
						// 				// 'Invalid user type. Only Sub-Engineer or Engineer can approve.'
						// 				'Something went wrong.'
						// 			);
						// 		}
						// 	})
						// 	.catch((err) => {
						// 		console.log('ERROR', err, typeof err);
						// 		setError('Network Error');
						// 		// actions.setErrors({ networkError: 'Network error.'});
						// 	});

						actions.setSubmitting(false);
					}}
					render={({ handleSubmit, errors, setFieldValue, values, handleChange }) => {
						return (
							<Form loading={loadingModal}>
								{error && <ErrorDisplay message={error} />}
								{apiErrors && <ErrorDisplay message={apiErrors.message} />}
								<Modal.Content scrolling>
									<Segment>
										<h3>
											{/* <strong> */}
											{approveReject.reject.content}
											{/* </strong> */}
										</h3>
										<MultiLangTextArea
											isNepali={isNepali[0]}
											name="comment"
											onChange={handleChange}
											setFieldValue={setFieldValue}
											value={values.comment}
											error={errors.comment}
											label={approveReject.reject.comment}
											toggler={
												<Label
													as="a"
													className="lang-selector"
													onClick={() => setIsNepali((prevState) => [!prevState[0], prevState[1]])}
												>
													<Icon name="keyboard" />
													<span>{isNepali[0] ? 'ने' : 'en'}</span>
												</Label>
											}
											cols={100}
											rows={4}
											pointing="above"
										/>
										<MultiLangTextArea
											isNepali={isNepali[1]}
											name="remark"
											onChange={handleChange}
											setFieldValue={setFieldValue}
											value={values.remark}
											error={errors.remark}
											label={approveReject.reject.remarks}
											toggler={
												<Label
													as="a"
													className="lang-selector"
													onClick={() => setIsNepali((prevState) => [prevState[0], !prevState[1]])}
												>
													<Icon name="keyboard" />
													<span>{isNepali[1] ? 'ने' : 'en'}</span>
												</Label>
											}
											cols={100}
											rows={4}
											pointing="above"
										/>
									</Segment>
								</Modal.Content>
								<Modal.Actions>
									<Segment textAlign="right">
										<Button negative onClick={handleClose}>
											{approveReject.reject.close}
										</Button>
										<Button
											positive
											icon="checkmark"
											labelPosition="right"
											content={approveReject.reject.save}
											onClick={() => handleSubmit()}
										/>
									</Segment>
								</Modal.Actions>
							</Form>
						);
					}}
				/>
			</>
		</Modal>
	);
}

const mapDispatchToProps = { putDynamicApprovalByUrl };
const mapStateToProps = (state: TReduxState) => {
	const { applicationId, formId } = selectCurrentIds(state);
	return {
		loadingModal: state.root.ui.loadingModal,
		success: state.root.formData.success,
		applicationId,
		formId,
		apiErrors: state.root.ui.errors,
	};
};

export const DynamicRejectionModal = connect(mapStateToProps, mapDispatchToProps)(DynamicRejectionModalComponent);
