import api from "../../utils/api";
import { getBackUrl } from "../../utils/config";
import { CURRENT_BUILD_PERMIT, FILE_STORAGE_CATEGORY, FORM_NAME_MASTER, MENU_LIST } from "../../utils/constants";
import { getBuildPermitObj, getToken, getUserInfoObj, isEmpty } from "../../utils/functionUtils";
import { getLocalStorage, setLocalStorage } from "../../utils/secureLS";
import { CLEAR_ERRORS, LOADING_UI, D_CLEAR_FORM_DATA, D_SET_FORM_DATA, SET_UNSET_PERMIT_ERRORS, STOP_LOADING, SET_ERRORS, SET_SUCCESS_FORM_SUBMIT, CLEAR_SUCCESS_STATE, STOP_LOADING_MODAL, LOADING_MODAL, D_SET_APPROVAL_REJECTION_SUCCESS, SET_CURRENT_PERMIT } from "../types";

import axios, { AxiosResponse } from 'axios';
import { ApiParam } from "../../utils/paramUtil";
import { DynamicFormUtility } from "../../components/dynamic-setup/dynamicFormComponents/utils/dynamicFormUtils";
import { ActionDispatch } from "../../interfaces/ReduxInterfaces";
import { translateNepToEng } from "../../utils/langUtils";
import { IPermitData } from "../../interfaces/ApiInterfaces";
import { LSKey } from "../../utils/enums/localStorageKeys";
import { IFormGroup } from "../../interfaces/FormGroupInterfaces";
import { IEBPSTableColumns, IFormNameMaster, IVocabulary } from "../../components/dynamic-setup/interfaces";
import { getStaticFiles } from "../../utils/formUtils";
import { EApproveRejectModule } from "../storeStateTypes";
import { getErrorMessage, isSuccessResponse } from "./actionUtils";

export const getDynamicFormContainerData = (urlArray: ApiParam[], formUtility: DynamicFormUtility,
    // localDataArray, 
    checkPrevious = false, formId: string) => async (dispatch: ActionDispatch) => {
        // console.log('multiple === ', urlArray);
        dispatch({ type: LOADING_UI });
        dispatch({ type: CLEAR_ERRORS });
        dispatch({ type: D_CLEAR_FORM_DATA });
        dispatch({ type: CLEAR_SUCCESS_STATE });
        let permitObj: IPermitData = getBuildPermitObj();
        const userObj = getUserInfoObj();
        const fileCategories: IFileCategory[] = JSON.parse(getLocalStorage(FILE_STORAGE_CATEGORY)) || [];
        const formNameMaster: IFormNameMaster[] = JSON.parse(getLocalStorage(FORM_NAME_MASTER)) || [];

        const cFormName: IFormNameMaster | null = formNameMaster ? formNameMaster.find(row => String(row.id) === String(formId)) || null : null;
        const tables: IEBPSTableColumns | null = JSON.parse(getLocalStorage(LSKey.TABLES_DATA));
        const vocabulary: IVocabulary[] | null = JSON.parse(getLocalStorage(LSKey.VOCABULARY_DATA));
        const needsFile = true;
        //  fileCategories && fileCategories.some(category => cFormName && category.viewUrl && category.viewUrl.trim() === cFormName.viewUrl);

        // if (localDataArray && Array.isArray(localDataArray) && localDataArray.length > 0) {
        //     try {
        //         for (const key of localDataArray) {
        //             dispatch({
        //                 type: D_SET_FORM_DATA,
        //                 payload: {
        //                     [key.getObjName()]: JSON.parse(getLocalStorage(key.getKey())),
        //                 },
        //             });
        //         }
        //     } catch (err) {
        //         console.error(err, "Couldn't load local storage data");
        //     }
        // }

        if (!isEmpty(permitObj) && !isEmpty(userObj)) {
            const appId = permitObj.applicantNo;

            const getUrl = [new ApiParam(api.d_general, 'generalData').setForm()]

            dispatch({ type: D_SET_FORM_DATA, payload: { cFormName, tables, vocabulary } })

            if (needsFile || checkPrevious) {
                const getFileUrl = `${getBackUrl()}${api.fileUpload}${permitObj.applicantNo}`;

                try {
                    const responseFile = await axios.get(getFileUrl);

                    if (responseFile.data.error) {
                        dispatch({ type: SET_ERRORS, payload: responseFile.data.error });
                    } else {
                        if (checkPrevious) setPreviousFormFilesUploaded(fileCategories, responseFile.data, formUtility.getUrl())(dispatch)
                        dispatch({
                            type: D_SET_FORM_DATA,
                            payload: {
                                files: responseFile.data,
                                fileCategories: fileCategories,
                                staticFiles: getStaticFiles(responseFile.data)
                            },
                        });
                    }
                } catch (error) {
                    dispatch({ type: SET_ERRORS, payload: error });
                    dispatch({ type: STOP_LOADING });
                }
            }
            makeRequestFile(0, getUrl, appId, permitObj, userObj, formId)(dispatch);

            // dispatch({ type: STOP_LOADING });
        } else {
            dispatch({ type: SET_UNSET_PERMIT_ERRORS });
            dispatch({ type: STOP_LOADING });
        }
    };

const makeRequestFile = (retry = 0, urlArray: ApiParam[], appId: number, permitObj: IPermitData, userObj: {}, formId: string) => async (dispatch: ActionDispatch) => {
    try {
        const responses: AxiosResponse[] = await Promise.all(
            urlArray.map(async urlObject => {
                const url = urlObject.getParams()
                const getUrl = url.utility ? `${getBackUrl()}${url.api}` : `${getBackUrl()}${url.api}${appId}?formId=${formId}`;

                if (url.concatToApi) {
                    //@ts-ignore
                    const fieldValue = permitObj[url.concatToApi.fieldName];
                    if (url.concatToApi.needsTranslation) {
                        return axios.get(`${getUrl}${url.concatToApi.prefix}${translateNepToEng(fieldValue)}`);
                    }
                    return axios.get(`${getUrl}${url.concatToApi.prefix}${fieldValue}`);
                }
                return axios.get(getUrl);
            })
        );

        responses.forEach(response => {
            if (response) {
                const urlObject = urlArray.find(obj => response.config.url!.includes(obj.getApi()));
                const url = urlObject!.getParams()

                if (url && url.form) {
                    dispatch({
                        type: D_SET_FORM_DATA,
                        payload: {
                            [url.objName]: response.data.data ? (response.data.data) : (response.data),
                            formData: response.data.formData,
                            comment: response.data.comment,
                            history: response.data.history,
                            referencedData: response.data.referencedData,
                            status: response.data.status,
                            permitData: permitObj,
                            userData: userObj,
                        },
                    });
                } else {
                    dispatch({
                        type: D_SET_FORM_DATA,
                        payload: {
                            [url.objName]: response.data.data ? (response.data.data) : (response.data),
                        },
                    });
                }
            }
        });
        dispatch({ type: STOP_LOADING });
    } catch (error) {
        if (retry < 3) {
            makeRequestFile(retry + 1, urlArray, appId, permitObj, userObj, formId)(dispatch);
        } else {
            console.log('Error in form data fetch', error, retry);
            dispatch({ type: SET_ERRORS, payload: error });
            dispatch({ type: STOP_LOADING });
            // throw error;
        }
    }
};

const setPreviousFormFilesUploaded = (fileCategories: IFileCategory[], files: IFile[], formUrl: string) => (dispatch: ActionDispatch) => {

    try {
        const formGroup: IFormGroup[] = JSON.parse(getLocalStorage(LSKey.FORM_GROUP)) || [];
        const formGroupRow = formGroup.find(obj => obj.formId.viewUrl.trim() === String(formUrl))
        const previousForm = formGroupRow && formGroupRow.previousForm
        const previousFormRequiredFiles = fileCategories.filter(categories => categories.formId === previousForm && categories.isRequired === 'Y')
        const hasFiles = previousFormRequiredFiles.every(cat => files.find(file => file.fileType.id === cat.formId))
        dispatch({ type: D_SET_FORM_DATA, payload: { hasPreviousFiles: hasFiles } })
    } catch (err) {
        console.error('Error in previous file check', err)
    }

}

export const postFormDataByUrl = (url: string, data: any, appId: string, formId: string, fetchMenu: boolean = false) => async (dispatch: ActionDispatch) => {
    // const buildPermitObj = JSON.parse(getLocalStorage(CURRENT_BUILD_PERMIT));

    const postUrl = `${getBackUrl()}${url}${appId}?formId=${formId}`;

    data.uploadFile && delete data.uploadFile;
    data.imagePreviewUrl && delete data.imagePreviewUrl;

    axios.defaults.headers.common['Authorization'] = getToken();

    try {
        const response = await axios.post(postUrl, data);

        if (response.data.message === 'Success') {
            if (fetchMenu) {
                dispatch({ type: LOADING_UI });
                try {
                    const response = await axios.get(`${getBackUrl()}${api.buildPermit}${appId}`);
                    if (response.data.error) {
                        dispatch({ type: SET_ERRORS, payload: response.data.error });
                    } else {
                        dispatch({
                            type: SET_CURRENT_PERMIT,
                            payload: { menu: response.data.menu, permitData: response.data.data },
                        });
                        setLocalStorage(MENU_LIST, JSON.stringify(response.data.menu));
                        setLocalStorage(CURRENT_BUILD_PERMIT, JSON.stringify(response.data.data));
                    }
                } catch (err) {
                    // console.log('Error', err);
                    dispatch({ type: SET_ERRORS, payload: err });
                }

                dispatch({ type: STOP_LOADING });
            };

            // if (bClass) {
            //     dispatch({ type: SET_CURRENT_PERMIT, payload: { buildingClass: bClass } });
            // }

            dispatch({
                type: SET_SUCCESS_FORM_SUBMIT,
                payload: { success: true, data: response.data },
            });
        } else {
            dispatch({ type: SET_ERRORS, payload: response.data.error });
        }
    } catch (err) {
        dispatch({ type: SET_ERRORS, payload: err });
    }
};

export const putDynamicApprovalByUrl = (url: string, data: any, module: EApproveRejectModule) => async (dispatch: ActionDispatch) => {
    dispatch({ type: STOP_LOADING_MODAL });
    dispatch({ type: CLEAR_ERRORS });
    try {
        dispatch({ type: LOADING_MODAL });
        const response = await axios.put(url, data);

        if (isSuccessResponse(response)) {
            dispatch({
                type: D_SET_APPROVAL_REJECTION_SUCCESS,
                payload: { success: true, data: response.data, module },
            });
            dispatch({ type: STOP_LOADING_MODAL });
        } else {
            dispatch({ type: SET_ERRORS, payload: getErrorMessage(response) });
            dispatch({ type: STOP_LOADING_MODAL });
        }
    } catch (err) {
        dispatch({ type: STOP_LOADING_MODAL });
        throw new Error(err);
    }
};