import { Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import { Button, Form, Grid, Header, Icon, Label, List, Segment, Table } from 'semantic-ui-react';
import AdminContainer from '../../containers/base/AdminContainer';
import { IAdminContainerProps } from '../../containers/base/containerInterfaces';
import api from '../../utils/api';
import { tableSetupData } from '../../utils/data/ebps-setup/tableSetup';
import { showErrorToast, showToast } from '../../utils/functionUtils';
import { ApiParam } from '../../utils/paramUtil';
import { AdminModalContainer } from '../ebps-setup/AdminModalContainer';
import { DeleteButtonV2, EditButtonV2 } from '../ebps-setup/forms/Buttons';
import { NormalInput } from '../shared/fields/NormalInput';
import { SelectField } from '../shared/Select';
import { FlexSpaceBetween } from '../uiComponents/FlexDivs';
import { SectionHeader } from '../uiComponents/Headers';
import { ebpsColumnsSchema } from './dynamicSetupSchemas';
import { IEBPSColumns, IEBPSTableColumns, IFormNameMaster, TOption } from './interfaces';
import { StatusLabel } from './shared/StatusLabel';

const tableHeadings = tableSetupData.table.tableHeadings;
const modalData = tableSetupData.columnModal;

const referenceOptions: TOption[] = [
	{ key: 1, value: 'text', text: 'Text' },
	{ key: 2, value: 'number', text: 'Number' },
	{ key: 3, value: 'date', text: 'Date' },
	{ key: 4, value: 'vocab', text: 'Vocabulary' },
	{ key: 5, value: 'time', text: 'Time' },
];

const initialAddState = {
	name: '',
	columnName: '',
	defaultValue: '',
	length: 10,
	reference: 'text',
};

const EditTableColumnsComponent = ({
	match,
	adminData,
	getAfterUpdateAdminData,
	postAdminDataByUrl,
	deleteAdminDataByUrl,
	errors,
	success,
	loading,
}: IAdminContainerProps<{ tables: []; formNames: IFormNameMaster[]; sync: { message: string } }>) => {
	const history = useHistory();
	const [data, setData] = useState<IEBPSTableColumns>();
	const [formNameId, setFormNameId] = useState<number>();
	const [hasPK, setHasPk] = useState<boolean>(false);

	const [openAddModal, setOpenAddModal] = useState(false);
	const [openDeleteModal, setOpenDeleteModal] = useState(false);
	const [isEditing, setIsEditing] = useState(false);
	const [deleteIndex, setDeleteIndex] = useState<number | null | undefined>(null);
	const [editData, setEditData] = useState<IEBPSColumns | null>(null);

	useEffect(() => {
		const cTable: IEBPSTableColumns | null =
			adminData.tables.find((row: IEBPSTableColumns) => String(row.id) === String(match.params.id)) || null;
		if (cTable) {
			setData(cTable);
			const hasPK = cTable!.ebpsColumns.some((row: IEBPSColumns) => row.isPk === 'Y');
			setHasPk(hasPK);
		}
	}, [adminData.tables, match.params.id]);

	useEffect(() => {
		if (adminData.formNames) {
			const formName = adminData.formNames.find((row) => String(row.tableId) === String(match.params.id));
			if (formName) {
				setFormNameId(formName.id);
			}
		}
	}, [adminData.formNames, match.params.id]);

	useEffect(() => {
		if (adminData.sync) {
			showToast(adminData.sync.message);
		}
	}, [adminData.sync]);

	useEffect(() => {
		if (success && success.success) {
			if (deleteIndex) {
				showToast('Data deleted successfully');
				setDeleteIndex(null);
				setOpenDeleteModal(false);
			} else {
				showToast('Data saved successfully');
				setEditData(null);
				setOpenAddModal(false);
			}
		}
	}, [success]);

	const handleEdit = (row: IEBPSColumns) => {
		setEditData(row);
		setIsEditing(true);
		setOpenAddModal(true);
	};

	const handleDelete = (row: IEBPSColumns) => {
		if (row) {
			setDeleteIndex(row.id);
			setOpenDeleteModal(true);
		}
	};

	const handleCloseModal = () => {
		setOpenAddModal(false);
		setOpenDeleteModal(false);
		setEditData(null);
		setIsEditing(false);
		setDeleteIndex(null);
	};

	const handleSync = async () => {
		await getAfterUpdateAdminData([new ApiParam(`${api.d_ebpsTablesSync}?tableId=${data && data.id}`, 'sync')]);
		await getAfterUpdateAdminData([new ApiParam(`${api.d_ebpsTables}`, 'tables')]);
	};

	return (
		<div>
			<Formik
				enableReinitialize
				validationSchema={!deleteIndex ? ebpsColumnsSchema : null}
				initialValues={isEditing ? editData : initialAddState}
				onSubmit={async (values, actions) => {
					actions.setSubmitting(true);
					try {
						if (isEditing) {
							const requiredColumns = data ? data.ebpsColumns.filter((row) => row.id === data.id).concat(values) : [];
							console.log(requiredColumns);

							postAdminDataByUrl(api.d_ebpsTables, {
								...data,
								ebpsColumns: requiredColumns,
							});
						} else if (deleteIndex !== null) {
							deleteAdminDataByUrl(api.d_ebpsTablesColumns, deleteIndex);
						} else {
							postAdminDataByUrl(api.d_ebpsTables, {
								...data,
								ebpsColumns: data ? data.ebpsColumns.concat({ tableId: data.id, ...values }) : [],
							});
							handleCloseModal();
						}
						actions.setSubmitting(false);
					} catch (error) {
						console.log('Error in menu master post', error);
						showErrorToast('Something went wrong.');
						actions.setSubmitting(false);
					}
					setOpenAddModal(false);
				}}
			>
				{({ isSubmitting, handleSubmit }) => (
					<>
						<AdminModalContainer
							key="add-edit-columns"
							size="small"
							open={openAddModal}
							scroll={false}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={handleCloseModal}
							title={isEditing ? modalData.titleEdit : modalData.titleAdd}
							saveText={isEditing ? modalData.editText : modalData.saveText}
						>
							<Form loading={loading || isSubmitting}>
								<Form.Group>
									<NormalInput name="name" label="Name" />
									<NormalInput name="columnName" label="Column Name" />
									<NormalInput name="defaultValue" label="Default Value" />
								</Form.Group>
								<Form.Group>
									<NormalInput name="length" label="Length" />
									<SelectField name="reference" label="Reference" options={referenceOptions} />
								</Form.Group>
							</Form>
						</AdminModalContainer>
						<AdminModalContainer
							key="delete-columns"
							size="tiny"
							open={openDeleteModal}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={handleCloseModal}
							title={modalData.delete.title}
							saveText={modalData.delete.title}
						>
							<Form loading={loading || isSubmitting}>{modalData.delete.content}</Form>
						</AdminModalContainer>
					</>
				)}
			</Formik>
			<div>
				{data ? (
					<Segment loading={loading}>
						<FlexSpaceBetween>
							<Header>Table Information</Header>
							<div>
								<Button
									onClick={() => history.push('/admin/v2-table-setup')}
									icon="arrow left"
									content="Back"
									compact
									size="tiny"
									className="danger-button"
								/>
								{formNameId ? (
									<Button
										onClick={() => history.push(`/admin/form-name-details/${formNameId}`)}
										icon="edit"
										content="Edit Related Form"
										compact
										size="tiny"
										className="primary-btn"
									/>
								) : null}
								<Button
									onClick={handleSync}
									icon="sync"
									content="Synchronize Table Columns"
									compact
									size="tiny"
									className="primary-btn"
								/>
							</div>
						</FlexSpaceBetween>
						<List className="app-info">
							<Grid>
								<Grid.Row columns="3">
									<Grid.Column>
										<List.Item>
											<List.Description>{data.name}</List.Description>
											<List.Header>{tableHeadings.name}</List.Header>
										</List.Item>
									</Grid.Column>
									<Grid.Column>
										<List.Item>
											<List.Description>{data.tableName}</List.Description>
											<List.Header>{tableHeadings.tableName}</List.Header>
										</List.Item>
									</Grid.Column>
									<Grid.Column>
										<List.Item>
											<List.Description>
												<StatusLabel status={data.active} />
											</List.Description>
											<List.Header>{tableHeadings.active}</List.Header>
										</List.Item>
									</Grid.Column>
								</Grid.Row>
								<Grid.Row columns="2">
									<Grid.Column></Grid.Column>
								</Grid.Row>
							</Grid>
						</List>
					</Segment>
				) : null}
				<Form.Group>
					<FlexSpaceBetween>
						<div style={{ display: 'flex', alignItems: 'center' }}>
							<SectionHeader>
								<h4 className="left-align">Columns</h4>
							</SectionHeader>
							{!hasPK && (
								<Label className="warning-label-light">
									<Icon name="warning sign" />
									Please add a primary key{' '}
								</Label>
							)}
						</div>
						<Button
							onClick={() => setOpenAddModal(true)}
							icon="add"
							content="Add Columns"
							className="success-button"
							compact
							size="tiny"
						/>
					</FlexSpaceBetween>
					<Table striped celled compact="very" size="small" className="english-div">
						<Table.Header>
							<Table.Row>
								<Table.HeaderCell>Name</Table.HeaderCell>
								<Table.HeaderCell>Column Name</Table.HeaderCell>
								<Table.HeaderCell>Default Value</Table.HeaderCell>
								<Table.HeaderCell>Length</Table.HeaderCell>
								<Table.HeaderCell>Reference</Table.HeaderCell>
								<Table.HeaderCell>Actions</Table.HeaderCell>
							</Table.Row>
						</Table.Header>
						<Table.Body>
							{data
								? data.ebpsColumns.map((row: IEBPSColumns) => (
										<Table.Row key={row.id}>
											<Table.Cell>
												{row.name}{' '}
												{row.isPk === 'Y' && (
													<Label className={'success-label'} size="small">
														<Icon name="key" />
														Primary Key
													</Label>
												)}
											</Table.Cell>
											<Table.Cell>{row.columnName}</Table.Cell>
											<Table.Cell>{row.defaultValue}</Table.Cell>
											<Table.Cell>{row.length}</Table.Cell>
											<Table.Cell>{row.reference}</Table.Cell>
											<Table.Cell>
												<EditButtonV2 handleEdit={handleEdit} row={row} />
												<DeleteButtonV2 handleDelete={handleDelete} row={row} />
											</Table.Cell>
										</Table.Row>
								  ))
								: null}
						</Table.Body>
					</Table>
				</Form.Group>
			</div>
		</div>
	);
};

export const EditTableColumns = (parentProps: any) => (
	//@ts-ignore
	<AdminContainer
		//@ts-ignore
		api={[new ApiParam(api.d_ebpsTables, 'tables'), new ApiParam(api.d_formNameMaster, 'formNames')]}
		//@ts-ignore
		updateApi={[{ api: api.d_ebpsTables, objName: 'tables' }]}
		//@ts-ignore
		render={(props: any) => <EditTableColumnsComponent {...props} parentProps={parentProps} />}
	/>
);
