import { FormikProps, getIn } from 'formik';
import React, { useEffect, useState } from 'react';
import { Label } from 'semantic-ui-react';
import { squareUnitOptions } from '../../../../utils/dataUtils';
import { DashedLangInput, DashedNormalInputIm } from '../../../shared/DashedFormInput';
import { DashedLangDateField } from '../../../shared/DateField';
import { TimeField } from '../../../shared/TimeField';
import { RadioInput } from '../../../shared/formComponents/RadioInput';
import EbpsTextareaField from '../../../shared/MyTextArea';
import { DashedSelect, SelectInputInline } from '../../../shared/Select';
import UnitDropdown from '../../../shared/UnitDropdown';
import { IVocabulary, TOption } from '../../interfaces';
import { FloorTable } from '../tables/FloorTable';
import { SurroundingTable } from '../tables/SurroundingTable';
import { EInputTypes } from '../utils/enums/inputEnums';
import { inputFormatter } from '../utils/inputFormatter';
import { TInputTypeObject } from '../utils/types/setupTypes';
import { CheckboxInput } from '../../../shared/formComponents/CheckboxInput';

type TProps = {
	vocabulary: IVocabulary[];
	content: string;
	formikProps: FormikProps<any>;
	inputTypes: TInputTypeObject[];
	permitData?: { floor: any[]; surrounding: any[] };
};

// const floorDummyData = [
// 	{ floor: 1, length: 12, height: 41, width: 52, area: 523 },
// 	{ floor: 2, length: 12, height: 41, width: 52, area: 523 },
// ];

export const RenderLayoutInput = ({ content, formikProps, inputTypes, vocabulary, permitData }: TProps) => {
	const [options, setOptions] = useState<TOption[]>([]);
	const [inputType, setInputType] = useState<TInputTypeObject>();
	const [type, setType] = useState<EInputTypes | string>();
	const [label, setLabel] = useState<string>();
	const [floorArray, setFloorArray] = useState<any[]>([]);
	const [surroundingArray, setSurroundingArray] = useState<any[]>([]);

	useEffect(() => {
		const [type, name, label] = inputFormatter(content);
		const inputType = inputTypes.find((row) => row.columnName === name);
		if (inputType) {
			setType(type);
			setLabel(label);
			setInputType(inputType);
			if (inputType.vocabularyId) {
				const vocab = vocabulary.find((row) => row.id === inputType.vocabularyId);
				if (vocab) {
					const opt = vocab.vocabularyDetails.map((row) => ({ key: row.id || 0, value: row.value, text: row.label }));
					setOptions(opt);
				}
			}

			if (permitData && permitData.floor) {
				setFloorArray(permitData.floor);
			}

			if (permitData && permitData.surrounding) {
				setSurroundingArray(permitData.surrounding);
			}
		}
	}, []);

	if (inputType) {
		const formFieldId = inputType.id;

		switch (type) {
			case EInputTypes.NEPALI_DATE:
				return (
					//@ts-ignore
					<DashedLangDateField
						inline
						name={formFieldId}
						label={label}
						setFieldValue={formikProps.setFieldValue}
						//@ts-ignore
						value={formikProps.values[formFieldId]}
						//@ts-ignore
						error={formikProps.errors[formFieldId]}
					/>
				);
			case EInputTypes.TIME_INPUT:
				return (
					//@ts-ignore
					<TimeField
						inline
						name={formFieldId}
						label={label}
						setFieldValue={formikProps.setFieldValue}
						//@ts-ignore
						value={formikProps.values[formFieldId]}
						//@ts-ignore
						error={formikProps.errors[formFieldId]}
					/>
				);
			case EInputTypes.FLOOR_TABLE:
				return (
					<FloorTable
						floorArray={floorArray}
						inputType={inputType}
						formFieldId={formFieldId}
						formikProps={formikProps}
						vocabulary={vocabulary}
					/>
				);
			case EInputTypes.SURROUNDING_TABLE:
				return (
					<SurroundingTable
						surroundingArray={surroundingArray}
						inputType={inputType}
						formikProps={formikProps}
						vocabulary={vocabulary}
						formFieldId={formFieldId}
					/>
				);
			case EInputTypes.DASHED_NORMAL_INPUT:
				//@ts-ignore
				return <DashedNormalInputIm name={formFieldId} />;
			case EInputTypes.UNIT_DROPDOWN:
				//@ts-ignore
				return <UnitDropdown name={'floorUnit'} options={squareUnitOptions} setFieldValue={formikProps.setFieldValue} />;
			case EInputTypes.DASHED_DROPDOWN:
				if (options) {
					return <DashedSelect label={label} name={formFieldId} options={options} />;
				} else return null;
			case EInputTypes.DROPDOWN:
				if (options) {
					return <SelectInputInline label={label} name={formFieldId} options={options} 
							widthSize={inputType.meta && inputType.meta.width && inputType.meta.width} />;
				} else return null;
			case EInputTypes.RADIO_INPUT:
				if (options) {
					return (
						<>
							{options.map((option) => (
								<RadioInput key={option.key} label={option.text} name={formFieldId} option={option.value} space />
							))}
							{getIn(formikProps.errors, String(formFieldId)) && (
								<Label pointing="left" prompt size="tiny" basic color="red">
									{getIn(formikProps.errors, String(formFieldId))}
								</Label>
							)}
						</>
					);
				} else return null;
			case EInputTypes.CHECKBOX_INPUT:
				if (options) {
					return (
						<>
						<CheckboxInput options={options} name={formFieldId} inline={true} />
						{getIn(formikProps.errors, String(formFieldId)) && (
							<Label pointing="left" prompt size="tiny" basic color="red">
								{getIn(formikProps.errors, String(formFieldId))}
							</Label>
						)}
						</>
					);
				} else return null;
			case EInputTypes.TEXT_AREA:
				return (
					<EbpsTextareaField
						labelName={label}
						name={formFieldId}
						value={formikProps.values[formFieldId]}
						error={formikProps.errors[formFieldId]}
						setFieldValue={formikProps.setFieldValue}
					/>
				);
			default:
				return (
					//@ts-ignore
					<DashedLangInput
						name={formFieldId}
						label={label}
						setFieldValue={formikProps.setFieldValue}
						//@ts-ignore
						value={formikProps.values[formFieldId]}
						//@ts-ignore
						error={formikProps.errors[formFieldId]}
					/>
				);
		}
	} else {
		return null;
	}
};
