import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import { Button, ButtonContent, Form, Icon, Segment, Table } from 'semantic-ui-react';
import { useAdminSetup } from '../../hooks/useAdminSetup';
import api from '../../utils/api';
import { vocabularyData } from '../../utils/data/ebps-setup/tableSetup';
import { showErrorToast } from '../../utils/functionUtils';
import { validateString } from '../../utils/validationUtils';
import * as Yup from 'yup';
import AdminContainer from '../../containers/base/AdminContainer';
import { DeleteButtonV2, EditButtonV2 } from '../ebps-setup/forms/Buttons';
import { Formik } from 'formik';
import Helmet from 'react-helmet';
import { AdminModalContainer } from '../ebps-setup/AdminModalContainer';
import { IVocabulary } from './interfaces';
import { NormalInput } from '../shared/fields/NormalInput';

const tableHeadings = vocabularyData.table.tableHeadings;
const modalData = vocabularyData.modal;

const initialState = {
	// id: '',
	name: '',
	vocabularyDetails: [],
};

const schema = Yup.object().shape({
	name: validateString,
});

const VocabularySetupComponent = ({ adminData, success, postAdminDataByUrl, deleteAdminDataByUrl, errors, loading }: any) => {
	const {
		isEditing,
		isDeleting,
		formState,
		openAddModal,
		openDeleteModal,
		handleEdit,
		handleDelete,
		setOpenAddModal,
		setOpenDeleteModal,
		resetFormState,
	} = useAdminSetup(initialState, success);

	const history = useHistory();
	const [vocabulary, setVocabulary] = useState<IVocabulary[]>([]);

	useEffect(() => {
		if (adminData.vocabulary && Array.isArray(adminData.vocabulary)) {
			setVocabulary(adminData.vocabulary);
		}
	}, [adminData.vocabulary]);

	const renderButtons = (row: IVocabulary) => {
		return (
			<>
				<Button
					title="Edit Permission and Fields"
					type="reset"
					className="success-button"
					onClick={() => {
						history.push(`/admin/vocabulary-details-setup/${row.id}`);
					}}
					icon="calendar plus outline"
					size="tiny"
					compact
				></Button>
				<EditButtonV2 row={row} handleEdit={handleEdit} />
				<DeleteButtonV2 handleDelete={handleDelete} row={row} />
			</>
		);
	};

	return (
		<div className="english-div">
			<Segment inverted className="tableSectionHeader">
				{vocabularyData.table.tableHeader}
				<Button
					basic
					style={{ right: 2, top: 4, position: 'absolute' }}
					inverted
					type="reset"
					onClick={() => {
						resetFormState();
						setOpenAddModal(true);
					}}
				>
					<ButtonContent>
						<Icon.Group>
							<Icon name="file outline" />
							<Icon inverted corner name="add" />
						</Icon.Group>
						{`  ${modalData.titleAdd}`}
					</ButtonContent>
				</Button>
			</Segment>
			<Formik
				enableReinitialize
				initialValues={{ ...formState }}
				validationSchema={!isDeleting ? schema : null}
				onSubmit={async (values, actions) => {
					actions.setSubmitting(true);
					try {
						if (isEditing) {
							postAdminDataByUrl(api.d_vocabulary, values);
						} else if (isDeleting) {
							deleteAdminDataByUrl(api.d_vocabulary, formState.id);
						} else {
							postAdminDataByUrl(api.d_vocabulary, values);
						}
						resetFormState();
						actions.setSubmitting(false);
					} catch (error) {
						console.log('Error in menu master post', error);
						showErrorToast('Something went wrong.');
						actions.setSubmitting(false);
					}
				}}
			>
				{({ handleSubmit, isSubmitting }) => (
					<>
						<Helmet title="Table Setup" />
						<AdminModalContainer
							key="form-add-modal"
							size="small"
							open={openAddModal}
							scroll={false}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={() => setOpenAddModal(false)}
							title={modalData.titleAdd}
							saveText={isEditing ? modalData.editText : modalData.saveText}
						>
							<Form loading={isSubmitting || loading}>
								<Form.Group widths="equal">
									<NormalInput name="name" label={tableHeadings.name} />
								</Form.Group>
							</Form>
						</AdminModalContainer>{' '}
						<AdminModalContainer
							key="delete-form"
							open={openDeleteModal}
							errors={errors}
							isSubmitting={isSubmitting || loading}
							handleSubmit={handleSubmit}
							handleClose={() => setOpenDeleteModal(false)}
							title={modalData.delete.title}
							saveText={modalData.deleteText}
						>
							<Form loading={isSubmitting || loading}>{modalData.delete.content}</Form>
						</AdminModalContainer>
					</>
				)}
			</Formik>

			<Table sortable striped celled compact="very" size="small">
				<Table.Header>
					<Table.Row>
						<Table.HeaderCell>{tableHeadings.id}</Table.HeaderCell>
						<Table.HeaderCell>{tableHeadings.name}</Table.HeaderCell>
						<Table.HeaderCell>{tableHeadings.actions}</Table.HeaderCell>
					</Table.Row>
				</Table.Header>
				<Table.Body>
					{vocabulary.length > 0 ? (
						vocabulary.map((row) => (
							<Table.Row key={row.id}>
								<Table.Cell>{row.id}</Table.Cell>
								<Table.Cell>{row.name}</Table.Cell>
								<Table.Cell>{renderButtons(row)}</Table.Cell>
							</Table.Row>
						))
					) : (
						<Table.Row>
							<Table.Cell colSpan="6">
								<Segment textAlign="center">No Data Found.</Segment>
							</Table.Cell>
						</Table.Row>
					)}
				</Table.Body>
			</Table>
		</div>
	);
};

export const VocabularySetup = (parentProps: any) => (
	//@ts-ignore
	<AdminContainer
		//@ts-ignore
		api={[{ api: api.d_vocabulary, objName: 'vocabulary' }]}
		//@ts-ignore
		updateApi={[{ api: api.d_vocabulary, objName: 'vocabulary' }]}
		//@ts-ignore
		render={(props: any) => <VocabularySetupComponent {...props} parentProps={parentProps} />}
	/>
);
