import React from 'react';
import { Button, Icon, Table, Label, Popup, Grid } from 'semantic-ui-react';
import { approvalStatus } from '../../../utils/data/genericData';
import { isStringEmpty } from '../../../utils/stringUtils';
import { getApprovalStatusObject, UserType } from '../../../utils/userTypeUtils';
import { TStatuses } from './utils/dynamicFormUtils';

type TProps = {
	statuses: TStatuses[];
	isRejected?: boolean;
};

export default function DynamicApprovalStatus({ statuses, isRejected = false }: TProps) {
	const getDynamicStatus = () => {
		if (isRejected) {
			return (
				<Button
					compact
					size="tiny"
					className={isRejected ? 'danger-button' : 'primary-btn'}
					icon={isRejected ? 'warning circle' : 'tag'}
					content="Status"
				/>
			);
		} else {
			return <Button compact size="tiny" className="primary-btn" icon="tag" content="Status" />;
		}
	};

	const renderStatus = (userType: UserType, statusCode: string, name: string, userDesignation: string) => {
		const { label, className, statusLabel, color, icon } = getApprovalStatusObject(userType, statusCode);

		return (
			<Table.Row>
				<Table.Cell>
					<Label className={className}>
						{name && !isStringEmpty(name) ? `${name} - ${userDesignation || label}` : userDesignation || label}
					</Label>
				</Table.Cell>
				<Table.Cell>
					<Label color={color}>
						<Icon name={icon} />
						{statusLabel}
					</Label>
				</Table.Cell>
			</Table.Row>
		);
	};

	return (
		<Popup
			trigger={getDynamicStatus()}
			hoverable
			flowing
			position="bottom center"
			className="popups"
		>
			<Grid centered divided>
				<Table>
					<Table.Header>
						<Table.Row>
							<Table.HeaderCell>{approvalStatus.user}</Table.HeaderCell>
							<Table.HeaderCell>{approvalStatus.status}</Table.HeaderCell>
						</Table.Row>
					</Table.Header>
					<Table.Body>
						{statuses &&
							statuses.map(({ status, name, userType, userDesignation }) => (
								<React.Fragment key={`${userType}-${name}`}>{renderStatus(userType, status, name, userDesignation)}</React.Fragment>
							))}
					</Table.Body>
				</Table>
			</Grid>
		</Popup>
	);
}
