import { Formik } from 'formik';
import React, { Dispatch, SetStateAction, useEffect } from 'react';
import { Button, Form, Grid, Segment } from 'semantic-ui-react';
import { TInputTypeObject, TOutputFormat } from './utils/types/setupTypes';
import * as Yup from 'yup';
import { FormValuesDisplay } from './FormValuesDisplay';
import { IFormFieldList, IVocabulary } from '../interfaces';
import { RenderLayoutInput } from './renderers/RenderLayoutInput';
import { useDyamicFormInitializer } from '../hooks/useDynamicFormInitializer';
import { RenderLayoutTable } from './renderers/RenderLayoutTable';
import { parseStringToLayout } from './utils/layoutParsers';
import { ELayoutIdentifier } from './utils/enums/LayoutIdentifiers';
import { RenderComponents } from './renderers/RenderComponents';
import { EComponentIdentifiers } from './utils/enums/ComponentIdentifiers';

type TProps = {
	data: string;
	dataMapping: IFormFieldList[];
	inputTypes: TInputTypeObject[];
	output: TOutputFormat[];
	vocabulary: IVocabulary[];
	setOutput: Dispatch<SetStateAction<TOutputFormat[]>>;
};

const floorDummyData = [
	{ floor: 1, length: 12, height: 41, width: 52, area: 523 },
	{ floor: 2, length: 12, height: 41, width: 52, area: 523 },
];

const dummyUserInfo = {
	organization: {
		address: 'विराटनगर,मोरङ',
		designerLoginType: 'public',
		name: 'विराटनगर महानगरपालिका',
		officeName: 'नगर कार्यपालिकाको कार्यालय',
		organizationCode: 'biratnagar',
		province: 'प्रदेश नं १ नेपाल',
		showSignatureImage: 'Y',
	},
};

const dummyPermitData = { photo: '' };

const surroundingDummyData = [
	{ side: 1, feet: 12, sandhiyar: 'राम मखन', kittaNo: '38988' },
	{ side: 3, feet: 12, sandhiyar: 'कालम मखन', kittaNo: '388023' },
];

export const Preview = ({ data, dataMapping, inputTypes, output, setOutput, vocabulary }: TProps) => {
	const { initialValues, getDataToSend } = useDyamicFormInitializer(inputTypes, {}, { floor: floorDummyData, surrounding: surroundingDummyData });

	useEffect(
		() => {
			const formattedTable = parseStringToLayout(data, dataMapping);
			// const formattedData = generateOutput(data, dataMapping);
			setOutput(formattedTable);
		},
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[data]
	);

	const generateSchema = () => {
		let schema = {};
		inputTypes!.forEach((inputType) => {
			const validation = inputType && inputType.meta && inputType.meta.validation;
			if (validation) {
				if (validation.required) {
					if (validation.dataType === 'string') {
						Object.assign(schema, {
							[inputType.name]: Yup.string().required('Required'),
						});
					} else if (validation.dataType === 'number') {
						Object.assign(schema, {
							[inputType.name]: Yup.number().typeError('Must be a number').required('Required'),
						});
					}
				} else {
					if (validation.dataType === 'number') {
						Object.assign(schema, {
							[inputType.name]: Yup.number().typeError('Must be a number').required('Required'),
						});
					}
				}
			}
		});
		return Yup.object().shape(schema);
	};

	return (
		<Form>
			<Grid>
				<Grid.Row>
					<Grid.Column>
						<h3>Preview</h3>
					</Grid.Column>
					{/* <Grid.Column width="5">
						<Button
							onClick={() =>
								//@ts-ignore
								setOutput(generateOutput(data, dataMapping))
							}
							content="Generate"
						/>
					</Grid.Column> */}
				</Grid.Row>
				<Grid.Row>
					<Formik
						enableReinitialize
						initialValues={initialValues}
						validationSchema={generateSchema()}
						onSubmit={(values, { setSubmitting }) => {
							// setTimeout(() => {
							console.log(JSON.stringify(values, null, 2));
							//   setSubmitting(false);
							// }, 400);
						}}
					>
						{(props) => (
							<>
								<Grid.Column width="12">
									<Segment style={{ textAlign: 'left' }}>
										{output &&
											output.map((row) => (
												<React.Fragment key={row.id}>
													{row.input ? (
														<RenderLayoutInput
															content={row.content}
															formikProps={props}
															inputTypes={inputTypes}
															vocabulary={vocabulary}
															permitData={{ floor: floorDummyData, surrounding: surroundingDummyData }}
														/>
													) : row.table ? (
														<RenderLayoutTable
															row={row}
															formikProps={props}
															readOnlyData={{}}
															inputTypes={inputTypes}
															vocabulary={vocabulary}
															permitData={{ floor: floorDummyData }}
															staticFiles={{}}
														/>
													) : row.type === ELayoutIdentifier.DESIGNER_INFO ? (
														row.content
													) : row.type === ELayoutIdentifier.USER_INFO ? (
														row.content
													) : row.type === ELayoutIdentifier.STATIC_IMAGE ? (
														row.content
													) : row.type === ELayoutIdentifier.COMPONENT ? (
														<RenderComponents
															row={row}
															type={row.content as EComponentIdentifiers}
															userInfo={dummyUserInfo}
															//@ts-ignore
															permitData={dummyPermitData}
														/>
													) : (
														<span
															// style={{
															// 	display: row.inline ? 'inline-block' : 'inline',
															// }}
															dangerouslySetInnerHTML={{ __html: row.content }}
														/>
													)}
												</React.Fragment>
											))}
										{/* {formattedData && (
                )} */}
										{/* <div dangerouslySetInnerHTML={{ __html: formattedData }} /> */}
										<br />
										<Button
											content="Submit"
											onClick={(e) => {
												e.preventDefault();
												props.handleSubmit();
											}}
										/>
									</Segment>
								</Grid.Column>
								<Grid.Column width="4">
									<FormValuesDisplay values={getDataToSend(props.values, '7778000001')} />
								</Grid.Column>
							</>
						)}
					</Formik>
				</Grid.Row>
			</Grid>
		</Form>
	);
};
