import React from 'react';
import { useHistory } from 'react-router';
import { Button } from 'semantic-ui-react';

export const BackButton = ({ url, compact = false }: { url: string; compact?: boolean }) => {
	const history = useHistory();
	return <Button onClick={() => history.push(url)} icon="arrow left" content="Back" className="danger-button" size="tiny" compact={compact} />;
};
