import React, { useCallback } from 'react';
import { Button, Icon, Label } from 'semantic-ui-react';
import { getOptionText } from '../../../utils/dataUtils';
import { SetupTable } from '../../ebps-setup/shared/SetupTable';
import { InputColumnFilter } from '../../shared/tableFilters/InputColumnFilter';
import { SelectColumnFilter } from '../../shared/tableFilters/SelectColumnFilter';
import { IFormFieldList, TOption } from '../interfaces';
import { StatusLabel } from '../shared/StatusLabel';

export const FormFieldListTable = ({
	formFieldList,
	handleDelete,
	handleEdit,
	tableOptions,
	columnOptions,
	pkField,
}: {
	formFieldList: IFormFieldList[];
	tableOptions: TOption[];
	columnOptions: TOption[];
	handleDelete: (id: number) => void;
	handleEdit: (id: number, row: IFormFieldList) => void;
	pkField: number | null;
}) => {
	const hEdit = useCallback(handleEdit, []);
	const hDelete = useCallback(handleDelete, []);
	const columns = React.useMemo(() => {
		return [
			{
				Header: 'Name',
				accessor: 'name',

				Cell: ({ row }: { row: { original: IFormFieldList } }) => {
					return (
						<>
							{row.original.name}{' '}
							{pkField === row.original.id && (
								<Label className={'success-label'} size="small">
									<Icon name="key" />
									Primary Key
								</Label>
							)}
						</>
					);
				},
				Filter: InputColumnFilter,
				filter: 'includes',
				switchable: true,
			},
			// { Header: 'Placeholder', accessor: 'placeholder', Filter: InputColumnFilter, filter: 'includes', switchable: true },
			{
				Header: 'Required',
				accessor: 'required',
				Cell: ({ row }: { row: { original: IFormFieldList } }) => {
					return <>{row.original.required ? <StatusLabel status={row.original.required} mode="yes-no" /> : null}</>;
				},
				Filter: SelectColumnFilter,
				filter: 'includes',
				semanticWidth: 1,
				id: 'required',
				defaultOption: 'All',
			},
			{
				Header: 'ReadOnly',
				accessor: 'readOnly',
				Cell: ({ row }: { row: { original: IFormFieldList } }) => {
					return <>{row.original.readOnly ? <StatusLabel status={row.original.readOnly} mode="yes-no" /> : null}</>;
				},
				Filter: SelectColumnFilter,
				filter: 'includes',
				semanticWidth: 1,
				id: 'readOnly',
				defaultOption: 'All',
			},
			{
				Header: 'Ebps Column',
				accessor: (originalRow: IFormFieldList) => {
					return getOptionText(originalRow.ebpsColumnId, columnOptions);
				},
				Filter: InputColumnFilter,
				filter: 'includes',
			},
			{
				Header: 'Reference Table',
				accessor: (originalRow: IFormFieldList) => {
					return getOptionText(originalRow.referencedTableId, tableOptions);
				},
				Filter: InputColumnFilter,
				filter: 'includes',
			},
			{
				Header: 'Reference Column',
				accessor: (originalRow: IFormFieldList) => {
					return getOptionText(originalRow.referencedColumnId, columnOptions);
				},
				Filter: InputColumnFilter,
				filter: 'includes',
			},
			{
				Header: 'Actions',
				semanticWidth: 1,
				Cell: ({ row }: { row: { original: IFormFieldList } }) => (
					<>
						<Button
							icon="edit"
							className="primary-btn"
							size="tiny"
							title="Edit"
							compact
							onClick={() => hEdit(row.original.id || 0, row.original)}
						/>
						<Button
							compact
							icon="close"
							title="Remove"
							size="tiny"
							className="danger-button"
							onClick={() => hDelete(row.original.id || 0)}
						/>
					</>
				),
			},
		];
	}, [tableOptions, columnOptions, hEdit, hDelete, formFieldList]);

	return (
		<div>
			{formFieldList.length < 1 ? (
				<p style={{ textAlign: 'center' }}>Please Add Form Fields</p>
			) : (
				<SetupTable columns={columns} data={formFieldList} />
				// <Table size="small" compact="very" striped celled textAlign="left" selectable>
				// 	<Table.Header>
				// 		<Table.Row>
				// 			<Table.HeaderCell>Name</Table.HeaderCell>
				// 			<Table.HeaderCell>Placeholder</Table.HeaderCell>
				// 			<Table.HeaderCell>Required</Table.HeaderCell>
				// 			<Table.HeaderCell>ReadOnly</Table.HeaderCell>
				// 			<Table.HeaderCell>Ebps Column</Table.HeaderCell>
				// 			<Table.HeaderCell>Reference Table</Table.HeaderCell>
				// 			<Table.HeaderCell>Reference Column</Table.HeaderCell>
				// 			<Table.HeaderCell>Actions</Table.HeaderCell>
				// 		</Table.Row>
				// 	</Table.Header>
				// 	<Table.Body>
				// 		{formFieldList.map((row) => (
				// 			<Table.Row key={row.id}>
				// 				<Table.Cell>
				// 					{row.name}{' '}
				// 					{pkField === row.id && (
				// 						<Label className={'success-label'} size="small">
				// 							<Icon name="key" />
				// 							Primary Key
				// 						</Label>
				// 					)}
				// 				</Table.Cell>
				// 				<Table.Cell>{row.placeholder}</Table.Cell>
				// 				<Table.Cell>
				// 					<StatusLabel status={row.required} mode="yes-no" />
				// 				</Table.Cell>
				// 				<Table.Cell>
				// 					<StatusLabel status={row.readOnly} mode="yes-no" />
				// 				</Table.Cell>
				// 				<Table.Cell>{getOptionText(row.ebpsColumnId, columnOptions)}</Table.Cell>
				// 				<Table.Cell>{getOptionText(row.referencedTableId, tableOptions)}</Table.Cell>
				// 				<Table.Cell>{getOptionText(row.referencedColumnId, columnOptions)}</Table.Cell>
				// 				<Table.Cell>
				// 					<Button
				// 						icon="edit"
				// 						className="primary-btn"
				// 						size="tiny"
				// 						title="Edit"
				// 						compact
				// 						onClick={() => handleEdit(row.id || 0)}
				// 					/>
				// 					<Button
				// 						compact
				// 						icon="close"
				// 						title="Remove"
				// 						size="tiny"
				// 						className="danger-button"
				// 						onClick={() => handleDelete(row.id || 0)}
				// 					/>
				// 				</Table.Cell>
				// 			</Table.Row>
				// 		))}
				// 	</Table.Body>
				// </Table>
			)}
		</div>
	);
};
