import React, { useState } from 'react';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Button, Modal, ModalActions, ModalContent, ModalHeader, Select } from 'semantic-ui-react';
import { TEMPLATE_DELIMETER } from './utils/constants';
import { ELayoutIdentifier } from './utils/enums/LayoutIdentifiers';

const userInfoOptions = [
	{ key: 1, value: 'userName', text: 'Name' },
	{ key: 2, value: 'educationQualification', text: 'Education Qualification' },
	{ key: 3, value: 'consultancyName', text: 'Consultancy Name' },
	{ key: 4, value: 'municipalRegNo', text: 'Municipal Registration Number' },
	{ key: 5, value: 'org-name', text: 'Organization Name' },
	{ key: 6, value: 'org-address', text: 'Organization Address' },
	{ key: 7, value: 'org-officeName', text: 'Organization Office Name' },
	{ key: 8, value: 'org-province', text: 'Organization Province' },
];

export const AddUserInfo = () => {
	const [open, setOpen] = useState(false);
	const [info, setInfo] = useState<string>('userName');

	const handleClose = () => {
		setOpen(false);
	};

	const handleOpen = () => {
		setOpen(true);
	};

	return (
		<>
			<Button content="Add User Info" size="tiny" compact onClick={handleOpen} className="primary-btn" icon="user add" />
			<Modal open={open} onClose={handleClose} size="tiny" closeIcon>
				<ModalHeader>Add User Info</ModalHeader>
				<ModalContent>
					Select user information:
					<Select options={userInfoOptions} value={info} onChange={(e, { value }) => setInfo(String(value))} />
				</ModalContent>
				<ModalActions>
					<Button negative onClick={handleClose}>
						Cancel
					</Button>
					<CopyToClipboard text={`[[${ELayoutIdentifier.USER_INFO}${TEMPLATE_DELIMETER}${info}]]`} onCopy={handleClose}>
						<Button positive icon="copy" type="button" content="Copy" />
					</CopyToClipboard>
				</ModalActions>
			</Modal>
		</>
	);
};
