import { TTableContent } from "../renderers/RenderLayoutTable";
import { TEMPLATE_CLOSE, TEMPLATE_DELIMETER, TEMPLATE_OPEN } from "./constants";
import { EComponentIdentifiers } from "./enums/ComponentIdentifiers";
import { ELayoutIdentifier } from "./enums/LayoutIdentifiers";

export const generateOutput = (rawHtmlString: string) => {
    let output: any[] = [];
    if (rawHtmlString.includes(TEMPLATE_OPEN)) {
        const [currentContent, ...rest] = rawHtmlString.split(TEMPLATE_OPEN);
        output.push({
            input: false,
            inline: true,
            content: currentContent.split(/<p>/).join('<span>').split(/<\/p>/).join('</span>'),
            id: output.length,
        });

        rest.forEach((openSplit) => {
            if (openSplit.includes(TEMPLATE_CLOSE)) {
                const [contentIdentifier, ...closeSplit] = openSplit.split(TEMPLATE_CLOSE);
                if (contentIdentifier.startsWith('data')) {
                    const [, content, vocabId] = contentIdentifier.split(TEMPLATE_DELIMETER);
                    output.push({
                        input: false,
                        type: ELayoutIdentifier.DATA,
                        content,
                        vocabId,
                        inline: true,
                        id: output.length,
                    });
                } else if (
                    contentIdentifier.startsWith(ELayoutIdentifier.USER_INFO) ||
                    contentIdentifier.startsWith(ELayoutIdentifier.DESIGNER_INFO) ||
                    contentIdentifier.startsWith(ELayoutIdentifier.STATIC_IMAGE) ||
                    contentIdentifier.startsWith(ELayoutIdentifier.COMPONENT)) {
                    const [type, content] = contentIdentifier.split(TEMPLATE_DELIMETER);
                    if (content === EComponentIdentifiers.FOOTER_SIGNATURE) {
                        const [, , signatures] = contentIdentifier.split(TEMPLATE_DELIMETER)
                        if (signatures) {
                            output.push({
                                input: false,
                                content,
                                meta: signatures,
                                type,
                                inline: true,
                                id: output.length,
                            });
                        }
                    } else {
                        output.push({
                            input: false,
                            content,
                            type,
                            inline: true,
                            id: output.length,
                        });
                    }
                    // } else if (contentIdentifier.startsWith(ELayoutIdentifier.STATIC_IMAGE)) {
                    //     const [, content] = contentIdentifier.split(TEMPLATE_DELIMETER);
                    //     output.push({
                    //         input: false,
                    //         content,
                    //         inline: true,
                    //         type: ELayoutIdentifier.STATIC_IMAGE,
                    //         id: output.length,
                    //     });
                }
                else {
                    output.push({
                        id: output.length,
                        input: true,
                        content: contentIdentifier,
                    });
                }
                output.push({
                    id: output.length,
                    input: false,
                    // inline: true,
                    content: closeSplit.join().split(/<p>/).join('<span>').split(/<\/p>/).join('</span>'),
                });
            } else {
                output.push({
                    id: output.length,
                    input: false,
                    content: openSplit.replace(/<p>/, '<span>'),
                });
            }
        });
    } else {
        output.push(
            {
                id: output.length,
                input: false,
                content: rawHtmlString.replace(/<p>/, '<span>'),
            },
        );
    }
    return output;
};

const attributeMapping: { [key: string]: string } = {
    'cellspacing': 'cellSpacing',
    'cellpadding': 'cellPadding'
};

export const getAttributeKey = (key: string) => {
    if (attributeMapping.hasOwnProperty(key)) {
        return attributeMapping[key]
    } else return key
}

export const propsParser = (rawProps: string) => {
    let returnProps: any = {};

    if (rawProps) {
        const props = rawProps.split(' ');
        props.forEach((prop) => {
            const [key, value] = prop.split('=');

            if (value) {
                if (key === 'style') {

                    const [...styles] = value.split(';');
                    let styleProp: any = {};
                    if (styles) {
                        styles.forEach(styl => {
                            const [property, val] = styl.split(':');
                            styleProp[property.split('"').join("")] = val.split('"').join("")
                        })
                    }
                    returnProps[getAttributeKey(key)] = styleProp;
                } else {
                    returnProps[getAttributeKey(key)] = value.split('"').join('');
                }
            }
        });
    }
    return returnProps;
};

const getProps = (raw: string) => {
    const [props] = raw.split('>')
    if (props) return propsParser(props)
    return {}
}

const getPropsAndContent = (raw: string, suffix: string) => {

    try {
        const [props, ...content] = raw.split('>')
        const [actualContent] = content.join('>').split(suffix)
        return { props: propsParser(props), content: actualContent }
    } catch {
        return { props: {}, content: '' }
    }
}

export const parseStringToLayout = (rawString: string, dataMapping: any) => {
    let parsed: any = [];

    const parseTable = (currentRawString: string) => {
        if (currentRawString.includes('<table')) {
            const [beforeTable, ...actualTable] = currentRawString.split('<table');
            parsed.push(generateOutput(beforeTable));

            const [tableProps, ...rest] = actualTable.join('<table').split('>');

            const [tableBody, ...afterTable] = rest.join('>').split('</table>');
            //@ts-ignore
            let tbody: TTableContent = { tableProps: getProps(tableProps), header: [], body: [] }
            if (tableBody.includes('<thead')) {
                const [, afterHeaderTag] = tableBody.split('<thead');
                //@ts-ignore
                tbody.header[0] = { headerProps: getProps(afterHeaderTag), headerRows: [] }

                const [tableHeader] = afterHeaderTag.split('</thead>');
                const [_, ...tableHeaderRows] = tableHeader.split('<tr');
                if (tableHeaderRows) {
                    tableHeaderRows.forEach((row, rowIndex) => {
                        //@ts-ignore
                        tbody.header[0].headerRows[rowIndex] = { headerCells: [], headerRowProps: getProps(row) }
                        const [_, ...tableHeaderCell] = row.split('<th');
                        if (tableHeaderCell) {
                            tableHeaderCell.forEach((data, cellIndex) => {
                                const { props, content } = getPropsAndContent(data, '</th>');

                                tbody.header[0].headerRows[rowIndex].headerCells[cellIndex] = { headerCell: '', headerCellProps: props }
                                tbody.header[0].headerRows[rowIndex].headerCells[cellIndex].headerCell = content.split('&nbsp;').join(' ');
                            });
                        }
                    });
                }
            }
            if (tableBody.includes('<tbody')) {
                const [, afterBodyTag] = tableBody.split('<tbody');
                //@ts-ignore
                tbody.body[0] = { bodyProps: getProps(afterBodyTag), rows: [] }

                const [tableBodyContent] = afterBodyTag.split('</tbody>');
                const [_, ...tableRows] = tableBodyContent.split('<tr');
                if (tableRows) {
                    tableRows.forEach((row, rowIndex) => {
                        // tbody.rows[rowIndex] = [];
                        //@ts-ignore
                        tbody.body[0].rows[rowIndex] = { rowProps: {}, cells: [] }
                        const [_, ...tableData] = row.split('<td');
                        if (tableData) {
                            tableData.forEach((data, cellIndex) => {
                                const { props, content } = getPropsAndContent(data, '</td>');
                                tbody.body[0].rows[rowIndex].cells[cellIndex] = { cell: '', cellProps: props }
                                tbody.body[0].rows[rowIndex].cells[cellIndex].cell = content.split('&nbsp;').join(' ');
                            });
                        }
                    });
                }
            }

            parsed.push({ input: false, content: JSON.stringify(tbody), id: 988, table: true });
            if (afterTable && Array.isArray(afterTable)) {

                const afterTableJoin = afterTable.join('</table>')
                if (afterTableJoin.includes('<table')) {
                    parseTable(afterTableJoin)
                } else {
                    parsed.push(generateOutput(afterTableJoin));
                }
            }
        } else if (rawString) {
            parsed.push(generateOutput(rawString))
        }
    }

    parseTable(rawString)

    if (parsed && Array.isArray(parsed) && parsed.length > 0) {
        return parsed.flat().map((row, idx) => ({ ...row, id: idx }))
    } else {
        return parsed.flat();
    }
};