const floorFields = ['area', 'floor', 'length', 'width', 'block', 'height', 'floorUnit'];
export const selectPostValues = (values) => {
	return pick(values, postFields);
};

export const selectFloorValues = (floors) => {
	let filteredFloors = [];
	for (const floor of floors) {
		filteredFloors.push(pick(floor, floorFields));
	}
	return filteredFloors;
};
