import React from 'react';
import { getIn, Field } from 'formik';
import { Checkbox, Form } from 'semantic-ui-react';

export const CheckboxInput = ({ options, name, inline = false }) => {
	return (
		<Field>
			{({ _, form }) => {
				const fieldValue = JSON.parse(getIn(form.values, name));
				return (
					<Form.Group grouped={!inline} inline={inline}>
						{options.map((option) => (
							<Form.Field key={option.key}>
								<Checkbox
									checkbox
									label={option.text}
									value={option.value}
									name={name}
									defaultChecked={fieldValue && Array.isArray(fieldValue) && fieldValue.includes(option.value)}
									onChange={(e) => {
										const val = option.value;
										if (Array.isArray(fieldValue)) {
											if (fieldValue.includes(val)) {
												form.setFieldValue(
													name,
													JSON.stringify(fieldValue.filter((el) => el !== val))
												);
											} else {
												form.setFieldValue(name, JSON.stringify(fieldValue.concat(val)));
											}
										} else {
											form.setFieldValue(name, JSON.stringify([val]));
										}
									}}
								/>
							</Form.Field>
						))}
					</Form.Group>
				);
			}}
		</Field>
	);
};
