import { Field, FormikValues, getIn } from 'formik';
import React, { useEffect, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import Viewer from 'react-viewer';
import { Divider, Form, Icon, Label, List } from 'semantic-ui-react';
import { IFile, IFileCategory } from '../../../interfaces/FileInterfaces';
import { selectExistingFiles, selectRequiredFileCategories } from '../../../store/selectors/dynamicSelectors';
import { TReduxState, TSuccess } from '../../../store/storeStateTypes';
import { getSplitUrl, hasMultipleFiles } from '../../../utils/functionUtils';
import { validateFileField, validateOptionalFileField } from '../../../utils/validationUtils';
import ErrorDisplay from '../ErrorDisplay';
import { ImageDisplayPreview } from './FileView';
import { getDocUrl } from '../../../utils/config';
import { dynamicDeleteFile, getDynamicUploadedFiles } from '../../../store/actions/dynamicFileActions';
import DeleteModal from '../DeleteModal';

const getValidationSchema = (isRequired: string, hasFiles: boolean) => {
	if (hasFiles) {
		return validateOptionalFileField;
	} else if (isRequired && isRequired === 'Y') {
		return validateFileField;
	} else {
		return validateOptionalFileField;
	}
};

type TProps = {
	handleFileChange: (fileObject: any) => void;
	hasDeletePermission?: boolean;
	// Redux state
	loading: boolean;
	errors: any;
	fileCategories: IFileCategory[];
	files: IFile[] | null;
	requiredFileCategories: IFileCategory[] | null;
	loadingModal: boolean;
	deleteSuccess?: TSuccess;
	dynamicDeleteFile: (id: string) => void;
};

function FormSaveFileUploadComponent({
	loadingModal,
	loading,
	errors,
	requiredFileCategories,
	files,
	handleFileChange,
	hasDeletePermission = false,
	dynamicDeleteFile,
	deleteSuccess,
}: TProps) {
	const dispatch = useDispatch();
	const [imageOpen, setImageOpen] = useState(false);
	const [currImg, setCurrImg] = useState<string>();
	const [isDeleting, setIsDeleting] = useState(false);
	const [deleteId, setDeleteId] = useState<string>();

	const closeDelete = () => {
		setIsDeleting(false);
	};

	const handleDeleteModalOpen = (id: string) => {
		setIsDeleting(true);
		setDeleteId(id);
	};

	useEffect(() => {
		if (deleteSuccess && deleteSuccess.success) {
			dispatch(getDynamicUploadedFiles());
		}
	}, [deleteSuccess]);
	return (
		<Form loading={loading}>
			{requiredFileCategories ? (
				requiredFileCategories.map((category) => {
					const categoryFiles = files && files.length > 0 ? files.filter((file) => file.fileType.id === category.id) : [];

					return (
						<div>
							{errors && <ErrorDisplay message={errors.message} />}
							<Divider horizontal>{category.name}</Divider>{' '}
							<Field>
								{({ form }: FormikValues) => {
									const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
										// const arrFiles = Array.from(e.target.files);
										const arrFiles = Array.from(e.target.files || []);
										handleFileChange({ fileCatId: category.id, fileCatName: category.name, files: arrFiles });
										form.setFieldValue(`uploadFile._${category.id}`, arrFiles);
										const multipleFiles = arrFiles.map((file, index) => {
											const src = window.URL.createObjectURL(file);
											return { file, id: index, src };
										});

										form.setFieldValue(`imagePreviewUrl._${category.id}`, multipleFiles);
									};
									const error = getIn(form.errors, `uploadFile._${category.id}`);
									const values = form.values;
									return (
										<Form.Field error={!!error}>
											<Field
												type="file"
												name={`uploadFile._${category.id}`}
												id={`file_${category.id}`}
												multiple={hasMultipleFiles(category.isMultiple)}
												value={undefined}
												onChange={handleChange}
												validate={getValidationSchema(category.isRequired, categoryFiles.length > 0)}
											/>
											{error && (
												<Label pointing prompt size="large">
													{error}
												</Label>
											)}
											{getIn(values, `imagePreviewUrl._${category.id}`) && (
												<div className="previewFileUpload">
													{getIn(values, `imagePreviewUrl._${category.id}`).map((value: any, index: number) => (
														<div key={index} className={'eachPreviewFile'}>
															<ImageDisplayPreview
																isInput={true}
																src={value.src}
																alt={value.file.name}
																type={value.file.type}
																title={value.file.name}
															/>
														</div>
													))}
												</div>
											)}
										</Form.Field>
									);
								}}
							</Field>
							<List relaxed>
								<List.Header>Existing Files</List.Header>
								<div className="previewFileUpload">
									{categoryFiles.length > 0 ? (
										categoryFiles.map((el) => {
											const fileTitleName = getSplitUrl(el.fileUrl, '/');
											return (
												<div key={el.fileUrl} className="eachPreviewFile viewFileSuccess" title={fileTitleName}>
													<>
														<ImageDisplayPreview
															src={el.fileUrl}
															alt={fileTitleName}
															title={fileTitleName}
															type={'image'}
															isInput={false}
															onClick={() => {
																setImageOpen(true);
																setCurrImg(`${getDocUrl()}${el.fileUrl}`);
															}}
														/>
														{hasDeletePermission && (
															<Icon name="close" className="closeImg" onClick={() => handleDeleteModalOpen(el.id)} />
														)}
													</>
												</div>
											);
										})
									) : (
										<List.Item>No files uploaded.</List.Item>
									)}
								</div>
							</List>
							<Viewer
								visible={imageOpen}
								onClose={() => setImageOpen(false)}
								images={[{ src: currImg || '' }]}
								// activeIndex={this.state.currImg}
								zIndex={10000}
							/>
							<DeleteModal
								open={isDeleting}
								onClose={closeDelete}
								loadingModal={loadingModal}
								deleteFile={() => deleteId && dynamicDeleteFile(deleteId)}
							/>
						</div>
					);
				})
			) : (
				<div>No Files</div>
			)}
		</Form>
	);
}

const mapStateToProps = (state: TReduxState) => ({
	fileCategories: state.root.dynamic.fileCategories,
	requiredFileCategories: selectRequiredFileCategories(state),
	files: selectExistingFiles(state),
	loading: state.root.ui.loading,
	errors: state.root.ui.errors,
	fileLoading: state.root.ui.fileLoading,
	loadingModal: state.root.ui.loadingModal,
	success: state.root.formData.success,
	permitData: state.root.currentForm.prevData,
	deleteSuccess: state.root.fileState.success,
});

const mapDispatchToProps = {
	// postFileValidation,
	// postFileWithProgress,
	dynamicDeleteFile,
};

export const FormSaveFileUpload = connect(mapStateToProps, mapDispatchToProps)(FormSaveFileUploadComponent);
