import { FormikProps } from 'formik';
import React from 'react';
import { Table } from 'semantic-ui-react';
import { surroundingMappingFlat } from '../../../../utils/dataUtils';
import { NormalInput } from '../../../shared/fields/NormalInput';
import { IVocabulary } from '../../interfaces';
import { RenderLayoutSubInput } from '../renderers/RenderLayoutSubInput';
import { TInputTypeObject, THeader } from '../utils/types/setupTypes';

type TProps = {
	surroundingArray: any[];
	inputType: TInputTypeObject | undefined;
	vocabulary: IVocabulary[];
	formFieldId: number;
	formikProps: FormikProps<any>;
};

export const SurroundingTable = ({ surroundingArray, formikProps, vocabulary, inputType, formFieldId }: TProps) => {
	const headers = inputType && inputType.meta && inputType.meta.header;
	if (!headers) return null;
	return (
		<div>
			<Table collapsing size="small" compact="very" className="certificate-ui-table">
				<Table.Header>
					<Table.Row>
						<Table.HeaderCell>दिशा</Table.HeaderCell>
						{Object.values(headers).map((header: THeader, idx) => {
							return (
								<React.Fragment key={`${formFieldId}.${idx}`}>
									{header.label ? <Table.HeaderCell key={header.type}>{header.label}</Table.HeaderCell> : null}
								</React.Fragment>
							);
						})}
					</Table.Row>
				</Table.Header>
				<Table.Body>
					{surroundingArray.map((side: { [key: string]: number }, idx) => (
						<Table.Row key={side.side}>
							<Table.Cell>{surroundingMappingFlat.find((f) => side.side === f.side)!.value}</Table.Cell>
							{Object.values(headers).map((header: THeader, headerIdx) => {
								return (
									<React.Fragment key={`${formFieldId}.${idx}.${headerIdx}`}>
										{header.label ? (
											<Table.Cell key={`${formFieldId}.${idx}.${header.label}`}>
												{header.subType ? (
													<RenderLayoutSubInput
														content={header.subType}
														formikProps={formikProps}
														inputType={inputType}
														vocabulary={vocabulary}
														name={`${formFieldId}.${idx}.${header.field || header.type}`}
													/>
												) : (
													<NormalInput label="" name={`${formFieldId}.${idx}.${header.field || header.type}`} />
												)}
											</Table.Cell>
										) : null}
									</React.Fragment>
								);
							})}
						</Table.Row>
					))}
				</Table.Body>
			</Table>
		</div>
	);
};
