import { validateNormalNumber, validateString } from "../../utils/validationUtils";
import * as Yup from 'yup';

export const tablesSchema = Yup.object().shape({
    name: validateString,
    tableName: validateString,
    active: validateString,
});

export const ebpsColumnsSchema = Yup.object().shape({
    name: validateString,
    columnName: validateString,
    length: validateNormalNumber,
    reference: validateString
});

export const vocabularyDetailsSchema = Yup.object().shape({
    value: validateString,
});