const getFormInfo = (form: IFormNameMaster) => {
    //Default Values 
    let savePermission = false, approvePermission = false, refetchMenuAfterSave = false;

    try {
        //         const menuList = JSON.parse(getLocalStorage(MENU_LIST));
        //         const formMaster = JSON.parse(getLocalStorage(FORM_NAME_MASTER));
        const userTypeMaster = JSON.parse(getLocalStorage(LSKey.USER_TYPE_ALL));
        const userDetailsMaster = JSON.parse(getLocalStorage(LSKey.USER_DETAILS));
        const userInfo = getUserInfoObj();

        //         const thisFormRow = formMaster.find((form) => form.viewUrl && form.viewUrl.trim() === url);

        let approveByUsers: TApproveByUser[] = [];


        const prefixes = form.formPermissions.filter(row => row.approvalStatus === 'Y')
            .map((row: IFormPermissions) => {
                //@ts-ignore
                const userRow = userTypePrefixMapping[row.userType];
                const userDesignation = userTypeMaster ? userTypeMaster.find((uT: TUserTypeMaster) => uT.userType === userRow.userType).designation : '';
                approveByUsers.push({ designation: userDesignation, userType: userRow.userType, prefix: userRow.prefix });
                if (userInfo.userType === row.userType) approvePermission = true;
                return { ...userRow, userDesignation };
            });

        let enterByUser = {
            designation: userTypeMaster ? userTypeMaster.find((user: TUserTypeMaster) => user.userType === form.enterBy).designation : '',
            userType: form.enterBy,
            name: '',
        };

        if (userInfo.userType === form.enterBy) {
            enterByUser.name = userInfo.userName
            savePermission = true
        }

        refetchMenuAfterSave = form.formFieldsList.some(row => row.name === "has_revised");

        //         const menuRow = menuList.find((menu) => menu.viewURL && menu.viewURL.trim() === url);
        //         const { approveBy, enterBy } = menuRow ? menuRow : { approveBy: 'N', enterBy: 'N' };

        return {
            // approveBy, 
            // enterBy, 
            prefixes,
            enterByUser,
            savePermission,
            approvePermission,
            approveByUsers: approveByUsers,
            refetchMenuAfterSave,
            // userDetailsMaster 
        };
    } catch (err) {
        //         console.log('err', err);
        return { savePermission, approvePermission, refetchMenuAfterSave, approveBy: 'N', enterBy: 'N', prefixes: null, enterByUser: null, approveByUsers: null, userDetailsMaster: [] };
    }
};