import React from 'react';
import { Button } from 'semantic-ui-react';

export const EditButton = ({ handleEdit, row }) => {
	return (
		<Button
			inverted
			title="Edit"
			type="reset"
			color="green"
			onClick={() => {
				handleEdit({ ...row });
			}}
			icon="edit"
			size="tiny"
			style={{ marginBottom: '2px' }}
		></Button>
	);
};

export const DeleteButton = ({ handleDelete, row }) => {
	return (
		<Button
			inverted
			title="Delete"
			type="reset"
			color="red"
			icon="delete"
			onClick={() => {
				handleDelete(row);
			}}
			size="tiny"
		></Button>
	);
};

export const EditButtonV2 = ({ handleEdit, row, className = '' }) => {
	return (
		<Button
			title="Edit"
			type="reset"
			onClick={() => {
				handleEdit({ ...row });
			}}
			compact
			className={className || 'primary-btn'}
			icon="edit"
			size="tiny"
		></Button>
	);
};

export const DeleteButtonV2 = ({ handleDelete, row, className = '' }) => {
	return (
		<Button
			className={className || 'danger-button'}
			title="Delete"
			type="reset"
			icon="delete"
			compact
			onClick={() => {
				handleDelete(row);
			}}
			size="tiny"
		></Button>
	);
};
