export class StatusDataArray{
	constructor(status) {
        this.status = status || [];
    }  

    userType = JSON.parse(localStorage.getItem('PHOENIX_USER_TYPE_ALL'))
	
	getStatusValue(identifier) {
        let id = identifier.split('-');
		switch (id[1]) {
			case 'name':
				return this.status && this.status[id[0]] && this.status[id[0]].name || '';
			case 'post':
				return this.userType.filter(x=> { return x.userType === id[0]})[0]['designation'] || '';
			default:
				return '';
		}
	}
}